.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.studioDisplayWrapper {
  z-index: var(--z-index-0);
}

.sideBySide {
  --side-by-side-progress: 0.5;
  --max-parallax-percent: 0;
  position: relative;
  z-index: var(--z-index-2);
  width: 100%;
  padding: 0 0 var(--spacer-96) 0;

  --side-by-side-radius: var(--border-radius-20);
  --shadow-radius: var(--side-by-side-radius);

  & + .sideBySide {
    @media (--sm) {
      padding-top: var(--spacer-96);
    }
  }
  @media (--sm) {
    &.hasLink {
      pointer-events: none;
    }
  }

  /* default: text left, media right */

  --content-start: start;
  --content-width: 4;
  --media-start: start;
  --media-end: end;

  @media (--sm) {
    --content-start: col 3;
    --content-width: 5;
    --media-start: col 9;
    --media-end: end;
  }

  @media (--md) {
    --content-start: col 2;
    --content-width: 4;
    --media-start: col 8;
    --media-end: end;
  }

  &.isImageFullBleed {
    @media (--sm) {
      --content-start: col 1;
      --content-width: 4;
      --media-start: wrapper-start;
      --media-end: col 8;
    }
  }

  &.isImageFullBleed.isTextLeft {
    @media (--sm) {
      --content-start: col 2;
      --content-width: 6;
      --media-start: col 9;
      --media-end: wrapper-end;
    }

    @media (--md) {
      --content-start: col 2;
      --content-width: 4;
      --media-start: col 7;
      --media-end: wrapper-end;
    }
  }

  &.isImageFullBleed.isTextRight {
    @media (--sm) {
      --content-start: col 9;
      --content-width: 6;
      --media-start: wrapper-start;
      --media-end: col 8;
    }

    @media (--md) {
      --content-start: col 11;
      --content-width: 4;
      --media-start: wrapper-start;
      --media-end: col 10;
    }
  }

  &.isImageFullBleed.isTextRight.isIndented {
    @media (--sm) {
      --content-start: col 10;
      --content-width: 5;
      --media-start: col 2;
      --media-end: col 9;
    }

    @media (--md) {
      --content-start: col 10;
      --content-width: 4;
      --media-end: col 9;
    }
  }

  &.isTextRight {
    @media (--sm) {
      --content-start: col 8;
      --media-start: start;
      --media-end: col 7;
    }

    @media (--md) {
      --content-start: col 9;
      --media-start: start;
      --media-end: col 8;
    }
  }

  &.isTextLeft.isIndented {
    @media (--sm) {
      --content-start: col 2;
      --content-width: 5;
      --media-start: col 8;
      --media-end: col 14;
    }

    @media (--md) {
      --media-start: col 7;
      --content-width: 4;
    }
  }

  &.isTextRight.isIndented {
    @media (--sm) {
      --content-start: col 9;
      --content-width: 5;
      --media-start: col 2;
      --media-end: col 8;
    }

    @media (--md) {
      --content-start: col 10;
      --content-width: 4;
      --media-end: col 9;
    }
  }
}

.container {
  composes: grid from '~g';
  align-items: center;
}

.withLink {
  composes: cardAndButtonHover from '~g';
}

.content {
  z-index: var(--z-index-4);
  display: flex;
  flex-flow: row wrap;
  grid-column: var(--content-start) / span var(--content-width);
  pointer-events: initial;

  .accents,
  .bodies {
    color: var(--color-grey-60);
  }

  .bodies {
    @media (--md) {
      /* TODO: figure out where this stupid thing was required */

      /* padding-right: var(--grid-computed-column-width); */
    }
  }

  .title3 {
    ~ .bodySmall {
      margin-top: var(--spacer-8);

      @media (--md) {
        margin-top: var(--spacer-20);
      }
    }
  }

  .bodySmall + .titles,
  .bodies + .titles {
    margin-top: var(--spacer-40);

    @media (--md) {
      margin-top: var(--spacer-64);
    }
  }

  .buttons {
    margin-top: var(--spacer-12);

    @media (--sm) {
      margin-top: var(--spacer-6);
    }

    @media (--md) {
      margin-top: var(--spacer-16);
    }
  }

  .stats,
  .touts {
    display: flex;
    flex-flow: row wrap;
    padding: 0;
    margin: 0;
    grid-row-gap: 0;

    @media (--sm) {
      padding: var(--spacer-32) 0;
    }

    > * {
      margin-bottom: var(--spacer-48);

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.mediaContainer {
  /* position: relative; */
  order: -1;
  margin-bottom: var(--spacer-40);
  grid-column: var(--media-start) / var(--media-end);

  @media (--sm) {
    order: 1;
    margin-bottom: 0;
  }

  .isImageFullBleed & {
    @media (--sm) {
      display: flex;
      justify-content: flex-end;
      order: 1;
      margin-bottom: 0;
    }

    .mediaImgElement {
      height: auto;
      border-radius: 0;
      object-fit: cover;

      /* the video element is not reachable via classNames through react-player component */
      video {
        border-radius: 0;
      }
    }

    .media {
      @media (--sm) {
        width: 120%;
        max-width: 90rem;
        flex-shrink: 0;
      }
    }

    &.wrapInStudioDisplay {
      z-index: var(--z-index-3);
      flex-shrink: 0;

      .media {
        width: min(156%, 60rem);
        @media (--xs) {
          width: auto;
        }
        @media (--sm) {
          width: 150%;
          max-width: 120rem;
        }
      }
    }
  }

  .isImageFullBleed.isTextLeft & {
    @media (--sm) {
      justify-content: flex-start;
    }

    .mediaImgElement {
      object-position: 0% 50%;
    }
  }

  .isImageFullBleed.isTextRight & {
    .mediaImgElement {
      object-position: 100% 50%;
    }
  }

  &:not(.isImageFullBleed)&:not(.wrapInStudioDisplay)&:not(.hasAlpha) {
    .mediaContainerInGlow {
      border: 0.56px solid rgb(104 106 124 / 20%);
    }
  }

  .isTextRight & {
    @media (--sm) {
      order: -1;
    }
  }
}

.media {
  width: 100%;
  height: 100%;
  pointer-events: initial;
}

.mediaElement {
  width: 100%;
  transform: scale(calc(var(--card-hover-progress, 0) * var(--default-card-hover-scale-amount) + 1));
  transition: var(--card-hover-transform-default-transition), opacity var(--duration-600ms);
}

.glowBackground {
  --glow-offset-y: 5%;
}

.mediaContainerInGlow {
  position: relative;
  z-index: var(--z-index-2);
  overflow: hidden;
  aspect-ratio: var(--media-aspect-ratio);

  --image-aspect-ratio: var(--media-aspect-ratio);
  border-radius: var(--side-by-side-radius);
}

.logo {
  position: absolute;
  z-index: var(--z-index-3);
  top: auto;
  bottom: var(--spacer-32);
  left: var(--spacer-32);
  width: auto;
  height: 3rem;

  @media (--md) {
    bottom: var(--spacer-40);
    left: var(--spacer-40);
  }
}

.parallax {
  .needsParallax {
    box-sizing: content-box;

    .mediaElement {
      transform: translate3d(0, calc(var(--side-by-side-progress) * (-1% * var(--max-parallax-percent))), 0);
      transform-origin: center;
      transition: transform var(--duration-150ms) var(--ease-out);
    }
  }
}

.wrapInStudioDisplay {
  .mediaContainerInGlow {
    border-radius: 0.2rem;
  }

  .media {
    padding: 2%;
    aspect-ratio: var(--studio-display-aspect-ratio);
  }
}
