.wrapper {
  display: none;
  padding-bottom: var(--parallax-distance, 0);
  grid-column: wrapper-start / wrapper-end;
  transform: translate3d(0, calc(var(--parallax-distance) * var(--scroll-progress)), 0);

  @media (--sm) {
    display: flex;
    width: 120%;
    flex-wrap: wrap;
    gap: var(--spacer-16);
  }

  @media (--md) {
    width: 110%;
    gap: var(--spacer-40);

    --parallax-distance: 30vh;
  }

  @media (--lg) {
    overflow: visible;
    width: 100%;
    justify-content: center;
  }
}

.rowBreak {
  display: none;

  @media (--lg) {
    display: block;
    flex-basis: 100%;
  }
}

.firstRow {
  position: relative;
  z-index: var(--z-index-2);
}
