.navigation {
  position: fixed;
  z-index: var(--z-index-nav);
  top: 0;
  width: 100%;
}

.navigationBannerEl {
  z-index: var(--z-index-2);
}

.mainNavigation {
  --desktop-subnav-backdrop-transition: 0.5s;
  --desktop-subnav-text-transition: 0.75s;
  --nav-saturate: 150%;
  --nav-blur: 15px;
  --nav-text-color-light: var(--color-grey-80);
  --nav-text-color-dark: var(--color-grey-60);
  --nav-text-color: var(--nav-text-color-dark);
  --nav-bg: var(--nav-gradient-when-blurred);
  --nav-filter: saturate(var(--nav-saturate)) blur(var(--nav-blur));
  position: relative;
  display: flex;
  width: 100%;
  height: var(--nav-height);
  justify-content: center;
  padding: 0 var(--spacer-24);
  color: var(--nav-text-color);
  pointer-events: auto;

  &.isNavigationOpaque {
    .backdrop {
      background: var(--color-grey-00);
    }
  }

  &::after {
    position: absolute;
    z-index: var(--z-index-1);
    top: var(--nav-height);
    left: 0;
    width: 100%;
    height: 1px;
    background: var(--color-alpha-light-10);
    content: '';

    @media (--sm) {
      z-index: var(--z-index-neg);
    }

    @media (--md) {
      z-index: var(--z-index-1);
    }
  }

  @media (--md) {
    padding: 0 var(--spacer-48) 0 var(--spacer-40);

    &.subNavOpen {
      .backdrop {
        transform: scaleY(1);

        /* animate-in */
        transition: transform var(--desktop-subnav-backdrop-transition) var(--ease-basic-butter);

        &::after {
          opacity: 1;
        }
      }
    }
  }
}

.navigationInner {
  position: relative;
  display: inline-flex;
  width: 100%;
  max-width: 144rem;
  height: 100%;
  align-items: center;
  justify-content: space-between;
}

.left {
  position: absolute;
  z-index: var(--z-index-2);
  display: flex;
}

.logoLeft {
  display: flex;
  align-items: center;
}

.logo {
  position: relative;
  width: 2rem;

  @media (--md) {
    width: auto;
    height: 3.4rem;
  }

  path {
    fill: var(--color-grey-97);
  }
}

.desktopNavigation {
  display: none;

  @media (--md) {
    position: absolute;
    left: 0;
    display: flex;
    width: 100%;
    height: var(--nav-height);
    justify-content: center;
    gap: max(2rem, 2%);
  }
}

.right {
  --icon-size: 2rem;
  display: none;

  @media (--md) {
    position: absolute;
    z-index: var(--z-index-2);
    right: 0;
    display: flex;
    flex-shrink: 0;
    align-items: center;
    gap: var(--spacer-24);
  }

  a {
    color: var(--color-brand-grey-100);
  }

  .navigationButton {
    color: var(--color-grey-97);
  }

  .navButtonContainer {
    --icon-size: 2rem;
  }
}

.navigationButton {
  /* yes this isn't using spacers, but this is a very small button so every pixel counts */
  padding: 1.4rem var(--spacer-16) 1.3rem;
}

.backdrop {
  position: absolute;
  z-index: var(--z-index-neg);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  /* on desktop, nav blur/unset on scroll */
  backdrop-filter: var(--nav-filter);
  background: var(--nav-bg);
  pointer-events: none;

  @media (--md) {
    height: calc(100% + 5rem);
    transform: scaleY(0.62);
    transform-origin: 0 0;

    /* animate out */
    transition: transform var(--desktop-subnav-text-transition) var(--ease-basic-butter);

    &::after {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: var(--color-alpha-light-10);
      content: '';
      opacity: 0;

      /* animate out */
      transition: opacity var(--desktop-subnav-text-transition) var(--ease-basic-butter);
    }
  }

  &::after {
    position: absolute;
    background: transparent;
    inset: 0;
    transition: background 0.2s var(--ease-default);
  }
}
