.hoverTestimonials {
  position: relative;
  display: none;
  @media (--sm) {
    composes: grid from '~g';
    display: grid;
  }
}

.title {
  composes: bodyLarge from '~typo';
  margin-bottom: 3rem;
  color: var(--color-accent);
  grid-column: 3 / span 3;
}

.content {
  display: block; /* cancel Subgrid */
  padding-right: var(--grid-computed-column-width);
  grid-column: 3 / span 4;

  .titles {
    margin: 0;
    color: var(--color-grey-50);

    &:first-child {
      color: var(--color-grey-100);
      white-space: nowrap;
    }
  }
}

.testimonialsWrapper {
  position: relative;
  grid-column: 7 / end;
}

.testimonials {
  .testimonial {
    position: relative;

    --testimonial-title-color: var(--color-grey-15);
    cursor: normal;

    &:hover {
      --testimonial-title-color: var(--color-grey-100);
    }
  }
}

.testimonialTitleWrapper {
  width: 100%;
  padding: 0;
  margin: 0;
  cursor: pointer;
}

.testimonialTitle {
  composes: title2 from '~typo';
  color: var(--testimonial-title-color);
  text-align: left;
  transition: color var(--duration-500ms) var(--ease-out);
}

.testimonialCardWrapper {
  position: absolute;
  z-index: var(--z-index-2);
  top: 50%;

  /* left: calc(var(--index-fancy-testimonial) * 3% + 5rem); */
  left: 0;
  pointer-events: none;
  transform: translate3d(var(--mouse-x), var(--mouse-y), 0);
  transition: transform var(--ease-out) var(--duration-300ms);
}

.testimonialGlowWrapper {
  opacity: var(--hover-progress, 0);
  transform: translate(calc(15% * var(--hover-progress)), calc(-50% + (var(--translate-y, 0) * 1px)));
  transform-origin: center center;
}

.testimonialHoverCard {
  position: relative;
  display: flex;
  overflow: hidden;
  width: 30vw; /* 433/1440 */
  max-width: 50rem;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  aspect-ratio: 433/205;
  border-radius: var(--testimonial-card-radius);
  transform: scale(1, var(--scale-y));
  transform-origin: center center;

  .testimonialMedia,
  .testimonialLogo {
    width: 100%;
    height: 100%;
    pointer-events: none;
    transform: scale(1, var(--inner-scale-y));
    transform-origin: center center;
    transition: none;
  }

  .testimonialLogo {
    position: absolute;
    top: 0;
    left: 0;
  }

  .moreButton {
    position: absolute;
    top: var(--spacer-16);
    right: var(--spacer-20);
    display: flex;
    width: 4rem;
    height: 4rem;
    align-items: center;
    justify-content: center;
    border: 0.1rem solid var(--color-alpha-light-15);
    border-radius: 50%;
    pointer-events: none;
    transform: scale(1, var(--inner-scale-y));

    .buttonIcon {
      width: 1rem;
      height: 1rem;
    }
  }
}

.testimonialsGradients {
  position: absolute;
  top: 0;
  left: 0;
  background: var(--fancy-testimonial-gradient);
  background-clip: text;
  opacity: 1;
  pointer-events: none;
  -webkit-text-fill-color: transparent;
  transition: opacity var(--duration-500ms) ease-in-out;

  &.hidden {
    opacity: 0;
  }

  .testimonial {
    /* Unset so that it doesn't prevent the gradient to apply */
    position: unset;
  }
}

/* mimick testimonial modal */
.preload {
  composes: grid from '~g';
  position: absolute;
  top: 0;
  left: 0;

  /* left: -100vw; */
  width: 100%;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;

  .preloadImage {
    grid-column: start/end;
  }
}
