.laptopCloseImageSequence {
  composes: grid from '~g';
  position: relative;

  --media-start: start;
  --media-end: end;
  --canvas-aspect-ratio: calc(16 / 9);

  .mediaText {
    margin-top: 0;
    margin-bottom: var(--spacer-96);
    @media (--sm) {
      margin-bottom: var(--spacer-160);
    }
    @media (--md) {
      margin-bottom: var(--spacer-240);
    }
  }

  .mediaContainer {
    grid-column: wrapper-start / wrapper-end;
  }
}

.mediaContainer {
  composes: grid from '~g';
}

.mediaWrapper {
  margin-bottom: var(--spacer-64);
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.middleContent {
  composes: grid from '~g';
  position: relative;
  margin-top: var(--canvas-offset);
  margin-bottom: 20vh;
  grid-column: wrapper-start / wrapper-end;

  --canvas-offset: 15vh;
  @media (--sm) {
    margin-bottom: -5vw;

    --canvas-offset: min(40vh, 25vw);
  }

  @media (--md) {
    --canvas-offset: 20vw;
  }

  @media (max-aspect-ratio: 3800 / 1500) and (min-width: 2200px) {
    --canvas-offset: min(22vh, 25vw);
  }

  .hiddenTextEyebrow,
  .hiddenTextTitle2 {
    padding-right: 0.5rem;

    @media (--sm) {
      text-align: center;

      --content-width: 12;
    }
  }

  .stats {
    --stat-width: 1;
    --content-start: col 1;
    --content-width: 4;
    --subgrid-width: 4;
    display: flex;
    margin-top: calc(20vw * var(--canvas-aspect-ratio));
    background: linear-gradient(270.01deg, #f9a78c 0.01%, #ed8d82 7.3%, #d970c8 42.19%, #c866af 50.52%, #b75cc4 89.58%, #b359ca 99.99%, #693295 99.99%);
    background-clip: text;
    -webkit-text-fill-color: transparent;

    @media (min-width: 435px) and (max-width: 960px) {
      margin-top: calc(19vw * var(--canvas-aspect-ratio));

      @media (max-aspect-ratio: 6/5) {
        margin-top: calc(22vw * var(--canvas-aspect-ratio));
      }

      @media (min-aspect-ratio: 45 / 46) {
        margin-top: calc(18vw * var(--canvas-aspect-ratio));

        @media (min-aspect-ratio: 239 / 134) {
          margin-top: calc(14vw * var(--canvas-aspect-ratio));
        }
      }
    }

    @media (--sm) {
      display: grid;
      padding-top: 0;
      margin-top: var(--spacer-40);

      --stat-width: 3;
      --content-start: col 4;
      --content-width: 9;
      --subgrid-width: 9;
    }

    @media (--lg) {
      margin-top: var(--spacer-64);
    }

    --stat-suffix-isolation: isolate;
  }

  .sequence {
    position: relative;
    z-index: var(--z-index-1);
    height: auto;
    aspect-ratio: 16 / 9;
    grid-column: start / end;
    pointer-events: none;

    @media (--sm) {
      aspect-ratio: 2 / 1;
    }
  }

  /* I know it's ugly to "important" things, but this is required to override the html attributes */
  .canvas {
    position: relative;

    /* height: calc(100vh - var(--canvas-top)) !important; */
    left: calc(var(--extra-width) / -2);
    /* stylelint-disable-next-line declaration-no-important */
    width: calc(100% + var(--extra-width)) !important;
    margin-top: calc(-1 * var(--canvas-offset));

    /* transform: translateX(-21.9%); */
    --canvas-mobile-width: 168vw;

    /* prettier-ignore */
    --canvas-mobile-height: calc(var(--canvas-mobile-width) / var(--canvas-aspect-ratio));
    --canvas-top: 0;
    --extra-width: 78%;

    @media (--sm) {
      --extra-width: 62%;
    }
  }
}

/* stylelint-disable-next-line selector-class-pattern */
:global(.older-safari) .stats {
  -webkit-text-fill-color: var(--color-grey-100);
}
/* stylelint-disable-next-line selector-class-pattern */
body:not(:global(.older-safari)) .stats {
  & > p:nth-child(1) {
    --stat-suffix-gradient: linear-gradient(131deg, rgb(184 93 195) 0%, var(--color-grey-00) 100.89%);
  }

  & > p:nth-child(2) {
    --stat-suffix-gradient: linear-gradient(131deg, rgb(200 102 175) 0%, var(--color-grey-00) 100.89%);
  }

  & > p:nth-child(3) {
    --stat-suffix-gradient: linear-gradient(131deg, rgb(231 132 149) 0%, var(--color-grey-00) 100.89%);
  }
}

.hiddenTextWrapper {
  position: absolute;
  top: calc(var(--canvas-offset) * -0.4);
  right: 0;
  left: 0;
  width: 100%;
  margin-top: -9vh;
  grid-column: wrapper-start / wrapper-end;
  @media (--sm) {
    display: flex;
    flex-direction: column;
    margin-top: 0;
    grid-column: unset;
  }
}

.hiddenTextGridHolder {
  composes: grid from '~g';
  left: 0;
}

.hiddenText {
  grid-column: start / end;
}
