.glowWrapper {
  position: relative;
  z-index: var(--z-index-1);

  --canvas-scale: var(--mobile-canvas-scale, 0.75);
  @media (--sm) {
    --canvas-scale: 0.75;
  }
}

.glowBackground {
  --default-glow-skew: 0;
  position: absolute;
  z-index: var(--z-index-neg);
  top: 50%;
  left: 50%;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  backface-visibility: hidden;
  opacity: calc(var(--glow-opacity, 0) * var(--glow-intensity, 1));
  pointer-events: none;
  transform: translate3d(calc(var(--glow-translate-x) + var(--glow-offset-x)), calc(var(--glow-translate-y) + var(--glow-offset-y)), 0) scale(calc(1 * var(--glow-multiplier) * var(--glow-scale, 1.5))) skew(var(--glow-skew, var(--default-glow-skew)));
  transform-origin: center center;

  --glow-opacity: 0;
  --glow-multiplier: 1;
  --glow-translate-x: -50%;
  --glow-translate-y: -50%;
  --glow-offset-x: 0%;
  --glow-offset-y: 0%;

  /* &.center {
    --glow-translate-x: -50%;
    --glow-translate-y: -50%;
  }

  &.topLeft {
    --glow-translate-x: -100%;
    --glow-translate-y: -100%;
  }

  &.topRight {
    --glow-translate-x: 0%;
    --glow-translate-y: -100%;
  }

  &.bottomLeft {
    --glow-translate-x: -100%;
    --glow-translate-y: 0%;
  }

  &.bottomRight {
    --glow-translate-x: 0%;
    --glow-translate-y: 0%;
  } */

  &.alignCenter {
    --glow-translate-x: -50%;
    --glow-translate-y: -50%;
  }

  &.alignTop {
    --glow-translate-y: calc(- 50% - (var(--glow-scale) - 1) / 2 * 100%);
  }

  &.alignLeft {
    --glow-translate-x: calc(- 50% - (var(--glow-scale) - 1) / 2 * 100%);
  }

  &.alignRight {
    --glow-translate-x: calc(- 50% + (var(--glow-scale) - 1) / 2 * 100%);
  }

  &.alignBottom {
    --glow-translate-y: calc(- 50% + (var(--glow-scale) - 1) / 2 * 100%);
  }
}

.displayed {
  --default-glow-transition-in-delay: 0s;

  /*  magic number 1 / WEBGL_SCALE   x 2 because we scaled down by two the canvas */
  --glow-multiplier: calc(1.67 * (1 / var(--canvas-scale)));
  --glow-opacity: 1;
  transition: opacity 1.5s var(--glow-transition-in-delay, var(--default-glow-transition-in-delay)), transform 1s var(--glow-transition-in-delay, var(--default-glow-transition-in-delay));
}

.glowCanvas {
  width: calc(var(--canvas-scale) * 100%);
  height: calc(var(--canvas-scale) * 100%);
  backface-visibility: hidden;
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition: opacity 0.5s;
}
