.capture {
  --capture-parallax-y: -30%;
  position: relative;
  z-index: var(--z-index-2);
  width: 100%;
  pointer-events: none;

  @media (--sm) {
    transform: translate3d(0, var(--capture-parallax-y), 0);
  }

  .content {
    pointer-events: all;

    .name {
      text-align: center;
    }

    .paragraph {
      color: var(--color-grey-60);
      text-align: center;
    }
  }
}

.container {
  composes: grid from '~g';
  position: relative;
  align-content: center;
  padding: var(--spacer-160) 0 var(--spacer-160);

  @media (--sm) {
    padding: var(--spacer-320) 0 var(--spacer-160);
  }
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.content {
  display: flex;
  flex-direction: column;
  grid-column: start / span var(--grid-number-of-columns);

  .name {
    padding: 0 var(--spacer-24);
    text-align: center;
  }
}

.paragraph {
  color: var(--color-grey-60);
  text-align: center;
}

.forms {
  margin-right: auto;
  margin-left: auto;

  --placeholder-color: var(--color-grey-100);
  --input-color: var(--color-grey-100);

  &:not(:first-child) {
    margin-top: var(--spacer-40);
  }
}

.buttons {
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  margin-right: auto;
  margin-left: auto;

  &:not(:first-child) {
    margin-top: var(--spacer-40);
  }
}

.capture .backgroundImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.capture .backgroundImageContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
