.root {
  position: relative;

  --content-start: start;
  --content-width: 4;
  --media-start: start;
  --media-end: end;

  @media (--sm) {
    --content-start: col 8;
    --media-start: start;
    --media-end: col 7;
  }

  @media (--md) {
    --content-start: col 9;
    --media-start: start;
    --media-end: col 8;
  }

  .wrapper {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;

    @media (--sm) {
      display: unset;
    }
  }

  .paginationDots {
    position: relative;
    z-index: var(--z-index-2);
    align-items: center;
    justify-content: center;
    margin: var(--spacer-16) auto;

    @media (--sm) {
      display: none;
    }
  }
}

.contentWrapper {
  composes: grid from '~g';
  padding-bottom: var(--spacer-96);
  @media (--sm) {
    padding-bottom: var(--spacer-124);
  }
}

.section {
  position: relative;
  flex: 1 0 auto;

  /* position: absolute;
  flex: 100%;

  &:first-child {
    position: relative;
  } */

  @media (--sm) {
    composes: grid from '~g';
    position: sticky;
    align-items: center;

    &:first-child {
      position: sticky;
    }
  }
}
