.radioButtons {
  position: relative;
}

.radioButtonItemsWrapper {
  display: flex;
  flex-flow: row nowrap;
}

.radioButtonItem {
  position: relative;
  margin-right: var(--spacer-28);
}

.radioButtonInner {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: flex-start;
  white-space: nowrap;
}

.radioButtonIcon {
  position: relative;
  width: 2.4rem;
  height: 2.4rem;
  border: 1px solid var(--color-grey-60);
  margin-right: var(--spacer-12);
  border-radius: 1.2rem;

  .isActive & {
    &::after {
      position: absolute;
      top: calc(50% - 0.5rem);
      left: calc(50% - 0.5rem);
      width: 1rem;
      height: 1rem;
      background: var(--color-cobalt);
      border-radius: 0.5rem;
      content: '';
    }
  }
}

.radioButtonTitle,
.radioButtonSubtitle {
  composes: body from '~typo';
}

.radioButtonTitle {
  color: var(--color-grey-100);
}

.radioButtonSubtitle {
  margin-left: var(--spacer-8);
  color: var(--color-grey-60);
}
