.glow {
  position: absolute;
  z-index: var(--z-index-neg);
  inset: 0;
}

.glow,
.shadow {
  display: none;

  @media (--sm) {
    display: block;
  }
}

.slide {
  position: relative;
  width: 100%;
  min-height: 100vh;
  opacity: 0.65;
  transition: opacity 0.4s var(--ease-out);

  --stagger-offset: 3rem;

  &::after {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    display: block;
    height: 15rem;
    background: linear-gradient(180deg, #000000 0%, rgb(0 0 0 / 0%) 100%);
    content: '';
  }
  @media (--sm) {
    min-height: inherit;
    aspect-ratio: 1.8;

    &::after {
      display: none;
    }
  }

  &[data-active] {
    opacity: 1;

    --stagger-offset: 0;
  }

  &.quoted {
    .titles,
    .bodies {
      position: relative;
      display: block;

      &::before,
      &::after {
        color: var(--color-grey-100);
        content: '';
      }

      &::before {
        position: absolute;
        padding-right: 0.04em;
        margin-left: -0.44em;
      }

      &::after {
        padding-left: 0.04em;
        content: '';
      }
    }
  }
}

.slideInner {
  display: flex;
  overflow: hidden;
  height: 100%;
  flex-flow: row wrap;
  align-content: flex-end;
  align-items: flex-end;
  justify-content: flex-start;
  padding: var(--spacer-48) var(--grid-computed-margin-width) var(--spacer-96);
  @media (--sm) {
    /* overflow: hidden; */
    padding: var(--spacer-48) calc(var(--grid-gutter-size) + var(--grid-computed-column-width));
    border-radius: 1.5rem;
  }
}

.slideImageWrapper {
  z-index: auto;
  transform: scale(calc((1 - var(--scroll-progress)) * 0.3 + 1)) translate3d(calc((1 - var(--scroll-progress)) * 10%), 0, 0);

  [data-active] & {
    @media (--sm) {
      transform: scale(1.15) translate3d(calc(var(--active-progress) * 4%), 0, 0);
    }
  }
}

.slideGlow {
  --glow-translate-x: -50%;
  --glow-translate-y: -40%;
}

.slideImageWrapper,
.slideImageOverlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  @media (--sm) {
    overflow: hidden;
    border-radius: 1.5rem;
  }
}

.slideImageOverlay {
  @media (--sm) {
    overflow: hidden;
    border-radius: 1.5rem;
  }

  &::after {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--carousel-gradient-mobile);
    @media (--sm) {
      background: var(--carousel-gradient);
    }
    content: '';
  }

  &.hasNoGradient {
    &::after {
      @media (--sm) {
        background: none;
      }
    }
  }

  &.hasNoGradientMobile {
    &::after {
      background: none;
    }
  }

  &.hasGradientLight {
    &::after {
      @media (--sm) {
        background: var(--carousel-gradient-light);
      }
    }
  }

  &.hasGradientLightMobile {
    &::after {
      background: var(--carousel-gradient-mobile-light);
    }
  }

  &.hasGradientHeavy {
    &::after {
      @media (--sm) {
        background: var(--carousel-gradient-heavy);
      }
    }
  }

  &.hasGradientHeavyMobile {
    &::after {
      background: var(--carousel-gradient-mobile-heavy);
    }
  }
}

.slideText,
.slideLogo {
  opacity: 0;
  transition: opacity 1s var(--ease-out);

  [data-active] & {
    opacity: 1;
  }
}

.slideText {
  position: relative;
  z-index: var(--z-index-1);
  width: var(--slide-text-width);
  color: var(--color-grey-97);

  --slide-text-width: calc(var(--grid-gutter-size) * 3 + var(--grid-computed-column-width) * 4);

  @media (--sm) {
    --slide-text-width: calc(var(--grid-gutter-size) * 7 + var(--grid-computed-column-width) * 8);
  }
}

.slideLogo {
  position: absolute;
  z-index: var(--z-index-1);
  top: var(--nav-height);
  left: var(--spacer-24);
  height: 4rem;
  margin-bottom: var(--spacer-16);
  transform: translate3d(calc(var(--active-progress) * 4rem), 0, 0);

  * {
    fill: var(--color-grey-97);
  }

  @media (--xs) {
    top: calc(var(--nav-height) + var(--spacer-12));
    left: unset;
  }

  @media (--sm) {
    position: relative;
    top: unset;
    left: unset;
    height: 3.5rem;
    margin-bottom: var(--spacer-32);
  }
}

.slideLogoImage {
  height: 4rem;
}

.lineBreak {
  position: relative;
  display: block;
  flex: 0 0 100%;
}

.titles {
  margin-bottom: var(--spacer-24);
}

.bodies,
.label {
  color: var(--color-grey-50);
  grid-column: col 1 / span var(--content-width);
}

.em {
  display: inline-block;
  padding: 0 0.2em var(--spacer-6) 0;
  color: var(--color-grey-90);
}

.title4 {
  composes: title5md4 from '~typo';
  padding-right: 5vw;

  @media (--sm) {
    padding-right: 0;
  }
}

.stagger {
  position: relative;
  display: block;
  transform: translate3d(calc(var(--active-progress) * 4rem), 0, 0);
}
