.hoverCard {
  composes: cardHover from '~g';
  position: relative;
  text-align: left;

  &.hasBorderAsymmetrical {
    --shadow-radius: var(--border-radius-20);
  }

  &.hasBorderCategory {
    --shadow-radius: var(--border-radius-12);
  }

  &.squaredAspectRatio {
    --card-aspect-ratio: 0.86;

    @media (--sm) {
      --card-aspect-ratio: 1;
    }
  }

  &.categoryAspectRatio {
    --card-aspect-ratio: 1.3;

    @media (--sm) {
      --card-aspect-ratio: 1.44;
    }
  }

  @media (hover: hover) {
    &:hover {
      --item-hover-progress: 1;

      .button {
        path {
          fill: var(--button-hover-color);
        }
      }
    }
  }

  &.active {
    --card-hover-progress: 1;
    --item-hover-progress: 1;

    .textContainer {
      transition: transform var(--duration-400ms) var(--ease-basic-butter) 0.02s;
    }

    .bodyWrapper {
      opacity: 1;
      transition: opacity var(--duration-400ms) var(--ease-embellishment) var(--duration-60ms), visibility 0ms;
      visibility: visible;
    }
  }
}

.shadowContainer {
  z-index: var(--z-index-1);
}

.glow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hoverCardInner {
  position: relative;
  z-index: var(--z-index-1);
  display: flex;
  overflow: hidden;
  width: 100%;
  align-items: flex-end;
  justify-content: space-between;
  padding: var(--spacer-24);
  aspect-ratio: var(--card-aspect-ratio, 1);
  border-radius: var(--shadow-radius);
  gap: var(--spacer-12);

  &::before {
    position: absolute;
    content: '';
    inset: 0;

    /* .hoverCard.hasGradientCategory & {
      background: var(--hover-card-category-gradient);
    } */
  }

  &::after {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 101%;
    height: calc(100% + 5rem);
    background: var(--hover-card-hover-gradient);
    content: '';
    opacity: 0;
    transform: translateY(5rem);
    transition: opacity var(--duration-400ms) var(--ease-embellishment) 0s, transform var(--duration-500ms) var(--ease-embellishment) 0s;

    .hoverCard.hasGradientAsymmetrical & {
      background: var(--hover-card-asymmetrical-hover-gradient);
      opacity: 1;
    }
  }

  .hoverCard.active &::before {
    opacity: 0;
    transition: opacity var(--duration-500ms) var(--ease-embellishment) 0.02s, transform var(--duration-200ms) var(--ease-embellishment) 0s;
  }

  .hoverCard.active &::after {
    opacity: 1;
    transform: translateY(0);
    transition: opacity var(--duration-500ms) var(--ease-embellishment) 0.02s, transform var(--duration-200ms) var(--ease-embellishment) 0s;
  }

  .imageContainer {
    position: absolute;
    z-index: var(--z-index-neg);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: scale(calc(var(--card-hover-progress, 0) * var(--default-card-hover-scale-amount) + 1));
    transition: var(--card-hover-transform-default-transition);
  }
}

.eyebrow,
.textContainer {
  pointer-events: none;
}

.eyebrow {
  composes: label from '~typo';
  position: absolute;
  z-index: var(--z-index-1);
  top: 0;
  left: 0;
  padding: var(--spacer-24);
  color: var(--color-accent);
  font-weight: 600;
  white-space: nowrap;
}

.textContainer {
  z-index: var(--z-index-1);
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: flex-start;
  transform: translate3d(0, var(--translate-offset), 0);
  transition: transform var(--duration-400ms) var(--ease-embellishment) 0s;
}

.titleWrapper {
  position: relative;
}

.titleWithoutLogo,
.titleWithLogo {
  line-height: 1;
}

.logo {
  position: relative;
  height: 3.5rem;
  margin-bottom: var(--spacer-8);
}

.title {
  display: block;
  line-height: 1.3;
}

.titleWithoutLogo {
  composes: title6 from '~typo';
  color: var(--color-grey-100);
}

.titleWithLogo {
  composes: bodySmall from '~typo';
  color: var(--color-grey-60);
}

.bodyWrapper {
  position: absolute;
  top: 100%;
  display: flex;
  flex-flow: row wrap;
  color: var(--color-grey-60);
  opacity: 0;
  transition: opacity var(--duration-200ms) var(--ease-embellishment) 0s, visibility 0ms linear var(--duration-200ms);
  visibility: hidden;
}

.body {
  flex: 1 0 100%;
  margin-top: var(--spacer-12);
}

.bodies,
.titles,
.accents {
  composes: bodySmall from '~typo';
  margin-top: var(--spacer-8);

  &:first-child {
    margin-top: 0;
  }
}

.bodies {
  color: var(--color-grey-70);
}

.link {
  color: var(--color-grey-60);
  pointer-events: auto;
  text-decoration: underline;
}

.button {
  position: relative;
  z-index: var(--z-index-1);
  flex-grow: 0;
  flex-shrink: 0;
  pointer-events: none;
}

.buttonOverlay {
  position: absolute;
  z-index: var(--z-index-1);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
