.hero {
  --overlay-opacity: 0;
  composes: overlaid from '~deco';
  position: relative;
  width: 100%;
  padding-top: var(--nav-height);
  background-color: var(--color-grey-00);
}

.hasBackground {
  &::before {
    position: absolute;
    z-index: var(--z-index-background-color);
    display: block;
    background: var(--module-background, var(--color-grey-00));
    content: '';
    inset: 0;
  }
}

.container {
  composes: grid from '~g';
  position: relative;
  z-index: var(--z-index-1);

  /* accomodates for bottom browser bar in ios safari */
  min-height: calc(100vh - var(--nav-height));
  align-content: end;

  --hero-border-radius: var(--border-radius-40);
  --hero-top-padding: calc(var(--nav-height) + var(--spacer-16));
  padding: var(--hero-top-padding) 0 calc(var(--spacer-64) + 8vh);

  @media (--sm) {
    --hero-top-padding: var(--spacer-96);
    padding: var(--hero-top-padding) 0;
  }

  @media (--md) {
    --hero-top-padding: var(--spacer-124);
    padding: var(--hero-top-padding) 0;
  }

  .valignMiddle & {
    @media (--md) {
      align-content: center;
    }
  }

  /* this is the layout used by customer case studies */

  .isGarageDoor & {
    --hero-bottom-padding: calc(var(--spacer-64) + var(--hero-border-radius) + 8vh);

    /* accomodates for bottom browser bar in ios safari */
    min-height: calc(100vh - var(--nav-height) + var(--hero-border-radius));
    padding: var(--hero-top-padding) 0 var(--hero-bottom-padding);

    @media (--sm) {
      --hero-bottom-padding: calc(var(--hero-top-padding) + var(--hero-border-radius));
    }
  }

  /* this is the layout used for most sub-pages */

  .isGarageDoor.hasGradientSuperHeavyMobile & {
    --hero-bottom-padding: calc(var(--spacer-24) + var(--hero-border-radius));

    @media (--xs) {
      --hero-bottom-padding: calc(var(--spacer-24) + var(--hero-border-radius) + 8vh);
    }

    @media (--sm) {
      --hero-bottom-padding: calc(var(--spacer-64) + var(--hero-border-radius) + 8vh);
    }
  }
}

.content {
  composes: subgrid from '~g';

  --subgrid-start: start;
  --subgrid-width: var(--grid-number-of-columns);

  .hasVideo & {
    margin-bottom: var(--spacer-32);
  }
}

.accents,
.titles {
  grid-column: start / end;

  @media (--sm) {
    grid-column: col 2 / span 11;
  }

  @media (--md) {
    grid-column: start / span 10;
  }

  .ragTight & {
    @media (--xs-only) {
      grid-column: start / span 3;
    }

    @media (--sm) {
      grid-column: col 2 / span 6;
    }

    @media (--md) {
      grid-column: start / span 6;
    }
  }

  .ragWide & {
    @media (--sm) {
      grid-column: col 2 / span 12;
    }

    @media (--md) {
      grid-column: start / span 12;
    }
  }

  .alignCenter & {
    @media (--sm) {
      grid-column: col 3 / span 10;
      text-align: center;
    }

    @media (--md) {
      grid-column: col 4 / span 8;
    }
  }

  .alignCenter.ragTight & {
    @media (--sm) {
      grid-column: col 3 / span 10;
      text-align: center;
    }

    @media (--md) {
      grid-column: col 4 / span 8;
    }
  }

  .alignCenter.ragWide & {
    @media (--sm) {
      grid-column: col 2 / span 12;
      text-align: center;
    }

    @media (--md) {
      grid-column: col 1 / span 14;
    }
  }
}

.accents {
  color: var(--color-grey-97);
}

.titles,
.bodies,
.backgroundWrapper {
  backface-visibility: hidden;
  transition: transform 0.1s;
}

.bodies,
.buttons,
.forms {
  grid-column: start / end;

  @media (--sm) {
    grid-column: col 2 / span 10;
  }

  @media (--md) {
    grid-column: start / span 8;
  }

  .ragTight & {
    @media (--sm) {
      grid-column: col 2 / span 6;
    }

    @media (--md) {
      grid-column: start / span 6;
    }
  }

  .ragWide & {
    @media (--sm) {
      grid-column: col 2 / span 10;
    }

    @media (--md) {
      grid-column: start / span 8;
    }
  }

  .alignCenter & {
    @media (--sm) {
      grid-column: col 3 / span 10;
      text-align: center;
    }

    @media (--md) {
      grid-column: col 4 / span 8;
    }
  }

  .alignCenter.ragTight & {
    @media (--sm) {
      grid-column: col 3 / span 10;
      text-align: center;
    }

    @media (--md) {
      grid-column: col 4 / span 10;
    }
  }

  .alignCenter.ragWide & {
    @media (--sm) {
      grid-column: col 3 / span 10;
      text-align: center;
    }

    @media (--md) {
      grid-column: col 4 / span 8;
    }
  }
}

.svgOrImage {
  grid-column: start / end;

  @media (--sm) {
    grid-column: col 2 / end;
  }

  @media (--md) {
    grid-column: start / end;
  }
}

.bodies {
  color: var(--color-grey-60);

  .isHighContrast & {
    color: var(--color-grey-80);
  }

  .ragTight & {
    @media (--xs-only) {
      padding-right: 3vw;
    }
  }
}

/* TODO: remove */
.forms,
.buttons {
  .alignCenter & {
    margin-right: auto;
    margin-left: auto;
  }
}

.forms {
  .alignCenter & {
    @media (--md) {
      max-width: 100%;
      grid-column: col 5 / span 6;
    }
  }
}

.ctaContainer {
  grid-column: 2 / span 4;

  @media (--sm) {
    display: flex;
    align-items: center;
    gap: var(--spacer-40);
    grid-column: 3 / span 13;
  }

  @media (--md) {
    grid-column: start / end;
  }

  &.alignCenter {
    @media (--sm) {
      display: flex;
      justify-content: center;
      grid-column: wrapper-start / wrapper-end;
    }
  }
}

.videoButton {
  position: relative;
  grid-column: start / end;
}

.secondaryCta {
  align-items: center;
  margin-top: var(--spacer-24);
  grid-column: start / end;

  @media (--sm) {
    margin-top: 0;
  }
}

/* portable text elements */

.backgroundWrapper {
  position: absolute;
  z-index: var(--z-index-0);

  /* top: var(--nav-height); */
  top: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;

  @media (--xs) {
    top: 0;
    height: 100%;
  }

  &::before {
    position: absolute;
    z-index: var(--z-index-3);
    overflow: hidden;
    background: var(--background-gradient-mobile);
    content: '';
    inset: 0;

    @media (--sm) {
      background: var(--background-gradient);
    }
  }

  --background-gradient: var(--hero-gradient-medium);
  --background-gradient-mobile: var(--hero-gradient-medium);

  /* default alignment uses default gradients */

  &.hasNoGradientMobile {
    &::before {
      --background-gradient-mobile: none;
    }
  }

  &.hasNoGradient {
    &::before {
      @media (--sm) {
        --background-gradient: none;
      }
    }
  }

  &.hasGradientLightMobile {
    &::before {
      --background-gradient-mobile: var(--hero-gradient-light);
    }
  }

  &.hasGradientLight {
    &::before {
      @media (--sm) {
        --background-gradient: var(--hero-gradient-light);
      }
    }
  }

  &.hasGradientMediumMobile {
    &::before {
      --background-gradient-mobile: var(--hero-gradient-medium);
    }
  }

  &.hasGradientMedium {
    &::before {
      @media (--sm) {
        --background-gradient: var(--hero-gradient-medium);
      }
    }
  }

  &.hasGradientHeavyMobile {
    &::before {
      --background-gradient-mobile: var(--hero-gradient-heavy);
    }
  }

  &.hasGradientHeavy {
    &::before {
      @media (--sm) {
        --background-gradient: var(--hero-gradient-heavy);
      }
    }
  }

  &.hasGradientSuperHeavyMobile {
    height: calc(80% - var(--nav-height));

    /* height: 100%; */

    @media (--xs) {
      height: calc(90% - var(--nav-height));
    }

    @media (--sm) {
      height: 100%;
    }

    &::before {
      --background-gradient-mobile: var(--hero-gradient-super-heavy);
    }
  }

  &.hasGradientSuperHeavy {
    &::before {
      @media (--sm) {
        --background-gradient: var(--hero-gradient-super-heavy);
      }
    }
  }

  /* center alignment uses center gradients */

  &.alignCenter.hasNoGradientMobile {
    &::before {
      --background-gradient-mobile: none;
    }
  }

  &.alignCenter.hasNoGradient {
    &::before {
      @media (--sm) {
        --background-gradient: none;
      }
    }
  }

  &.alignCenter.hasGradientLightMobile {
    &::before {
      --background-gradient-mobile: var(--hero-center-gradient-light);
    }
  }

  &.alignCenter.hasGradientLight {
    &::before {
      @media (--sm) {
        --background-gradient: var(--hero-center-gradient-light);
      }
    }
  }

  &.alignCenter.hasGradientMediumMobile {
    &::before {
      --background-gradient-mobile: var(--hero-center-gradient-medium);
    }
  }

  &.alignCenter.hasGradientMedium {
    &::before {
      @media (--sm) {
        --background-gradient: var(--hero-center-gradient-medium);
      }
    }
  }

  &.alignCenter.hasGradientHeavyMobile {
    &::before {
      --background-gradient-mobile: var(--hero-center-gradient-heavy);
    }
  }

  &.alignCenter.hasGradientHeavy {
    &::before {
      @media (--sm) {
        --background-gradient: var(--hero-center-gradient-heavy);
      }
    }
  }

  &.alignCenter.hasGradientSuperHeavyMobile {
    &::before {
      --background-gradient-mobile: var(--hero-center-gradient-super-heavy);
    }
  }

  &.alignCenter.hasGradientSuperHeavy {
    &::before {
      @media (--sm) {
        --background-gradient: var(--hero-center-gradient-super-heavy);
      }
    }
  }
}

.hero .backgroundImageContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.bottomGlow {
  position: absolute;
  z-index: var(--z-index-neg);
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20rem;
  transform: scaleX(1.5) rotate(-10deg);
}
