/* TODO: what is this file? */

.content {
  composes: subgrid from '~g';

  --subgrid-start: start;
  --subgrid-width: var(--grid-number-of-columns);

  &:not(.alignCenter) {
    .bodies,
    .list {
      @media (--md) {
        --content-width: 8;
      }
    }
  }
}

/* TODO: remove or at minimum move to portableText/styles */

.alignCenter {
  .accents,
  .titles,
  .bodies {
    @media (--sm) {
      text-align: center;
    }
  }

  .button {
    @media (--sm) {
      margin-right: auto;
      margin-left: auto;
    }
  }
}

.title0 {
  background: linear-gradient(60deg, var(--color-accent) -120%, var(--color-grey-100) 50%);
  background-clip: text;
  -webkit-text-fill-color: transparent;

  @media (--sm) {
    grid-column: start / end;
  }
}
