.button {
  position: relative;
  display: flex;
  align-items: center;

  &:hover {
    --item-hover-progress: 1;

    path {
      fill: var(--button-hover-color);
    }
  }
}

.pillButton {
  .iconContainer {
    position: relative;
    display: flex;
    width: var(--spacer-24);
    align-items: center;
    margin: -1.1em 0.25em -1em -0.5em;
  }

  .icon {
    width: 100%;
  }
}

.buttonLabel {
  composes: body from '~typo';
  align-self: center;
  padding-left: var(--spacer-24);
  color: var(--color-grey-97);
  white-space: nowrap;
}
