.paginationDotsContainer {
  position: relative;
  display: flex;
  overflow: hidden;
  width: 10rem;
  margin: auto;
}

.paginationDotsList {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: flex-start;

  &.center {
    justify-content: center;
  }
}

.dotItem {
  position: relative;
  opacity: 0.3;
  transition: opacity var(--duration-600ms) var(--ease-out);

  &:first-child {
    margin-left: 0;
  }

  &.active {
    opacity: 1;
  }
}

.dotButton {
  position: relative;
  overflow: hidden;
  width: 2rem;
  height: 2rem;
  border-radius: 1rem;
}

.dotFigure {
  position: absolute;
  top: calc(50% - 0.35rem);
  left: calc(50% - 0.35rem);
  width: 0.7rem;
  height: 0.7rem;
  background: var(--color-grey-100);
  border-radius: 50%;
}
