.chiclet {
  --transition-out-progress: 0;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: var(--spacer-8);

  @media (--sm) {
    gap: var(--spacer-12);
    transform: translate3d(0, calc(var(--transition-out-progress) * 60%), 0);
  }

  /* TODO: eventually removing translateX from rows on chiclet */

  /*
  the following translateX is being created to simulate the offset each row has to each other.
  Once we have this animating, the offset will likely be done via javascript (tbd)

  At the moment the values below represent half of each chicletItem floored
  e.g.: 35vw / 2 = 12.5vw = 12vw
  */
  & > * {
    &:nth-child(odd) {
      .innerRow {
        transform: translateX(-51vw);

        @media (--xs) {
          transform: translateX(-22vw);
        }

        @media (--sm) {
          transform: translateX(-25vw);
        }

        @media (--md) {
          transform: translateX(-12vw);
        }

        @media (--lg) {
          transform: translateX(-10vw);
        }
      }
    }
  }
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.contentWrapper {
  composes: grid from '~g';
}

.row {
  display: flex;
  overflow-x: hidden;
}

.innerRow {
  display: flex;
  gap: var(--spacer-8);

  @media (--md) {
    gap: var(--spacer-12);
  }
}

.chicletItem {
  width: calc(100vw / 1.6);

  @media (--xs) {
    width: calc(100vw / 3);
  }

  @media (--sm) {
    width: calc(100vw / 3.2);
  }

  @media (--md) {
    width: calc(100vw / 5);
  }

  @media (--lg) {
    width: calc(100vw / 5.5);
  }
}

.chicletInner {
  height: 8.8rem;
  align-items: center;
  border: 0.05rem solid var(--color-alpha-light-10);
  border-radius: var(--spacer-18);
}

.text {
  composes: subgrid from '~g';
  margin-bottom: var(--spacer-96);
  grid-column: start/end;

  @media (--sm) {
    margin-bottom: var(--spacer-124);
  }
}
