.dummy {
  position: relative;
  height: 0;
  align-self: center;
  padding-top: 47%; /* ratio */
  grid-column: start/end;
  pointer-events: none;
}

.modalBackground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.modalWrapper {
  composes: grid from '~g';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  grid-column: unset;
}

.modalContent {
  composes: subgrid from '~g';
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  width: var(--width, 100vw);
  height: var(--height, 47vw); /* 0.47 card ratio */
  border-radius: var(--card-border-radius);
  grid-column: unset; /* for some reason it mess with top/left positioning */
  opacity: 0;
  transform-origin: top left; /* important for positioning at exact spot where the card was */
}

.carousel {
  position: relative;
  display: flex;
  height: var(--height);
  align-items: center;
  justify-content: flex-start;
  grid-column: start / end;
  transform: translateX(calc(var(--carousel-index) * -100%));
  transition: transform var(--duration-300ms) var(--ease-basic-butter);
}

/* Carousel items */
.carouselItem {
  composes: subgrid from '~g';
  position: relative;
  min-width: var(--width);
  height: 100%;
  padding-bottom: calc(10.2rem + var(--spacer-48));

  /* shadow for readability */
  &::before {
    position: absolute;
    z-index: var(--z-index-1);
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, var(--transparent) -20%, var(--color-alpha-black-90) 108%);
    content: '';
    opacity: var(--shadow-opacity, 0);
    pointer-events: none;
  }
}

.testimonialMedia {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  .testimonialMediaImg {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.testimonialContent {
  position: relative;
  z-index: var(--z-index-2);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: var(--spacer-54);
  grid-column: 2 / span 5;
  opacity: var(--item-progress);
  transform: translateX(calc(var(--item-progress) * 2rem));
}

.testimonialTitle {
  composes: title6 from '~typo';
  z-index: var(--z-index-2);
}

/* Controls */
.controls {
  position: absolute;
  z-index: var(--z-index-3);
  bottom: 0;
  display: flex;
  width: 100%;
  height: 10.2rem;
  justify-content: space-between;
  grid-column: 2 / span 12;

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 0.1rem;
    background-color: var(--color-grey-50);
    content: '';
    transform: scaleX(var(--controls-progress, 0));
    transform-origin: center left;
  }
}

.controlsButton {
  composes: medium from '~typo';
  padding: 0;
  cursor: pointer;

  .controlsButtonLabel {
    display: inline-block;
    padding: 0 var(--spacer-8);
  }

  .controlsButtonArrow {
    display: inline-block;
    width: 1rem;

    path {
      fill: var(--color-grey-100);
    }
  }

  &:first-child {
    .controlsButtonArrow {
      transform: scaleX(-1);
    }
  }

  &:disabled {
    opacity: 0.1;
    pointer-events: none;
  }
}

.closeButton {
  position: absolute;
  top: var(--spacer-40);
  right: var(--spacer-40);
  width: 4rem;
  height: 4rem;
  padding: 0;
  cursor: pointer;
  transform-origin: top right;

  &::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border: 0.1rem solid var(--color-alpha-light-15);
    border-radius: 50%;
    content: '';
    transition: transform var(--duration-300ms) var(--ease-basic-butter);
  }

  &:hover {
    &::before {
      transform: scale(1.2);
    }
  }

  .buttonIcon {
    width: 1rem;
    height: 1rem;
    transform: rotate(calc(var(--rotate, 0) * 1deg));
    transform-origin: center center;
  }
}
