.caseStudy {
  composes: grid from '~g';
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.sideRail {
  margin-top: var(--spacer-160);
  grid-column: start / end;

  @media (--sm) {
    padding-top: var(--spacer-40);
    border-top: 1px solid var(--color-alpha-light-20);
    grid-column: start / col 5;
    grid-row: 1 / 3;
  }

  @media (--md) {
    grid-column: start / col 4;
  }
}

.sideRailLogo {
  width: 50%;
  margin-bottom: var(--spacer-24);

  @media (--sm) {
    width: 100%;
    margin-bottom: var(--spacer-40);
  }

  img,
  svg {
    width: auto;
    height: 2.8rem;
  }
}

.smallTitles {
  margin-top: var(--spacer-64);

  @media (--sm) {
    margin-top: var(--spacer-64);
  }
  @media (--md) {
    margin-top: var(--spacer-64);
  }
}

.sideRailText {
  margin-bottom: var(--spacer-24);

  @media (--sm) {
    margin-bottom: var(--spacer-48);
  }
}

.sideRailStats {
  padding-top: var(--spacer-20);
  border-top: 1px solid var(--color-alpha-light-20);

  @media (--sm) {
    margin-bottom: var(--spacer-16);
  }
}

.sideRailAccents,
.sideRailBodies {
  margin-bottom: var(--spacer-8);
  color: var(--color-grey-60);

  em {
    color: var(--color-grey-88);
  }

  @media (--sm) {
    margin-bottom: var(--spacer-8);
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.sideRailBodies {
  display: inline-block;

  &:not(:last-of-type)::after {
    display: inline-block;
    content: ',\00a0';
  }

  @media (--sm) {
    display: block;

    &:not(:last-of-type)::after {
      content: none;
    }
  }
}

.textContainer,
.speedbbumpContainer {
  /* The aside is actually the first child */
  &:nth-child(2) {
    margin-top: var(--spacer-96);
    @media (--sm) {
      padding-top: var(--spacer-40);
      border-top: 1px solid var(--color-alpha-light-20);
      margin-top: var(--spacer-160);
    }
  }

  &:last-child {
    margin-bottom: var(--spacer-96);
    @media (--sm) {
      margin-bottom: var(--spacer-160);
    }
  }
}

.textContainer {
  margin-bottom: var(--spacer-96);
  grid-column: start / end;

  @media (--sm) {
    margin-bottom: var(--spacer-124);
    grid-column: col 5 / span 8;
  }

  @media (--md) {
    grid-column: col 5 / span 7;
  }

  /* The aside is actually the first child */
  &:nth-child(2) {
    @media (--sm) {
      grid-column: col 5 / end;
    }
  }
}

.bodies {
  margin-top: var(--spacer-16);
}

.bodyLarge {
  color: var(--color-grey-100);
}

.speedbbumpContainer {
  margin-bottom: var(--spacer-96);
  grid-column: wrapper-start / wrapper-end;

  @media (--sm) {
    margin-bottom: var(--spacer-124);
  }
}
