.formInput {
  composes: button from '~typo';
  composes: inputField from '~deco';
  padding: var(--spacer-16) var(--spacer-24) 1.4rem; /* value should be "18 36" should we ignore spacers? */
  background-color: var(--color-alpha-black-40);
  border-radius: 10em;
  color: var(--input-color, --color-grey-88);
  outline: 0;
  transition-duration: var(--duration-500ms);
  transition-property: background-color, color;
  transition-timing-function: var(--ease-basic-butter);

  &:hover {
    background-color: var(--color-alpha-black-100);

    &::placeholder {
      color: var(--color-grey-100);
    }
  }

  &::placeholder {
    color: var(--placeholder-color, --color-grey-70);
  }

  &.disabled {
    color: var(--color-grey-50);
  }

  &.disabled,
  &::placeholder {
    transition-duration: var(--duration-500ms);
    transition-property: color, border;
    transition-timing-function: var(--ease-basic-butter);
  }

  &:not(:hover):focus {
    &::placeholder {
      color: var(--color-grey-100);
    }
  }
}

.hasError {
  border-color: var(--color-coral-dark);
}
