.itemList {
  composes: grid from '~g';
  position: relative;
  padding-top: var(--spacer-96);
  padding-bottom: var(--spacer-96);
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.content {
  composes: subgrid from '~g';

  --subgrid-start: start;
  --subgrid-width: var(--grid-number-of-columns);
}

.eyebrow,
.titles,
.bodies,
.buttons {
  grid-column: start / end;

  @media (--sm) {
    grid-column: col 2 / span 12;
  }

  .alignCenter & {
    @media (--sm) {
      text-align: center;
    }
  }
}

.eyebrow {
  composes: eyebrow from '~typo';
}

.bodies {
  margin-top: var(--spacer-20);
  color: var(--color-grey-60);

  @media (--md) {
    grid-column: col 2 / span 8;
  }

  .alignCenter & {
    @media (--sm) {
      grid-column: col 4 / span 8;
    }
  }
}

.buttons {
  margin-top: var(--spacer-32);
}

.lists {
  composes: subgrid from '~g';
  margin-top: var(--spacer-48);
  row-gap: var(--spacer-64);

  --list-width: 4;

  @media (--sm) {
    row-gap: var(--spacer-64);

    --subgrid-start: col 2;
    --subgrid-width: calc(var(--grid-number-of-columns) - 2);
  }

  &.with3Lists {
    @media (--sm) {
      --list-width: 4;
    }
  }

  &.with4Lists {
    @media (--sm) {
      --subgrid-start: col 2;
      --list-width: 6;
    }
  }
}

.list {
  margin-bottom: var(--spacer-16);
  grid-column: auto / span var(--list-width);

  @media (--sm) {
    margin-bottom: var(--spacer-24);
  }

  @media (--md) {
    margin-bottom: var(--spacer-16);
  }
}

.listLabel {
  composes: title6 from '~typo';
  margin-bottom: var(--spacer-32);

  @media (--sm) {
    margin-bottom: var(--spacer-24);
  }
}

.listItem {
  display: flex;
  align-items: center;
  margin-bottom: var(--spacer-16);
}

.listItem:last-of-type {
  margin-bottom: 0;
}

.listItemIcon {
  width: 1.9rem;
  flex-shrink: 0;
  margin-right: 1.5rem;
}

.listItemIcon path {
  fill: var(--color-accent, var(--color-iris));
}

.listItemText {
  composes: bodySmall from '~typo';
  color: var(--color-grey-70);
}

.leftGlow {
  position: absolute;
  z-index: var(--z-index-neg);
  top: 0;
  left: 90vw;
  width: 40vw;
  height: 40vh;
  transform: translate3d(0, -50%, 0);

  @media (--sm) {
    top: 45vh;
    left: 90vw;
    width: 50rem;
    height: 90%;
  }
}
