.largeStats {
  position: relative;
  padding-top: var(--spacer-160);
  padding-bottom: var(--spacer-124);
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.container {
  composes: grid from '~g';
  position: relative;
  z-index: var(--z-index-1);
}

.columnLeft {
  position: relative;
  margin-bottom: var(--spacer-64);
  grid-column: start / end;

  @media (--sm) {
    position: sticky;
    top: calc(var(--nav-height) + var(--spacer-24));
    margin-bottom: 0;
    grid-column: col 2 / span 5;
  }

  .titles {
    color: var(--color-grey-50);

    /* this block has <br />s set in the cms so adding this to maintain ragging across all breakpoints */
    white-space: nowrap;

    .em {
      display: block;
      padding-right: 15%;
      color: var(--color-grey-100);
    }
  }
}

.columnRight {
  grid-column: start / end;

  @media (--sm) {
    grid-column: col 8 / span 6;
    grid-row: 1 / span 9;
  }

  .stats {
    margin-top: 0;

    @media (--sm) {
      --subgrid-start: col 1;
      --subgrid-width: 6;
    }
  }
}

.topLeftGlow {
  position: absolute;
  z-index: var(--z-index-0);
  top: 0;
  left: 0;
  width: 150rem;
  height: 50rem;
  transform: translate(-100%, -50%);
}

.rightGlow {
  position: absolute;
  z-index: var(--z-index-1);
  right: 0;
  bottom: 0;
  width: 50rem;
  height: 90vh;
  transform: translate(90%, 40%);
}
