.logoTextItem {
  position: relative;
  display: flex;
  overflow: hidden;
  flex-direction: column;
  padding: var(--spacer-16) var(--spacer-24);
  background-color: var(--color-grey-05);
  border-radius: var(--spacer-6);
  transition: var(--duration-500ms) background-color var(--ease-basic-butter);

  &::after {
    position: absolute;
    z-index: 2;
    border-radius: var(--spacer-6);
    box-shadow: inset 0 0 0 0.2rem var(--color-grey-15);
    content: '';
    inset: 0;
    opacity: 0;
    transition: var(--duration-500ms) opacity var(--ease-basic-butter);
  }

  @media (--sm) {
    flex-direction: row;
    align-items: center;
    padding: var(--spacer-28);
  }
}

.icon {
  width: 2.5rem;
  height: 2.5rem;
  flex-shrink: 0;
  margin-bottom: var(--spacer-8);

  @media (--sm) {
    margin-right: var(--spacer-24);
    margin-bottom: initial;
  }
}

.label {
  composes: bodySmall from '~typo';
  composes: ellipsis from '~deco';
  color: var(--color-grey-70);
}

.chiclet {
  flex-direction: row;
  border: 1px solid var(--color-alpha-light-10);
  background-color: var(--color-alpha-dark-70);

  .icon {
    margin-right: var(--spacer-24);
    margin-bottom: initial;
  }

  .label {
    color: var(--color-grey-80);
  }
}
