.integrationTable {
  composes: grid from '~g';

  --content-width: 4;
  --group-width: 4;
  position: relative;

  @media (--sm) {
    --content-width: 12;
    --group-width: 8;
  }

  @media (--md) {
    --group-width: 9;
  }

  &::before {
    position: absolute;
    z-index: var(--z-index-0);
    border: 0.1rem solid rgb(37 39 54 / 25%);
    background: var(--integrations-table-background-gradient);
    border-radius: var(--border-radius-20);
    content: '';
    inset: 0;

    @media (--sm) {
      grid-column: start / end;
    }
  }
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.topBackGlow {
  position: absolute;
  top: -5vh;
  right: 0;
  left: 0;
  height: 100vh;
}

.backGlow {
  position: absolute;
  top: 20%;
  right: 20vw;
  bottom: 20%;
  left: 0;
}

.integrationTableInner {
  composes: subgrid from '~g';
  position: relative;
  z-index: var(--z-index-1);
  padding-top: var(--spacer-64);
  padding-bottom: var(--spacer-64);
}

.header {
  composes: subgrid from '~g';
  position: relative;
  align-items: center;
  padding-bottom: var(--spacer-32);

  @media (--sm) {
    --subgrid-start: col 2;
    --subgrid-width: var(--content-width);
    padding-bottom: var(--spacer-40);
  }

  @media (--md) {
    padding-bottom: var(--spacer-64);
  }
}

.keyline {
  --keyline-scale: 0;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  transform: scaleX(var(--keyline-scale));
  transform-origin: center left;
  transition: transform var(--duration-400ms) var(--ease-out);

  @media (--sm) {
    background-color: var(--color-grey-15);
  }
}

.headerTitle {
  composes: title5 from '~typo';
  margin-bottom: var(--spacer-24);
  color: var(--color-grey-100);
  grid-column: start / span 4;

  @media (--sm) {
    grid-column: start / span 6;
  }

  @media (--md) {
    grid-column: start / span 8;
  }
}

.headerSearch {
  grid-column: start / span 4;

  @media (--sm) {
    color: var(--color-grey-90);
    grid-column: auto / span 6;
  }

  @media (--md) {
    grid-column: auto / span 4;
  }
}

.anchorNav {
  display: none;
  grid-template-rows: auto 1fr;

  @media (--sm) {
    composes: subgrid from '~g';
    display: grid;
    margin-top: var(--spacer-64);

    --subgrid-start: col 2;
    --subgrid-width: 4;
  }

  @media (--md) {
    --subgrid-start: col 2;
    --subgrid-width: 3;
  }
}

.anchorNavItems {
  position: sticky;
  top: calc(var(--nav-height) + var(--spacer-24));
  grid-column: start / span 3;

  @media (--sm) {
    grid-column: start / span 4;
  }

  @media (--md) {
    grid-column: start / span 3;
  }

  & > * {
    margin-bottom: var(--spacer-20);

    &:last-child {
      margin-bottom: initial;
    }
  }
}

/* TODO: Rework the animation if needed after we define our animation conventions */
.anchorNavItem {
  composes: label from '~typo';
  position: relative;
  display: block;
  color: var(--color-grey-60);
  text-align: left;
  text-decoration: none;
  transition: color var(--duration-200ms);

  --dot-scale: 0.5;

  &::before {
    position: absolute;
    top: 0.8rem;
    left: 0;
    width: 0.8rem;
    height: 0.8rem;
    background-color: var(--color-cobalt);
    border-radius: 50%;
    content: '';
    opacity: 0;
    transform: translateX(-100%) translateX(-1rem) scale(var(--dot-scale));
    transition: opacity var(--duration-200ms), transform var(--duration-200ms);
  }
}

.active {
  color: var(--color-grey-90);

  --dot-scale: 1;

  &::before {
    opacity: 1;
  }
}

.groups {
  composes: subgrid from '~g';

  --subgrid-width: var(--group-width);

  @media (--sm) {
    --subgrid-start: col 6;
    margin-top: var(--spacer-64);
  }

  @media (--md) {
    --subgrid-start: col 5;
  }

  & > * {
    margin-bottom: var(--spacer-64);

    &:last-child {
      margin-bottom: initial;
    }
  }
}

.groupContainer {
  --subgrid-start: start;
  --subgrid-width: var(--group-width);
  composes: subgrid from '~g';
}

.groupTitle {
  composes: title6 from '~typo';
  margin-bottom: var(--spacer-20);
  grid-column: start / span 4;

  @media (--sm) {
    margin-bottom: var(--spacer-24);
    grid-column: start / span var(--group-width);
  }
}

.groupItems {
  composes: subgrid from '~g';
  row-gap: var(--grid-gutter-size);

  --subgrid-start: start;
  --subgrid-width: var(--group-width);
}

.groupItem {
  grid-column: auto / span 2;
  grid-column-gap: var(--grid-gutter-size);

  @media (--sm) {
    grid-column: auto / span 4;
  }

  @media (--md) {
    grid-column: auto / span 3;
  }
}

.integrationItem {
  &:hover {
    border-color: var(--color-grey-15);
    background-color: transparent;

    &::after {
      opacity: 1;
    }
  }
}

.label {
  text-align: left;
}
