.fullscreenVideo {
  composes: grid from '~g';
  composes: overlaid from '~deco';
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  align-content: center;
  border-radius: var(--top-rounded-module-border-radius);

  --scroll-progress-opacity: 0;
  --scroll-progress-y: 0;

  @media (--sm) {
    height: auto;
    aspect-ratio: 1.77;
  }
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.posterImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.posterImageContainer {
  background: var(--color-grey-00);
  opacity: var(--scroll-progress-opacity);
  transform: translate3d(0, var(--scroll-progress-y), 0);
}

.backgroundWrapper,
.posterImageContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.contentWrapper {
  position: relative;
  z-index: var(--z-index-1);
  display: flex;
  height: 100%;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding-bottom: var(--spacer-40);
  grid-column: start / end;

  @media (--sm) {
    align-items: center;
    justify-content: center;
    padding-bottom: 0;
  }

  @media (--sm) {
    padding-bottom: var(--spacer-48);
  }
}

.textOverlay {
  composes: title5 from '~typo';
  display: flex;
  flex-wrap: wrap;
  align-self: flex-start;
  justify-content: center;
  margin-bottom: var(--spacer-24);
  font-weight: 500;
  line-height: 1.33;

  @media (--sm) {
    margin-bottom: var(--spacer-40);
    font-size: 3.2rem;
    letter-spacing: -0.035em;
    line-height: 1.31;
    text-align: center;
  }

  @media (--md) {
    margin-bottom: var(--spacer-64);
    font-size: 4.2rem;
    line-height: 1.38;
  }
}

.buttonWrapper {
  position: relative;
}

.buttonCloneWrapper,
.buttonClone {
  position: absolute;
  inset: 0;
  opacity: 0;
  pointer-events: none;
}

.buttonCloneIcon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.buttonCloneLabel {
  position: absolute;
  top: 50%;
  left: 50%;
  clip-path: unset;
  transform: translate(-100%, -50%);
  white-space: nowrap;
}

.buttonLabel.buttonCloneLabelTarget {
  clip-path: unset;
  white-space: nowrap;
}

.button {
  position: relative;
}

.buttonIcon {
  display: flex;
  width: var(--spacer-48);
  height: var(--spacer-48);
  align-items: center;
  justify-content: center;
  background-color: var(--color-grey-97);
  border-radius: 50%;
}

.buttonLabel {
  composes: body from '~typo';
  align-self: center;
  padding-left: var(--spacer-24);

  /* clip-path: polygon(calc(100% * (1 - var(--clip-progress))) 0%, 200% 0%, 100% 100%, calc(100% * (1 - var(--clip-progress))) 100%); */
  color: var(--color-grey-97);

  --clip-progress: 1;
  @media (--md) {
    --clip-progress: 0;
  }
}

.videoModal {
  /* add some padding around the modal to accomodate for video control buttons very close to the modal edge - try to prevent triggering modal close when touching them */
  --modal-touch-area-padding: 3rem;

  @media (--sm) {
    --modal-touch-area-padding: 0;
  }
}
