.speedbump {
  position: relative;
  width: 100%;
  padding: var(--spacer-64) 0 0 0;

  --container-start: start;
  --container-width: var(--grid-number-of-columns);
  --shadow-radius: 0;

  @media (--sm) {
    --container-start: col 2;
    --container-width: calc(var(--grid-number-of-columns) - 2);
  }

  @media (--md) {
    --container-start: start;
    --container-width: var(--grid-number-of-columns);
  }

  &.isCardIndented {
    @media (--sm) {
      --container-start: col 2;
      --container-width: 12;
    }

    @media (--md) {
      --container-start: col 2;
      --container-width: 12;
    }
  }

  &.isBackgroundFullBleed {
    padding: 0;

    @media (--sm) {
      --container-start: start;
      --container-width: var(--grid-number-of-columns);
    }
  }

  &.isBackgroundContained,
  &.isBackgroundFullBleed.isCardOnMobile {
    --shadow-radius: var(--border-radius-20);
  }

  &.isBackgroundFullBleed.isCardOnMobile {
    @media (--sm) {
      --shadow-radius: 0;
    }
  }

  &.isRevealedByGarageDoor {
    position: sticky;
  }
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-alpha-dark-70));
  content: '';
  inset: 0;
}

.spacerWrapper {
  composes: grid from '~g';
  position: relative;
  width: 100%;
  height: auto;

  .isBackgroundFullBleed & {
    min-height: max(41.8vw, 97vh);

    @media (--sm) {
      min-height: clamp(41.8vw, 50vh, 100vw);
    }

    @media (--md) {
      min-height: clamp(41.8vw, 80vh, 56.25vw);
    }
  }

  .isBackgroundFullBleed.isRevealedByGarageDoor.verticalCenter,
  .isBackgroundFullBleed.isRevealedByGarageDoor.verticalBottom & {
    height: 100vh;
  }

  .isDefaultContainedLayout.hasImage.isMinimumPhotoHeight & {
    min-height: calc(100vh - var(--nav-height-padded));

    @media (--sm) {
      min-height: 33rem;
    }

    @media (--md) {
      min-height: 44rem;
    }
  }

  .isAlternativeContainedLayout.hasImage.isMinimumPhotoHeight & {
    min-height: 52rem;

    @media (--sm) {
      min-height: 36rem;
    }

    @media (--md) {
      min-height: 50rem;
    }
  }

  .verticalCenter & {
    align-items: center;
  }

  .verticalBottom &,
  .logoTopTextBottom & {
    align-items: end;
  }

  .logoTopTextBottomSpread & {
    align-items: stretch;
  }

  .logoTopTextBottomSpreadMobile & {
    align-items: stretch;

    @media (--sm) {
      align-items: end;
    }
  }
}

.container {
  composes: subgrid from '~g';

  --subgrid-start: var(--container-start);
  --subgrid-width: var(--container-width);
  position: relative;
}

.glowWrapper {
  position: absolute;
  z-index: var(--z-index-1);
  grid-column: var(--wrapper-start) / var(--wrapper-end);
  inset: 0;

  @media (--sm) {
    grid-column: var(--container-start) / span var(--container-width);
  }
}

.innerWrapper {
  composes: subgrid from '~g';
  position: relative;
  z-index: var(--z-index-5);
  align-content: space-between;
  align-items: end;
  padding: var(--spacer-64) 0;

  --subgrid-start: start;
  --subgrid-width: 4;

  @media (--sm) {
    align-content: end;
    padding: var(--spacer-64) 0;

    --subgrid-start: start;
    --subgrid-width: var(--container-width);
  }

  .hasImage & {
    padding: var(--spacer-48) 0;

    @media (--sm) {
      padding: var(--spacer-48) 0;
    }

    @media (--md) {
      padding: var(--spacer-64) 0;
    }
  }

  .hasImage.isMinimumPhotoHeight & {
    @media (--md) {
      padding: var(--spacer-64) 0 var(--spacer-96);
    }
  }

  .isAlternativeContainedLayout & {
    padding: var(--spacer-64) 0 var(--spacer-240);

    @media (--xs) {
      padding: var(--spacer-64) 0;
    }

    @media (--md) {
      padding: var(--spacer-64) 0;
    }
  }

  .isAlternativeContainedLayout.logoTopTextBottomSpread & {
    padding: var(--spacer-40) 0;

    @media (--sm) {
      padding: var(--spacer-40) 0;
    }

    @media (--md) {
      padding: var(--spacer-64) 0;
    }
  }

  .noImage & {
    align-content: center;
    padding: var(--spacer-64) 0;

    @media (--sm) {
      padding: var(--spacer-64) 0;
    }

    @media (--md) {
      padding: var(--spacer-64) 0;
    }
  }

  .isQuoted & {
    padding-left: 1.2em;

    @media (--sm) {
      padding-left: 0;
    }
  }

  .logoTopTextBottomSpreadMobile & {
    align-content: space-between;
    padding: calc(var(--nav-height) + var(--spacer-12)) 0 var(--spacer-64);

    @media (--md) {
      align-content: end;
    }
  }

  .isBackgroundFullBleed.verticalCenter & {
    align-content: center;

    @media (--md) {
      align-content: center;
    }
  }

  .isBackgroundFullBleed.verticalBottom & {
    padding: var(--spacer-96) 0 var(--spacer-64);

    @media (--md) {
      padding: var(--spacer-64) 0 var(--spacer-96);
    }
  }

  .isBackgroundFullBleed.verticalBottom.logoTopTextBottom & {
    align-content: space-between;

    @media (--md) {
      align-content: end;
    }
  }

  .isBackgroundContained.logoTopTextBottomSpread &,
  .isBackgroundFullBleed.verticalBottom.logoTopTextBottomSpread & {
    align-content: space-between;

    @media (--md) {
      align-content: space-between;
    }
  }

  .isBackgroundFullBleed.verticalBottom.logoTopTextBottomSpreadMobile & {
    align-content: space-between;
    padding: calc(var(--nav-height) + var(--spacer-12)) 0 var(--spacer-64);

    @media (--md) {
      align-content: end;
    }
  }
}

.content {
  position: relative;
  padding-right: 4vw;
  grid-column: var(--content-start) / span var(--content-width);

  --content-start: start;
  --content-width: 4;

  @media (--sm) {
    --content-start: 2;
    --content-width: 9;
    padding-right: 0;
  }

  @media (--md) {
    --content-start: 4;
    --content-width: 8;
  }

  .isAlternativeContainedLayout & {
    @media (--sm) {
      --content-start: 2;
      --content-width: 8;
    }

    @media (--md) {
      --content-start: 2;
      --content-width: 7;
    }

    > * {
      padding: 0;
      margin-bottom: var(--spacer-32);

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .isAlternativeContainedLayout.ragTight & {
    --content-width: 3;

    @media (--xs) {
      --content-width: 4;
    }

    @media (--sm) {
      --content-width: 6;
    }

    @media (--md) {
      --content-width: 6;
    }
  }

  .isAlternativeContainedLayout.ragWide & {
    @media (--sm) {
      --content-width: 9;
    }

    @media (--md) {
      --content-width: 9;
    }
  }

  .isDefaultContainedLayout.noLogo & {
    @media (--sm) {
      --content-start: 2;
      --content-width: 8;
    }

    @media (--md) {
      --content-start: 2;
      --content-width: 7;
    }

    > * {
      padding: 0;
      margin-bottom: var(--spacer-32);

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .noImage & {
    @media (--sm) {
      --content-width: 8;
    }

    @media (--md) {
      --content-width: 8;
    }
  }

  .isDefaultContainedLayout.isCardIndented & {
    @media (--sm) {
      --content-start: 2;
      --content-width: 9;
    }

    @media (--md) {
      --content-start: 3;
      --content-width: 8;
    }
  }

  .isDefaultContainedLayout.isContentIndented.hasImage &,
  .isDefaultContainedLayout.isContentIndented.noImage & {
    @media (--sm) {
      --content-start: col 2;
      --content-width: 12;
    }

    @media (--md) {
      --content-start: col 2;
      --content-width: 12;
    }
  }

  .isBackgroundFullBleed & {
    @media (--sm) {
      --content-start: start;
      --content-width: 9;
    }

    @media (--md) {
      --content-start: start;
      --content-width: 9;
    }
  }

  .isBackgroundFullBleed.ragTight & {
    padding-right: 14vw;

    @media (--sm) {
      --content-start: start;
      --content-width: 7;
      padding-right: unset;
    }

    @media (--md) {
      --content-start: start;
      --content-width: 7;
    }
  }

  .isBackgroundFullBleed.ragWide & {
    @media (--sm) {
      --content-start: start;
      --content-width: 11;
    }

    @media (--md) {
      --content-start: start;
      --content-width: 11;
    }
  }

  .isBackgroundFullBleed.logoTopTextBottomSpread & {
    @media (--sm) {
      --content-start: start;
      --content-width: 10;
    }

    @media (--md) {
      --content-start: start;
      --content-width: 10;
    }
  }

  .isBackgroundFullBleed.logoTopTextBottomSpread.ragTight & {
    @media (--sm) {
      --content-start: start;
      --content-width: 8;
    }

    @media (--md) {
      --content-start: start;
      --content-width: 8;
    }
  }

  .isBackgroundFullBleed.logoTopTextBottomSpread.ragWide & {
    @media (--sm) {
      --content-start: start;
      --content-width: 13;
    }

    @media (--md) {
      --content-start: start;
      --content-width: 14;
    }
  }

  .isDefaultContainedLayout.logoLeftTextRight.isContentIndented & {
    @media (--sm) {
      --content-start: 2;
      --content-width: 10;
    }

    @media (--md) {
      --content-start: 3;
      --content-width: 9;

      /* design wants text to be 8 cols + 1 gutter */
      padding-right: var(--grid-computed-column-width);
    }
  }

  .isDefaultContainedLayout.logoTopTextBottom & {
    @media (--sm) {
      --content-start: 2;
      --content-width: 9;
    }

    @media (--md) {
      --content-start: 2;
      --content-width: 10;
    }
  }

  .isDefaultContainedLayout.logoTopTextBottom.noLogo & {
    @media (--sm) {
      --content-start: 2;
      --content-width: 8;
    }

    @media (--md) {
      --content-start: 2;
      --content-width: 6;
    }
  }

  .isDefaultContainedLayout.logoTopTextBottom.hasImage & {
    @media (--sm) {
      --content-start: 2;
      --content-width: 8;
    }

    @media (--md) {
      --content-start: 3;
      --content-width: 7;
    }
  }

  .isDefaultContainedLayout.logoTopTextBottom.hasImage.ragTight & {
    @media (--sm) {
      --content-start: 2;
      --content-width: 8;
    }

    @media (--md) {
      --content-start: 3;
      --content-width: 6;
    }
  }

  .isDefaultContainedLayout.logoTopTextBottom.hasImage.ragWide & {
    @media (--sm) {
      --content-start: 2;
      --content-width: 8;
    }

    @media (--md) {
      --content-start: 3;
      --content-width: 8;
    }
  }

  .isDefaultContainedLayout.logoTopTextBottomSpread &,
  .isDefaultContainedLayout.logoTopTextBottomSpreadMobile & {
    @media (--sm) {
      --content-start: 2;
    }

    @media (--md) {
      --content-start: 3;
    }
  }

  .isDefaultContainedLayout.logoTopTextBottomSpread.ragTight &,
  .isDefaultContainedLayout.logoTopTextBottomSpreadMobile.ragTight & {
    @media (--sm) {
      --content-width: 7;
    }

    @media (--md) {
      --content-width: 7;
    }
  }

  .isDefaultContainedLayout.logoTopTextBottomSpread.ragWide &,
  .isDefaultContainedLayout.logoTopTextBottomSpreadMobile.ragWide & {
    @media (--sm) {
      --content-width: 10;
    }

    @media (--md) {
      --content-width: 10;
    }
  }

  .isBackgroundContained.logoTopTextBottomSpread.isQuoted &,
  .isBackgroundContained.logoTopTextBottomSpreadMobile.isQuoted & {
    @media (--md) {
      left: 0;
      width: 100%;
    }

    > * {
      @media (--sm) {
        padding: 0;
      }
    }
  }

  .isQuoted & {
    > * {
      padding: 0;

      @media (--sm) {
        padding: 0 0 0 var(--spacer-20);
      }

      @media (--md) {
        padding: 0 0 0 var(--spacer-16);
      }
    }

    .quoteText {
      position: relative;
      display: block;

      &::before,
      &::after {
        color: var(--color-grey-100);
        content: '';
      }

      &::before {
        position: absolute;
        padding-right: 0.04em;
        margin-left: -0.44em;
      }

      &::after {
        padding-left: 0.04em;
        content: '';
      }
    }

    .quoteText + .attribution {
      color: var(--color-alpha-light-50);

      .em {
        color: var(--color-grey-100);
      }
    }
  }

  .isQuoted.hasImage & {
    @media (--sm) {
      left: -0.9em;
      width: calc(100% + 0.9em);
    }
  }

  /* need some specificity help */

  .accents {
    color: inherit;
  }
}

.logoWrapper {
  position: relative;
  align-self: baseline;
  margin: 0 0 var(--spacer-16);
  grid-column: start;

  @media (--sm) {
    margin: 0 0 var(--spacer-28);
    grid-column: col 2 / span 3;
  }

  @media (--md) {
    margin: 0;
    grid-column: start / span 2;
  }

  .logo {
    position: relative;
    height: 3.5rem;

    * {
      fill: var(--color-alpha-light-50);
    }
  }

  .hasImage & {
    .logo {
      * {
        fill: var(--color-grey-97);
      }
    }
  }

  .logoLeftTextRight & {
    top: 0.2em;

    @media (--sm) {
      display: flex;
      justify-content: flex-end;
      padding: 0 var(--spacer-20) 0 1.3em;
      grid-column: col 2 / span 2;
    }

    @media (--md) {
      padding: 0 var(--spacer-20) 0 0;
    }
  }

  .logoLeftTextRight.isCardIndented & {
    @media (--sm) {
      display: unset;
      margin: 0 0 var(--spacer-12);
      grid-column: col 2 / span 3;
    }

    @media (--md) {
      display: flex;
      padding: 0 var(--spacer-20) 0 1.3em;
      margin: 0;
      grid-column: start / span 2;
    }

    .logo {
      @media (--sm) {
        height: 3rem;
      }
    }
  }

  .logoLeftTextRight.isContentIndented & {
    @media (--md) {
      /* design wants logo to sit on a gutter */
      padding-left: var(--grid-computed-column-width);
      grid-column: col 1 / span 2;
    }
  }

  .logoTopTextBottom & {
    margin-bottom: var(--spacer-32);

    @media (--sm) {
      grid-column: col 2;
    }

    @media (--md) {
      grid-column: col 3;
    }

    .logo {
      * {
        fill: var(--color-grey-97);
      }
    }
  }

  .logoTopTextBottom.isCardIndented & {
    @media (--sm) {
      grid-column: col 2;
    }

    @media (--md) {
      grid-column: col 2;
    }
  }

  .isBackgroundFullBleed & {
    display: unset;
    padding: 0;
    margin-bottom: var(--spacer-16);
    grid-column: start / end;

    * {
      fill: var(--color-grey-100);
    }
  }

  .isBackgroundFullBleed.logoTopTextBottomSpreadMobile & {
    @media (--sm) {
      margin: 0 0 var(--spacer-40);
    }
  }

  .isBackgroundContained.logoTopTextBottomSpreadMobile & {
    grid-column: start;

    @media (--sm) {
      margin: 0 0 var(--spacer-32);
      grid-column: col 2;
    }

    @media (--md) {
      grid-column: col 3;
    }
  }

  .isAlternativeContainedLayout.logoTopTextBottomSpread & {
    --content-start: start;
    grid-column: var(--content-start) / end;

    @media (--sm) {
      --content-start: 2;
    }

    @media (--md) {
      --content-start: 2;
    }
  }
}

.quoteText {
  margin-bottom: var(--spacer-32);
  grid-column: start / span var(--content-width);

  .isAlternativeContainedLayout & {
    margin-bottom: var(--spacer-24);

    @media (--xs) {
      margin-bottom: var(--spacer-20);
    }
  }

  .isAlternativeContainedLayout.isMinimumPhotoHeight & {
    margin-top: var(--spacer-12);
    margin-bottom: var(--spacer-4);
  }

  .logoTopTextBottom & {
    @media (--sm) {
      grid-column: start / span var(--content-width);
    }
  }

  .hasImage & {
    @media (--sm) {
      grid-column: start / span var(--content-width);
    }
  }
}

.attribution {
  color: var(--color-grey-50);
  grid-column: start / span var(--content-width);

  .isBackgroundFullBleed & {
    color: var(--color-grey-100);
  }
}

.em {
  display: block;
  padding: 0 0 var(--spacer-6);
  color: var(--color-grey-90);
  font-weight: normal;

  @media (--xs) {
    display: inline-block;
  }
}

.buttons {
  margin-top: 0;
  grid-column: start / span var(--content-width);
}

.title4 {
  composes: title5md4 from '~typo';
}

.title3md2 {
  composes: title3md2 from '~typo';
}

.cropBorders {
  overflow: hidden;
  border-radius: var(--shadow-radius);

  .isBackgroundContained &,
  .isBackgroundFullBleed.isCardOnMobile & {
    &::after {
      position: absolute;
      z-index: var(--z-index-3);
      overflow: hidden;
      border-radius: var(--shadow-radius);
      box-shadow: inset 0 0 0 1px var(--color-alpha-light-10);
      content: '';
      inset: 0;
    }
  }

  .isBackgroundFullBleed.isCardOnMobile & {
    &::after {
      @media (--sm) {
        display: none;
      }
    }
  }
}

.speedbump .backgroundWrapper {
  z-index: var(--z-index-3);
}

.speedbump .backgroundWrapper,
.speedbump .backgroundWrapperFullBleed {
  position: absolute;
  inset: 0;
}

.speedbump .backgroundImageContained {
  @media (--sm) {
    grid-column: var(--container-start) / span var(--container-width);
  }
}

.speedbump.hasImage .backgroundWrapper {
  position: absolute;
  z-index: var(--z-index-3);
  width: 100%;
  height: 100%;
  inset: 0;

  &::before {
    position: absolute;
    z-index: var(--z-index-3);
    overflow: hidden;
    background: var(--speedbump-gradient-medium);
    border-radius: var(--shadow-radius);
    content: '';
    inset: 0;
  }

  &.hasNoGradientMobile {
    &::before {
      background: none;
    }
  }

  &.hasNoGradient {
    &::before {
      @media (--sm) {
        background: none;
      }
    }
  }

  &.hasGradientLightMobile {
    &::before {
      background: var(--speedbump-gradient-light);
    }
  }

  &.hasGradientLight {
    &::before {
      @media (--sm) {
        background: var(--speedbump-gradient-light);
      }
    }
  }

  &.hasGradientMediumMobile {
    &::before {
      background: var(--speedbump-gradient-medium);
    }
  }

  &.hasGradientMedium {
    &::before {
      @media (--sm) {
        background: var(--speedbump-gradient-medium);
      }
    }
  }

  &.hasGradientHeavyMobile {
    &::before {
      background: var(--speedbump-gradient-heavy);
    }
  }

  &.hasGradientHeavy {
    &::before {
      @media (--sm) {
        background: var(--speedbump-gradient-heavy);
      }
    }
  }

  &.hasGradientSuperHeavyMobile {
    &::before {
      background: var(--speedbump-gradient-super-heavy);
    }
  }

  &.hasGradientSuperHeavy {
    &::before {
      @media (--sm) {
        background: var(--speedbump-gradient-super-heavy);
      }
    }
  }

  /* full bleed mode uses different gradients */

  &.backgroundImageFullBleed.hasGradientLightMobile {
    &::before {
      background: var(--speedbump-gradient-light-full-bleed);
    }
  }

  &.backgroundImageFullBleed.hasGradientLight {
    &::before {
      @media (--sm) {
        background: var(--speedbump-gradient-light-full-bleed);
      }
    }
  }

  &.backgroundImageFullBleed.hasGradientMediumMobile {
    &::before {
      background: var(--speedbump-gradient-medium-full-bleed);
    }
  }

  &.backgroundImageFullBleed.hasGradientMedium {
    &::before {
      @media (--sm) {
        background: var(--speedbump-gradient-medium-full-bleed);
      }
    }
  }

  &.backgroundImageFullBleed.hasGradientHeavyMobile {
    &::before {
      background: var(--speedbump-gradient-heavy-full-bleed);
    }
  }

  &.backgroundImageFullBleed.hasGradientHeavy {
    &::before {
      @media (--sm) {
        background: var(--speedbump-gradient-heavy-full-bleed);
      }
    }
  }

  &.backgroundImageFullBleed.hasGradientSuperHeavyMobile {
    &::before {
      background: var(--speedbump-gradient-super-heavy-full-bleed);
    }
  }

  &.backgroundImageFullBleed.hasGradientSuperHeavy {
    &::before {
      @media (--sm) {
        background: var(--speedbump-gradient-super-heavy-full-bleed);
      }
    }
  }

  /* alt layout uses different gradients */

  &.isAlternativeContainedLayout.hasGradientLightMobile {
    &::before {
      background: var(--speedbump-gradient-light-alt);
    }
  }

  &.isAlternativeContainedLayout.hasGradientLight {
    &::before {
      @media (--sm) {
        background: var(--speedbump-gradient-light-alt);
      }
    }
  }

  &.isAlternativeContainedLayout.hasGradientMediumMobile {
    &::before {
      background: var(--speedbump-gradient-medium-alt);
    }
  }

  &.isAlternativeContainedLayout.hasGradientMedium {
    &::before {
      @media (--sm) {
        background: var(--speedbump-gradient-medium-alt);
      }
    }
  }

  &.isAlternativeContainedLayout.hasGradientHeavyMobile {
    &::before {
      background: var(--speedbump-gradient-heavy-alt);
    }
  }

  &.isAlternativeContainedLayout.hasGradientHeavy {
    &::before {
      @media (--sm) {
        background: var(--speedbump-gradient-heavy-alt);
      }
    }
  }

  &.isAlternativeContainedLayout.hasGradientSuperHeavyMobile {
    &::before {
      background: var(--speedbump-gradient-super-heavy-alt);
    }
  }

  &.isAlternativeContainedLayout.hasGradientSuperHeavy {
    &::before {
      @media (--sm) {
        background: var(--speedbump-gradient-super-heavy-alt);
      }
    }
  }
}

.blur {
  position: relative;
  z-index: var(--z-index-1);
  width: 100%;
  height: 100%;

  /* backdrop-filter: blur(10px); */
  background: var(--color-alpha-dark-70);
}

.dropShadowWrapper {
  position: absolute;
  z-index: var(--z-index-2);
  inset: 0;
}

.dropShadow {
  position: absolute;
  inset: 0;
}
