.card {
  position: absolute;
  width: var(--card-width);
  transition: transform var(--duration-100ms) var(--ease-out);

  --card-width: min(38vw, 18rem);
  --distance: -3vh;
  --shadow-radius: var(--border-radius-2);

  @media (--sm) {
    transform: translate3d(0, calc(var(--cloud-cards-progress) * var(--distance) * var(--card-speed-multiplier)), 0);

    --shadow-radius: var(--border-radius-5);
  }
  @media (--md) {
    --shadow-radius: var(--border-radius-8);
  }

  /* Top cards (for mobile) */
  &:nth-child(1) {
    top: 5%;
    left: 28%;

    --card-speed-multiplier: 2;
  }

  &:nth-child(2) {
    top: 9%;
    left: 85%;

    --card-speed-multiplier: 0.5;
  }

  &:nth-child(3) {
    top: 74%;
    left: -28%;

    --card-speed-multiplier: 2.5;
  }

  /* Bottom cards (for mobile) */
  &:nth-child(4) {
    top: 54%;
    left: -27%;

    --card-speed-multiplier: 1.5;
  }

  &:nth-child(5) {
    top: 67%;
    left: 20%;

    --card-speed-multiplier: 1.1;
  }

  &:nth-child(6) {
    top: 55%;
    left: 81%;

    --card-speed-multiplier: 3.5;
  }

  &:nth-child(7) {
    top: 51%;
    left: 25%;

    --card-speed-multiplier: 1.2;
  }

  &:nth-child(8) {
    top: 74%;
    left: 73%;

    --card-speed-multiplier: 1.8;
  }

  &:nth-child(9) {
    top: 84%;
    left: 15%;

    --card-speed-multiplier: 1.8;
  }

  @media (--xs) {
    &:nth-child(2) {
      top: 9%;
      left: 105%;
    }

    &:nth-child(3) {
      top: 1%;
      left: -44%;
    }

    &:nth-child(4) {
      top: 50%;
      left: -47%;
    }

    &:nth-child(5) {
      top: 77%;
      left: -32%;
    }

    &:nth-child(6) {
      top: 45%;
      left: 94%;
    }

    &:nth-child(7) {
      top: 56%;
    }

    &:nth-child(8) {
      top: 65%;
      left: 110%;
    }

    &:nth-child(9) {
      top: 83%;
      left: 46%;
    }
  }

  @media (--sm) {
    --card-width: min(38vw, 52rem);
    --distance: min(20rem, -10vh);

    &:nth-child(1) {
      top: 3%;
      left: 52%;
    }

    &:nth-child(2) {
      top: 36%;
      left: 25%;

      --card-speed-multiplier: 1.5;
    }

    &:nth-child(3) {
      top: 41%;
      left: -23%;
    }

    &:nth-child(4) {
      top: 70%;
      left: -31%;
    }

    &:nth-child(5) {
      top: 61%;
      left: 70%;

      --card-speed-multiplier: 2;
    }

    &:nth-child(6) {
      display: none;
    }

    &:nth-child(7) {
      display: none;
    }

    &:nth-child(8) {
      top: 32%;
      left: 76%;
    }

    &:nth-child(9) {
      top: 72%;
      left: 20%;
    }
  }
  @media (--md) {
    --card-width: min(38vw, 52rem);
    width: var(--card-width);

    &:nth-child(1) {
      top: 1%;
      left: var(--card-width);

      --card-speed-multiplier: 2;
    }

    &:nth-child(2) {
      top: 18%;
      left: calc(var(--card-width) * 2 + 14rem);

      --card-speed-multiplier: 0.5;
    }

    &:nth-child(3) {
      top: 28%;
      left: calc(var(--card-width) * 0.8 - 4rem);

      --card-speed-multiplier: 2.2;
    }

    &:nth-child(4) {
      top: 36%;
      left: calc(var(--card-width) * -0.5);

      --card-speed-multiplier: 1.5;
    }

    &:nth-child(5) {
      top: 45%;
      left: 78%;

      --card-speed-multiplier: 1.1;
    }

    &:nth-child(6) {
      top: 54%;
      left: calc(var(--card-width) * 0.7);
      display: block;

      --card-speed-multiplier: 2.5;
    }

    &:nth-child(7) {
      top: 63%;
      left: calc(var(--card-width) * -0.6);
      display: block;

      --card-speed-multiplier: 1.2;
    }

    &:nth-child(8) {
      top: 72%;
      left: 72%;
      display: block;

      --card-speed-multiplier: 1.8;
    }

    &:nth-child(9) {
      top: 78%;
      left: 23%;
      display: block;

      --card-speed-multiplier: 1.1;
    }
  }

  .imageContainer {
    /* increase canvas size to prevent cropping because these are too small */
    --mobile-canvas-scale: 2;
  }

  .image {
    display: block;
    overflow: hidden;
    width: 100%;
    background-color: var(--color-alpha-dark-20);
    border-radius: var(--shadow-radius);
  }
}
