/* That will supposedly always be the column the lowest */
$bottomPartOfMiddleColumnY: 55rem;
$aspectRatio: 4 / 3;
@value aspectRatio: $aspectRatio;

.heroImageWall {
  --vertical-factor: 0.6;
  --content-wrapper-height: min((100vh - var(--nav-height)), 100rem);
  --hero-image-gutter: var(--spacer-24);
  --hero-image-width: 15rem;
  --hero-image-aspect-ratio: calc(4 / 3);
  --hero-image-height: calc(var(--hero-image-width) / var(--hero-image-aspect-ratio));
  --image-wall-progress: 0;
  --entry-animation-progress: 0;

  /* middle bottom column y + number of images */
  /* stylelint-disable-next-line max-line-length */
  --max-bottom-element: calc(($bottomPartOfMiddleColumnY * var(--vertical-factor)) + (var(--hero-image-height) + var(--hero-image-gutter)) * 1);
  position: relative;
  height: calc(var(--max-bottom-element) * var(--height-ratio, 0.67) + (var(--content-wrapper-height) + var(--hero-image-height)) / 2);
  padding-top: var(--nav-height);

  --height-ratio: 0.73;

  @media (min-aspect-ratio: 6 / 10) {
    --height-ratio: 0.9;
  }

  @media (min-aspect-ratio: 15 / 10) {
    --height-ratio: 0.8;
  }

  @media (min-aspect-ratio: 17 / 10) {
    --height-ratio: 0.7;
  }

  @media (min-aspect-ratio: 17 / 10) and (min-width: 2100px) {
    --height-ratio: 0.8;
  }

  @media (min-aspect-ratio: 20 / 10) and (min-width: 2100px) {
    --height-ratio: 0.68;
  }

  @media (--sm) {
    --vertical-factor: 0.85;
    --hero-image-width: 24rem;
  }

  @media (--md) {
    --content-wrapper-height: min(100vh, 100rem);
    --vertical-factor: 1;
    --hero-image-width: 32rem;
  }
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.contentWrapper {
  composes: grid from '~g';
  position: relative;
  height: var(--content-wrapper-height);
  align-items: center;
}

.content {
  composes: grid from '~g';
  grid-column: wrapper-start / wrapper-end;
  opacity: 0;
}

.title,
.subtitle {
  /* Yeah i don't understand the autoThings that are happening in PortableText and that's the only way I have to fight that specificity */
  /* stylelint-disable-next-line declaration-no-important */
  grid-column: start / end !important;
  text-align: center;
  @media (--sm) {
    /* stylelint-disable-next-line declaration-no-important */
    grid-column: col 4 / col 12 !important;
  }
}

.imagesWrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;

  --first-column-x: -52rem;

  @media (--sm) {
    --first-column-x: -79.3rem;
  }

  @media (--md) {
    --first-column-x: -103.3rem;
  }
}

.imageWrapper {
  position: absolute;
  width: var(--hero-image-width);
  opacity: var(--entry-animation-opacity, 0);

  --normalized-entry-animation-progress: calc((var(--entry-animation-progress) / 2 + 0.5));

  /* increase canvas size to prevent cropping because these are too small */
  --mobile-canvas-scale: 2;

  /* @media (--md) {
    --normalized-entry-animation-progress: var(--entry-animation-progress);
  } */

  --normalized-image-wall-progress: calc(var(--image-wall-progress) * 0.15);
  @media (--md) {
    --normalized-image-wall-progress: var(--image-wall-progress) * 0.4;
  }
  transform: translateY(calc(((100vh + var(--image-wall-increment, 0) * 200%) * (1 - var(--normalized-entry-animation-progress, 0))) - (var(--normalized-image-wall-progress) * 100rem * var(--image-wall-column-ratio))));

  --base-top: calc(50% - var(--hero-image-height) / 2);
  --base-left: calc(50% - var(--hero-image-width) / 2);

  /* We display: none the images that are not visible on xs */
  &:nth-child(-n + 5),
  &:nth-last-child(-n + 8) {
    display: none;
    @media (--sm) {
      display: unset;
    }
  }

  /* We display: none the images that are not visible on sm */
  &:nth-child(-n + 2),
  &:nth-last-child(-n + 3) {
    display: none;
    @media (--md) {
      display: unset;
    }
  }
}

.image {
  overflow: hidden;
  width: 100%;
  border-radius: var(--card-border-radius);

  --image-aspect-ratio: $aspectRatio;
}

$imgHeightAndGutter: var(--hero-image-height) + var(--hero-image-gutter);

$firstColX: var(--first-column-x);

$colX: calc($firstColX + (var(--hero-image-width) + var(--hero-image-gutter)) * 0);
$colY: calc(-30rem * var(--vertical-factor));
$colRatio: 0;

.imageWrapper:nth-child(1) {
  --image-wall-increment: 0;
  --image-wall-column-ratio: $colRatio;
  top: calc(var(--base-top) + calc($colY + ($imgHeightAndGutter) * 0));
  left: calc(var(--base-left) + $colX);
}

.imageWrapper:nth-child(2) {
  --image-wall-increment: 1;
  --image-wall-column-ratio: $colRatio;
  top: calc(var(--base-top) + calc($colY + ($imgHeightAndGutter) * 1));
  left: calc(var(--base-left) + $colX);
}

