.appleTv {
  position: relative;

  /* Disable hidden from the garage door because of position sticky */
  /* stylelint-disable-next-line declaration-no-important */
  overflow: unset !important;
  background-color: var(--color-alpha-black-100);
  opacity: 1;

  --content-start: start;
  --content-width: 4;

  @media (--md) {
    margin-top: calc(var(--spacer-64) * -1);
  }
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.container {
  composes: grid from '~g';
  padding-top: var(--spacer-40);
  row-gap: var(--gap);

  --gap: var(--spacer-40);

  @media (--sm) {
    --gap: calc(2 * var(--spacer-124));
    padding: var(--gap) 0 calc(var(--gap) * 1.5);
    margin-top: -100vh; /* to compensate background sticky 100vh */
  }

  @media (--md) {
    /* calculated based on current TV screen position in relation to screen sizes */
    --gap: max(13vw, 36vh);
  }

  .section {
    align-items: center;
    align-self: center;
    grid-column: start / end;

    @media (--sm) {
      grid-column: 3 / span 5;
    }
    @media (--md) {
      grid-column: start / span 4;
    }

    .touts {
      --tout-width: 14;
      row-gap: var(--spacer-48);

      @media (--lg) {
        row-gap: var(--spacer-40);
      }

      svg path {
        /* make sure each icons has the same color */
        fill: var(--color-accent);
      }
    }
  }
}

.background,
.foreground {
  position: relative;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 38rem;
  background: transparent;
  pointer-events: none;

  @media (--sm) {
    position: sticky;
    z-index: var(--z-index-2);
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    margin-left: 0;
    border-radius: var(--bottom-rounded-module-border-radius);
  }
  @media (--lg) {
    right: 0;
    left: auto;
    padding-left: min(8vw, 70rem);

    /* overflow: visible; */
    margin: 0 auto;
  }
  @media (--xl) {
    padding-left: min(20vw, 60rem);
  }

  .backgroundWrapper,
  .foregroundWrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    width: calc(100% * var(--scale));
    height: calc(100% * var(--scale));
    transform: translate(-50%, -50%) scale(2);

    --scale: 1;

    /* Made the positioning of the background & video relative to center so
    that both placement are synced - and only need to scale/translate parent */
    @media (--sm) {
      --scale: 1.7;

      /* Have to put on both, because Background also have TV, so it doesn't work */
      transform: translate3d(calc(40% / var(--scale) - 50%), calc(-10% / var(--scale) - 50% + var(--appletv-enter-progress) * 10rem), 0);
    }

    @media (--md) {
      /* fixed width for bigger screen size as per design request */
      width: calc(144rem * var(--scale));
      transform: translate3d(calc(20% / var(--scale) - 50%), calc(-10% / var(--scale) - 50% + var(--appletv-enter-progress) * 15rem), 0);

      --scale: 1.4; /* scale by 40% so that TV screen takes approx. 60% of the screen - calc based on comp */
    }

    @media (--lg) {
      transform: translate(calc(25% / var(--scale) - 50%), calc(-10% / var(--scale) - 50% + var(--appletv-enter-progress) * 20rem));
    }
  }
}

.background {
  z-index: var(--z-index-neg);

  .backgroundGlow {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 42%; /* video size in relation to image size */
    aspect-ratio: 16/9;
    transform: translate(-50%, -50%);

    @media (--md) {
      transform: translate(-50%, -32%);
    }
  }
}

.foreground {
  position: absolute;

  /* margin-top: -100vh; */
  @media (--sm) {
    position: sticky;
    margin-top: -100vh;
  }

  .foregroundImage {
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    width: calc((1 / 0.42) * 100%); /* scale it up */
    height: auto;
    margin-top: -0.2rem; /* make room for TV border */
    transform: translate(-50%, -50%);
  }

  .videoWrapper {
    position: absolute;
    z-index: var(--z-index-2);
    top: 50%;
    left: 50%;
    width: 42%; /* video size in relation to image size */
    transform: translate(-50%, -50%);
  }

  .videoGlow {
    z-index: var(--z-index-neg);
  }

  .video {
    position: relative;
    z-index: var(--z-index-1); /* Make it go in front TV */
  }
}
