.chicletConstellation {
  composes: grid from '~g';
  position: relative;
  z-index: var(--z-index-neg);
  margin-top: min(-20vh, 10rem);
}

.dummy {
  position: absolute;
  top: 0;
  right: 0;
  bottom: -20vh;
  left: 0;
  pointer-events: none;
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.logos {
  position: relative;
  z-index: var(--z-index-neg);
  display: grid;
  padding-bottom: var(--spacer-32);
  margin-right: var(--spacer-12);
  margin-bottom: var(--spacer-40);
  margin-left: var(--spacer-12);
  grid-auto-rows: 1fr;
  grid-column: wrapper-start / wrapper-end;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(17, 1fr);

  &.hasContent {
    padding-bottom: var(--spacer-16);
    margin-bottom: var(--spacer-40);
  }

  @media (--sm) {
    padding-bottom: var(--spacer-64);
    margin-right: var(--spacer-32);
    margin-left: var(--spacer-32);
    grid-template-columns: repeat(24, 1fr);
    grid-template-rows: repeat(9, 1fr);

    &.hasContent {
      padding-bottom: var(--spacer-124);
      margin-bottom: var(--spacer-96);
    }
  }

  @media (--lg) {
    margin-bottom: var(--spacer-16);
    grid-column: wrapper-start / wrapper-end;
  }

  .logoItem {
    --logo-translate-x: 0;
    --logo-translate-y: 0;
    --logo-parallax-distance: 10rem;
    --logo-parallax-distance-multiplier: 1;
    opacity: calc(1.2 - (0 * (0.7 + var(--logo-parallax-distance-multiplier) * 0.3)));
    transform: translate3d(var(--logo-translate-x), calc(var(--logo-translate-y) * 1px + 0.5 * (var(--logo-parallax-distance) * var(--logo-parallax-distance-multiplier))), 0);

    @media (--sm) {
      opacity: calc(1.2 - (var(--constellation-progress) * (0.7 + var(--logo-parallax-distance-multiplier) * 0.3)));
      transform: translate3d(var(--logo-translate-x), calc(var(--logo-translate-y) * 1px + var(--constellation-progress) * (var(--logo-parallax-distance) * var(--logo-parallax-distance-multiplier))), 0);

      --logo-parallax-distance: 20rem;
    }

    &:nth-child(1n + 2) {
      --logo-parallax-distance-multiplier: 1.1;
    }

    &:nth-child(2n + 1) {
      --logo-parallax-distance-multiplier: 1.05;
    }

    &:nth-child(4n + 1) {
      --logo-parallax-distance-multiplier: 1.2;
    }

    &:nth-child(5n + 1) {
      --logo-parallax-distance-multiplier: 1.4;
    }

    &:nth-child(7n + 1) {
      --logo-parallax-distance-multiplier: 1.5;
    }

    &:nth-child(8n + 1) {
      --logo-parallax-distance-multiplier: 1.7;
    }

    &:nth-child(9n + 1) {
      --logo-parallax-distance-multiplier: 1.4;
    }

    &:nth-child(10n + 1) {
      --logo-parallax-distance-multiplier: 1.3;
    }

    &:nth-child(11n + 1) {
      --logo-parallax-distance-multiplier: 1.4;
    }

    &:nth-child(14n + 1) {
      --logo-parallax-distance-multiplier: 1.6;
    }

    &:nth-child(15n + 1) {
      --logo-parallax-distance-multiplier: 1.5;
    }
  }

  /* ################### CLOUD CONFIG ################## */

  &.cloud {
    .logoItem {
      /* Positioning */
      &:nth-child(1) {
        grid-area: 1 / 1 / 3 / 3;
      }

      &:nth-child(2) {
        grid-area: 2 / 4 / 4 / 6;
      }

      &:nth-child(3) {
        grid-area: 1 / 7 / 3 / 9;
      }

      &:nth-child(4) {
        grid-area: 2 / 11 / 4 / 13;
      }

      &:nth-child(5) {
        grid-area: 5 / 2 / 7 / 4;
      }

      &:nth-child(6) {
        grid-area: 4 / 7 / 7 / 9;
      }

      &:nth-child(7) {
        grid-area: 7 / 10 / 9 / 12;
      }

      &:nth-child(8) {
        grid-area: 8 / 1 / 10 / 3;
      }

      &:nth-child(9) {
        grid-area: 9 / 4 / 11 / 6;
      }

      &:nth-child(10) {
        grid-area: 7 / 6 / 9 / 8;
      }

      &:nth-child(11) {
        grid-area: 10 / 10 / 12 / 12;
      }

      &:nth-child(12) {
        grid-area: 13 / 2 / 15 / 4;
      }

      &:nth-child(13) {
        grid-area: 12 / 7 / 14 / 9;
      }

      &:nth-child(14) {
        grid-area: 14 / 10 / 16 / 12;
      }

      &:nth-child(15) {
        grid-area: 16 / 3 / 18 / 5;
      }

      &:nth-child(16) {
        grid-area: 15 / 6 / 17 / 8;
      }

      &:nth-child(17) {
        grid-area: 17 / 9 / 19 / 11;
      }

      &:nth-child(18) {
        grid-area: 19 / 1 / 21 / 3;
      }

      &:nth-child(19) {
        grid-area: 20 / 4 / 22 / 6;
      }

      &:nth-child(20) {
        grid-area: 20 / 7 / 22 / 9;
      }

      &:nth-child(21) {
        grid-area: 20 / 11 / 22 / 13;
      }

      &:nth-child(22) {
        grid-area: 23 / 2 / 25 / 4;
      }

      &:nth-child(23) {
        grid-area: 23 / 5 / 25 / 7;
      }
    }

    @media (--sm) {
      .logoItem {
        /* TODO: might consider converting those into nth-child formula format if we want a lot more */
