        }

        &:nth-child(16) {
          grid-area: 6 / 19 / 8 / 21;
        }

        &:nth-child(17) {
          grid-area: 8 / 22 / 10 / 24;
        }

        &:nth-child(18) {
          grid-area: 12 / 1 / 14 / 3;
        }

        &:nth-child(19) {
          grid-area: 13 / 4 / 15 / 6;
        }

        &:nth-child(20) {
          grid-area: 12 / 8 / 14 / 10;
        }

        &:nth-child(21) {
          grid-area: 12 / 14 / 14 / 16;
        }

        &:nth-child(22) {
          grid-area: 13 / 18 / 15 / 20;
        }

        &:nth-child(23) {
          grid-area: 12 / 23 / 14 / 25;
        }
      }
    }
  }
}

