.text {
  composes: subgrid from '~g';

  --subgrid-start: start;
  --subgrid-width: var(--grid-number-of-columns);
}

.eyebrow,
.titles,
.bodies {
  text-align: center;
}

.titles {
  @media (--sm) {
    grid-column: col 2 / span 12;
  }
}

.bodies {
  @media (--sm) {
    grid-column: col 3 / span 10;
  }
}

.forms {
  margin-right: auto;
  margin-left: auto;

  @media (--md) {
    max-width: 100%;
    grid-column: col 4 / span 8;
  }

  @media (--lg) {
    max-width: 100%;
    grid-column: col 5 / span 6;
  }
}

.contentWrapper {
  grid-column: start/end;

  /* transform: translate3d(0, calc(var(--constellation-progress) * -20rem), 0); */
}

.featuredLogo {
  justify-content: start;

  @media (--sm) {
    justify-content: center;
  }
}

.content {
  padding-top: var(--spacer-160);
  padding-bottom: var(--spacer-124);

  &.hasReversePyramid {
    padding-top: var(--spacer-32);
  }
}

.buttons {
  margin-right: auto;
  margin-left: auto;
}

.topRightMobileOnlyGlow {
  position: absolute;
  z-index: var(--z-index-neg);
  top: 0;
  left: 0;
  width: 50vw;
  height: 50vw;
  transform: translate(-50%, -50%);

  @media (--sm) {
    display: none;
  }
}

.bottomLeftWithContentGlow {
  position: absolute;
  z-index: var(--z-index-neg);
  bottom: 0;
  left: 0;
  width: 50vw;
  height: 50vw;
  transform: translate3d(-50%, 50%, 0);

  @media (--sm) {
    width: 80rem;
    height: 50rem;
    transform: translate3d(-70%, 70%, 0);
  }
  @media (--md) {
    width: 80rem;
    height: 50rem;
    transform: translate3d(-70%, 70%, 0);
  }
}
