.touts {
  composes: subgrid from '~g';
  position: relative;
  row-gap: var(--spacer-48);

  &.toutsCardsLayout {
    row-gap: var(--spacer-20);
  }

  --tout-width: 4;
  --shadow-scale: 0.5;
  --shadow-radius: var(--border-radius-20);

  @media (--sm) {
    --subgrid-start: col 2;
    --subgrid-width: calc(var(--grid-number-of-columns) - 2);
    row-gap: var(--spacer-40);
  }

  &.isMultipleColumns {
    --subgrid-start: var(--column-start);
    --subgrid-width: var(--column-width);
    margin-top: var(--spacer-40);

    @media (--sm) {
      margin-top: 0;
      grid-column-gap: calc(var(--grid-gutter-size, 20px) * 2);
    }

    .tout {
      position: relative;
    }

    .icon {
      @media (--md) {
        position: absolute;
        left: calc(var(--spacer-8) * -1);
        transform: translateX(-100%);
      }
    }
  }

  &.with3Touts {
    @media (--sm) {
      --tout-width: 4;
    }
  }

  &.with4Touts {
    @media (--sm) {
      --tout-width: 6;
    }
    @media (--md) {
      --tout-width: 3;
    }

    &.isMultipleColumns {
      flex-grow: 0;
      flex-shrink: 0;

      --subgrid-start: col 3;
      --tout-width: 4;

      @media (--sm) {
        --tout-width: 5;
      }

      @media (--md) {
        --subgrid-start: col 3;
        --tout-width: 4;
      }

      .tout {
        position: relative;
      }
    }
  }
}

.cardsLayout {
  min-height: 100%;
  padding: var(--spacer-32) var(--spacer-32) var(--spacer-40) var(--spacer-32);
  border: 1px solid var(--color-grey-10);
  background-color: var(--color-grey-05);
  border-radius: var(--border-radius-20);

  @media (--sm) {
    padding: var(--spacer-32) var(--spacer-24);
  }
  @media (--md) {
    padding: var(--spacer-32);
  }
}

.shadow > :first-child {
  z-index: var(--z-index-neg);
}

.tout {
  position: relative;
  grid-column: auto / span var(--tout-width);
}

.toutInnerWrapper {
  position: relative;
  grid-column: auto / span var(--tout-width);
}

.icon {
  position: relative;
  width: auto;
  height: 2.8rem;
  margin-bottom: var(--spacer-16);

  &.isLarge {
    height: 3.5rem;
    margin-bottom: var(--spacer-24);
  }

  * {
    fill: var(--color-accent);
  }
}

.heading {
  color: var(--color-grey-97);
}

.paragraph {
  color: var(--color-grey-60);
}

.hidden {
  display: none;
}

.title6 {
  + .paragraph {
    margin-top: var(--spacer-8);
  }
}

.backgroundGlow {
  position: absolute;
  z-index: var(--z-index-neg);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transform: translate3d(0, 25%, 0);

  @media (--sm) {
    transform: translate3d(0, 40%, 0);
  }
}
