.question {
  composes: title6 from '~typo';
  color: var(--color-grey-90);
}

.questionIcon {
  width: 1.6rem;
  height: 1.6rem;
}

.answer {
  color: var(--color-grey-60);
}

.portableText {
  .paragraph {
    margin-bottom: var(--spacer-16);

    &:last-child {
      margin-bottom: 0;
    }
  }

  & > * {
    &:last-child {
      margin-bottom: initial;
    }
  }
}
