.bento {
  composes: grid from '~g';
  position: relative;
  overflow: hidden; /*  CRITICAL or bentos will jitter  */
  width: 100%;
  padding: var(--spacer-64) 0 var(--spacer-32);

  @media (--sm) {
    padding: var(--spacer-96) 0 var(--spacer-32);
  }

  @media (--md) {
    /* too little bottom padding and cards will get cropped */
    padding: var(--spacer-124) 0 var(--spacer-64);
  }
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.topGlow {
  position: absolute;
  z-index: var(--z-index-0);
  top: 0;
  left: 0;
  width: 50rem;
  height: 50rem;
  @media (--sm) {
    transform: translate(-50%, calc(-50% - (var(--bento-scroll-progress) * 100vh)));
  }
}

.container {
  composes: subgrid from '~g';
  position: relative;
  z-index: var(--z-index-1);
  align-content: end;
  padding: var(--spacer-24) 0 0;

  --subgrid-start: start;
  --subgrid-width: var(--grid-number-of-columns);

  @media (--sm) {
    align-content: start;
    padding: var(--spacer-48) 0 0;
  }

  @media (--md) {
    padding: var(--spacer-64) 0 0;
  }

  &.hasImage {
    @media (--base-only) {
      padding: var(--spacer-320) 0 var(--spacer-24) 0;
    }
  }
}

/* prettier-ignore */
.content {
  grid-column: start / end;
}

.content + .cardsWrapper {
  margin-top: var(--spacer-64);

  @media (--sm) {
    margin-top: var(--spacer-160);
  }
}

.cardsWrapper {
  position: relative;
  grid-column: start / end;
}

.cardsDesktop {
  composes: subgrid from '~g';
  display: none;
  flex-flow: row wrap;
  grid-column: start / end;

  --subgrid-start: start;
  --subgrid-width: var(--grid-number-of-columns);

  @media (--sm) {
    display: grid;
  }
}

.cardsMobile {
  position: relative;
  display: flex;
  width: 100%;
  flex-flow: row wrap;
  row-gap: var(--spacer-16);

  @media (--sm) {
    display: none;
  }
}

.cardsColLeft,
.cardsColRight {
  position: relative;
  display: flex;
  width: 100%;
  flex-flow: row wrap;
  align-content: flex-start;
  align-items: flex-start;
  row-gap: var(--spacer-24);
}

.cardsColLeft {
  grid-column: start / col 8;
}

.cardsColRight {
  grid-column: col 8 / end;
}

.cardsWrapper .card {
  position: relative;
  z-index: calc(var(--flex-order) + 2);
  width: 100%;

  @media (--sm) {
    z-index: var(--z-index-2); /* prevent glow to go above */
  }

  &.noGlow {
    z-index: calc(var(--flex-order) + 3); /* requires z-index minimum of 3 */
  }
}

.bottomGlassContainer {
  position: absolute;
  top: auto;
  bottom: 0;
  width: 100%;
  height: var(--spacer-240);
}

.bottomGlass {
  width: 100%;
  height: 100%;
  background: var(--color-grey-00);
  border-radius: var(--bottom-rounded-module-border-radius);

  /* backdrop-filter: blur(10px); */
}

.bottomGlow {
  position: absolute;
  z-index: var(--z-index-neg);
  bottom: 0;
  left: 50%;
  width: 50rem;
  height: 50rem;
  transform: translate(-50%, 50%);
}
