.heroParallax {
  --content-wrapper-top: 0;
  --transition-out-progress: 0;
  composes: grid from '~g';
  position: relative;
  padding: calc(var(--nav-height) + var(--banner-height, 6rem) + var(--spacer-64)) 0 0;

  @media (--sm) {
    --content-wrapper-top: calc(var(--spacer-96) + var(--nav-height));
    padding-top: calc(var(--nav-height) + var(--spacer-96));
  }
  @media (--md) {
    padding-top: calc(var(--nav-height) + var(--spacer-160));
    padding-bottom: 10vh;
  }
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.contentWrapper {
  composes: subgrid from '~g';
  opacity: calc(1 - 2 * var(--transition-out-progress));
  transform-origin: 50% -200%;

  @media (--sm) {
    position: sticky;
    top: var(--content-wrapper-top);
    opacity: calc(1 - 3 * var(--transition-out-progress));
    transform: scale(calc(1 - 0.06 * var(--transition-out-progress)));
  }

  @media (--md) {
    grid-column: start / end;
  }
}

.eyebrow,
.titles,
.bodies,
.buttons {
  grid-column: start / end;
}

.titles {
  @media (--md) {
    grid-column: col 2 / span 12;
  }
}

.bodies {
  @media (--sm) {
    grid-column: col 3 / span 10;
  }

  @media (--md) {
    grid-column: col 4 / span 8;
  }
}

.forms {
  --placeholder-color: var(--color-grey-70);

  @media (--md) {
    margin-top: var(--spacer-48);
  }
}

.buttons {
  @media (--sm) {
    margin-right: auto;
    margin-left: auto;
  }
}

.mediaGridWrapper {
  composes: grid from '~g';
  grid-column: wrapper-start / wrapper-end;
  pointer-events: none;
}

.mediaContainer {
  margin-top: var(--spacer-48);
  margin-bottom: var(--spacer-64);
  aspect-ratio: var(--studio-display-aspect-ratio);
  grid-column: start / end;
  pointer-events: none;
  transform: translate3d(0, calc(max(-50vw, -30vh) * var(--transition-out-progress)), 0);

  @media (orientation: portrait) {
    transform: none;
  }
  @media (--sm) {
    margin-top: var(--spacer-96);
    margin-bottom: var(--spacer-124);
    grid-column: start / end;
  }

  @media (--md) {
    transform: translate3d(0, calc(max(-20vw, -10vh) * var(--transition-out-progress)), 0);
  }
}

.mediaWrapper {
  position: relative;
  z-index: var(--z-index-1);
  display: flex;
  width: 100%;
  align-self: center;

  /* studio display screen borders */
  padding: 2%;
  grid-column: wrapper-start / wrapper-end;

  @media (--sm) {
    width: auto;
    grid-column: col 2 / col 14;
  }
}

.uiContainer {
  z-index: 2;
  overflow: hidden;
  width: 100%;
}

.uiContainerInner {
  position: relative;
  width: 100%;
  height: 100%;

  &.hasUiMedia {
    transform: scale(1.66) translate3d(14.6%, 7.8%, 0);
  }

  /* fix issue with placeholder thumbnail poking out being video in safari */
  figure img {
    position: absolute;
    top: 0.3rem;
    left: 0.3rem;
    width: calc(100% - 0.3rem);
    height: calc(100% - 0.3rem);
  }
}

.uiMedia {
  width: 100%;
  height: 100%;
}

.media {
  width: 100%;
}

.media.hasUiMedia {
  position: absolute;
  top: 9.97%;
  left: 0;
  width: 72.4%;
}

.glowWrapper {
  position: absolute;
  z-index: var(--z-index-neg);
  bottom: var(--spacer-64);
  left: 50%;
  width: var(--glow-width, 100vw);
  height: var(--glow-height, 100vh);
  pointer-events: none;
  transform: translate3d(-50%, 0, 0);
  @media (--sm) {
    transform: translate3d(-50%, 0, 0);
  }
  @media (--md) {
    transform: translate3d(-55%, -35%, 0);
  }
}

.glow {
  position: absolute;
  inset: 0;
}

.isMobileLeftAligned {
  .mediaContainer {
    justify-self: flex-start;

    @media (--sm) {
      padding-left: 0;
    }
    @media (--md) {
      justify-self: center;
    }
  }
}

.isMobileRightAligned {
  .mediaContainer {
    justify-self: flex-end;

    @media (--sm) {
      padding-right: 0;
    }
    @media (--md) {
      justify-self: center;
    }
  }
}

.isMobileLeftAligned,
.isMobileRightAligned {
  flex-shrink: 0;

  .mediaContainer {
    width: min(156%, 60rem);
    @media (--xs) {
      width: 100%;
    }
  }
}

.title1 {
  @media (--md) {
    /* Exception for spacing if we have a body large after */
    + .bodyLarge {
      margin-top: var(--spacer-20);
    }
  }
}
