.iconContainer {
  position: relative;
  display: inline-block;
  width: auto;
  height: 1em;
  margin: 0;

  .icon {
    position: relative;
    top: calc(-1 * (var(--icon-size) / 2));
    width: auto;
    height: var(--icon-size);
    object-fit: contain;

    path {
      fill: currentcolor;
    }
  }
}

.downloadIconContainer {
  svg {
    top: 0.2em;
  }
}

.phoneIconContainer {
  --icon-size: 2.5rem;
  height: 0.85em;
  margin-left: var(--spacer-16);

  .phoneIcon {
    position: relative;
    top: calc(-50%);
    width: auto;
    height: var(--icon-size);
    object-fit: contain;

    path {
      fill: currentcolor;
    }
  }
}

.arrowContainer {
  --icon-size: 3rem;
  position: relative;
  top: -0.1rem;

  @media (--sm) {
    top: -0.2rem;
  }
}

.arrow {
  width: var(--icon-size);
  height: var(--icon-size);

  --icon-color: var(--color-grey-97);
}
