.stats {
  composes: subgrid from '~g';
  margin-top: var(--spacer-64);
  row-gap: var(--spacer-32);

  --stat-width: 4;
  --label-width: 4;

  &:first-child {
    margin-top: 0;
  }

  @media (--sm) {
    --stat-width: 2;
    --label-width: 2;
    --subgrid-start: col 2;
    --subgrid-width: calc(var(--grid-number-of-columns) - 2);
    margin-top: var(--spacer-64);
    grid-column: var(--subgrid-start) / span var(--subgrid-width);
    row-gap: var(--spacer-24);
  }

  &.gridColumnChildIndex0 {
    margin-top: 0;
  }

  &.gridColumnIndex0,
  &.gridColumnIndex1 {
    @media (--sm) {
      --subgrid-start: col 8;
      --subgrid-width: 7;
    }
  }

  &.with3Stats {
    @media (--sm) {
      --stat-width: 3;
      --label-width: 3;
    }
  }

  &.with4Stats {
    @media (--sm) {
      --stat-width: 3;
      --label-width: 3;
    }
  }

  &.isLarge {
    --label-width: 2.8;
    margin-top: 0;
    row-gap: var(--spacer-64);

    @media (--sm) {
      --stat-width: 6;
      --label-width: 5;
      row-gap: var(--spacer-96);
    }

    @media (--md) {
      --label-width: 3.5;
      row-gap: var(--spacer-124);
    }
  }

  &.isLargeMobile {
    row-gap: var(--spacer-64);
  }
}

.stat {
  composes: title2 from '~typo';
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 0;
  grid-column: auto / span var(--stat-width);
}

.value,
.valueLarge {
  padding-right: 0.06em;
}

.value {
  margin-bottom: var(--spacer-8);
  color: var(--color-grey-100);
}

.valueLarge {
  composes: largeStats from '~typo';
  margin-bottom: calc(var(--spacer-16) - 0.28em);
  color: var(--color-grey-30);

  @media (--sm) {
    margin-bottom: calc(var(--spacer-24) - 0.24em);
  }

  @media (--md) {
    margin-bottom: calc(var(--spacer-32) - 0.24em);
  }
}

.valueLargeMobile {
  composes: largeStatsMobile from '~typo';
  margin-bottom: calc(var(--spacer-16) - 0.28em);
  color: var(--color-grey-100);

  @media (--sm) {
    margin-bottom: var(--spacer-8);
  }
}

.valueAndSuffix {
  white-space: nowrap;

  .isNormal &,
  .isLargeMobile & {
    @media (--sm) {
      margin-bottom: calc(0rem - var(--spacer-4));
    }

    @media (--md) {
      margin-bottom: calc(0rem - var(--spacer-4));
    }
  }

  .isLarge & {
    @media (--sm) {
      margin-bottom: calc(0rem - var(--spacer-4));
    }

    @media (--md) {
      margin-bottom: calc(0rem - var(--spacer-16));
    }
  }
}

.valueSuffix {
  composes: statSuffixRegularGradient from '~deco';
  padding-right: 0.05em;
  color: var(--color-grey-30);
  font-size: 0.6em;
  isolation: var(--stat-suffix-isolation, unset);
  vertical-align: text-top;
}

.valueSuffixLarge {
  composes: statSuffixLargeGradient from '~deco';
  composes: title1 from '~typo';
  position: relative;
  top: 0.05em;
  color: var(--color-grey-30);
  vertical-align: top;
}

.valueSuffixLargeMobile {
  composes: largeStatsSuffixMobile from '~typo';
  composes: statSuffixRegularGradient from '~deco';
  position: relative;
  top: 0;
  margin-left: var(--spacer-4);
  color: var(--color-grey-30);
  vertical-align: top;

  @media (--sm) {
    top: 0.06em;
    margin-left: var(--spacer-8);
  }
}

.label,
.labelLarge,
.labelLargeMobile {
  width: 100%;
}

.label {
  composes: label from '~typo';
  position: relative;
  color: var(--color-grey-60);
  -webkit-text-fill-color: var(--color-grey-60);
}

.labelLarge {
  composes: bodyLarge from '~typo';
  flex: 0 0 calc(var(--label-width) / var(--stat-width) * 100%);
  color: var(--color-grey-50);
}

.labelLargeMobile {
  composes: largeStatsDescriptionMobile from '~typo';
  flex: 0 0 calc(var(--label-width) / var(--stat-width) * 100%);
  color: var(--color-grey-50);
}
