.mediaWithText {
  composes: grid from '~g';
  position: relative;
  width: 100%;
  height: var(--spacer-height-before-xs);
  padding: var(--spacer-24) 0 var(--spacer-48);
  margin: 0;

  --media-with-text-progress: 0;
  --max-parallax-percent: 0;
  --shadow-radius: var(--border-radius-16);

  @media (--sm) {
    --shadow-radius: var(--border-radius-20);
    padding: var(--spacer-48) 0 var(--spacer-96);
  }
  @media (--md) {
    padding-top: var(--spacer-64);
    padding-bottom: var(--spacer-124);
    grid-column: start / end;
  }
}

.isReverseGarageDoor {
  /* stylelint-disable-next-line declaration-no-important */
  padding-bottom: 0 !important;
  margin-bottom: calc(var(--spacer-88) * -1);
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.contentWrapper {
  position: relative;
  z-index: var(--z-index-1);
  width: 100%;
  height: 100%;
}

.mediaContainer {
  position: absolute;
  overflow: hidden;
  inset: 0;
}

.mediaWrapper {
  --default-media-start: start;
  --default-media-end: end;
  position: relative;
  display: flex;
  width: 100%;
  grid-column: var(--media-start, var(--default-media-start)) / var(--media-end, var(--default-media-end));

  @media (--sm) {
    --default-media-start: col 2;
    --default-media-end: col 14;
    width: auto;
  }

  &.studioDisplay {
    .contentWrapper {
      aspect-ratio: 1.77777;
    }
  }

  &:not(.macbookPro):not(.studioDisplay) {
    @media (--md) {
      --default-media-start: start;
      --default-media-end: end;
    }

    .contentWrapper {
      overflow: hidden;
      border-radius: var(--shadow-radius);
    }

    .mediaContainer {
      position: relative;
      overflow: hidden;
      aspect-ratio: 1;
      border-radius: var(--shadow-radius);

      @media (--sm) {
        aspect-ratio: var(--media-aspect-ratio);
      }
    }

    .media {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      height: 100%;
      aspect-ratio: 1;
      @media (--sm) {
        aspect-ratio: var(--media-original-aspect-ratio);
      }
      transition: transform var(--duration-150ms) var(--ease-out);

      &:not(.noMediaParallax) {
        @media (--sm) {
          transform: translate3d(0, calc((var(--media-with-text-progress) - 0.5) * (-1% * var(--max-parallax-percent))), 0) scale(calc(1 + (var(--max-parallax-percent) / 100)));
        }
      }
    }
  }
}

.media {
  width: 100%;
  height: 100%;
}

.isMobileLeftAligned,
.isMobileRightAligned {
  flex-shrink: 0;

  .mediaWrapper {
    width: min(166%, 60rem);
    @media (--xs) {
      width: auto;
    }
  }

  &:not(.macbookPro):not(.studioDisplay) {
    --shadow-radius: var(--border-radius-12);

    .mediaContainer {
      aspect-ratio: var(--media-aspect-ratio);
    }

    .media {
      aspect-ratio: var(--media-original-aspect-ratio);
    }
  }

  &.studioDisplay {
    .mediaWrapper {
      width: min(166%, 60rem);
      @media (--xs) {
        width: auto;
      }
    }
  }
}

.isMobileLeftAligned {
  .mediaWrapper {
    justify-self: flex-start;
    @media (--xs) {
      justify-self: unset;
    }
  }
}

.isMobileRightAligned {
  .mediaWrapper {
    justify-self: flex-end;
    @media (--xs) {
      justify-self: unset;
    }
  }
}

.glow {
  position: absolute;
  z-index: var(--z-index-neg);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.macbookPro {
  align-self: center;
  margin-bottom: 10%;
  aspect-ratio: var(--macbook-pro-aspect-ratio);

  .dropShadow {
    display: none;
  }

  .media {
    overflow: hidden;
    border-top-left-radius: var(--shadow-radius);
    border-top-right-radius: var(--shadow-radius);
  }
}

.studioDisplay {
  align-self: center;

  /* studio display screen borders */
  padding: 2%;

  /* Extra margin for the studio display stand, which is 45% of the width */
  margin-bottom: 24%;

  /* aspect-ratio: var(--studio-display-aspect-ratio); */
  /* stylelint-disable-next-line length-zero-no-unit */
  --shadow-radius: 0px;
}

.withText {
  .studioDisplay {
    margin-bottom: 0;
  }
}

.textContent {
  position: relative;
  z-index: var(--z-index-1);
  padding: 0;
  margin-top: var(--spacer-40);
  aspect-ratio: unset;

  @media (--sm) {
    margin-top: var(--spacer-64);
  }
}

.isTextAbove {
  margin-bottom: var(--spacer-64);
  grid-column: start / end;

  @media (--sm) {
    margin-bottom: var(--spacer-96);
  }

  @media (--md) {
    margin-bottom: var(--spacer-124);
  }
}

.dropShadow {
  z-index: var(--z-index-neg);
}

.centerRightGlow {
  position: absolute;
  top: 0;
  right: 0;
  width: 70vw;
  height: 100vw;
  transform: translate(75%, -25%);

  @media (--sm) {
    width: 45rem;
    height: 80rem;
    transform: translate(95%, -20%);
  }
  @media (--md) {
    width: 45rem;
    height: 80rem;
    transform: translate(95%, -20%);
  }
}

.fullGlow {
  position: absolute;
  z-index: var(--z-index-neg);
  top: 10vh;
  right: -25vh;
  bottom: -50vh;
  left: -25vh;
  pointer-events: none;

  &::after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(180deg, var(--transparent) 10%, var(--color-alpha-black-90) 50%);
    content: '';
  }
}
