.carouselContainer {
  composes: grid from '~g';
}

.slidesContainer {
  position: relative;
  padding: 2%;
  border-radius: var(--border-radius-20);
  grid-column: start / end;

  @media (--sm) {
    grid-column: col 2 / col -1;
  }
}

.glows {
  position: absolute;
  z-index: var(--z-index-neg);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  .glowSlide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: var(--duration-500ms) opacity var(--ease-out) var(--duration-300ms);

    &.active {
      opacity: 1;
    }

    .glow {
      width: 100%;
      height: 100%;
    }
  }
}

.slidesViewport {
  position: relative;
  z-index: var(--z-index-1);
  overflow: hidden;
  aspect-ratio: 1.77777;
  border-radius: 0.2rem;
}

.slidesInner {
  position: absolute;
  width: 100%;
  height: 100%;
}

.slides {
  display: flex;
  justify-content: space-between;
  list-style: none;
}

.slide {
  flex: 1 0 100%;

  &::after {
    position: absolute;
    z-index: var(--z-index-neg);
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: var(--carousel-overlay-gradient);
    content: '';
    opacity: var(--slide-overlay-opacity);
  }
}

.slideInner {
  transform: translateX(var(--slide-x));

  img {
    user-select: none;
  }
}

.labelsContainer {
  z-index: var(--z-index-1);
  padding: var(--spacer-24) 0 var(--spacer-32);
  grid-column: start / end;

  @media (--sm) {
    padding-top: var(--spacer-32);
    padding-bottom: var(--spacer-32);
  }
  @media (--md) {
    padding-top: var(--spacer-40);
    grid-column: 4 / span 10;
  }
}

.labels {
  position: relative;
  display: flex;
  justify-content: var(--justify-labels);
}

.deviceWrapper {
  z-index: var(--z-index-0);
}

.labelsWrapper {
  position: relative;
  display: flex;

  @media (--md) {
    width: 100%;
    justify-content: space-between;
  }
}

.smallSet .labelsWrapper {
  justify-content: center;

  .labelButtonWrapper {
    padding-right: var(--spacer-40);

    @media (--sm) {
      padding-right: var(--spacer-32);
    }

    @media (--md) {
      padding-right: var(--spacer-54);
    }

    &:last-child {
      padding-right: 0;
    }
  }

  .labelHighlight {
    left: 0;
  }
}

.labelButtonWrapper {
  padding-right: var(--spacer-40);

  @media (--sm) {
    padding-right: var(--spacer-32);
  }

  @media (--md) {
    padding-right: var(--spacer-54);
  }

  &:last-child {
    padding-right: 0;
  }

  @media (--md) {
    padding-right: 0;
  }
}

.labelHighlight,
.labelButton::after {
  display: block;
  width: 100%;
  height: 0.2rem;
  margin-top: var(--spacer-8);
  background-color: var(--color-alpha-light-10);
  border-radius: 0.2rem;

  @media (--sm) {
    margin-top: var(--spacer-12);
  }
}

.labelHighlight {
  position: absolute;
  bottom: 0;
  display: block;
  width: var(--label-highlight-width);
  background-color: var(--color-accent);
  transform: translateX(var(--label-highlight-left));
  transform-origin: 0 0;
}

.labelButton {
  composes: label from '~typo';
  position: relative;
  padding: 0;
  margin: 0;
  color: var(--color-grey-60);
  cursor: pointer;
  line-height: 1;
  transition: color 0.2s var(--ease-micro-bounce);
  white-space: nowrap;

  &::after {
    content: '';
    transform: scaleX(0);
    transform-origin: left bottom;
    transition: transform 0.3s var(--ease-micro-bounce);
  }

  &.active {
    color: var(--color-grey-90);
  }

  @media (hover: hover) {
    &:hover {
      &::after {
        transform: scaleX(1);
      }
    }
  }
}
