.carousel {
  position: relative;
  z-index: var(--z-index-4);
  padding-top: var(--spacer-124);
  padding-bottom: calc(var(--spacer-64) + var(--spacer-40));
  /* stylelint-disable-next-line length-zero-no-unit */
  --shadow-radius: 0px;

  @media (--sm) {
    padding-bottom: var(--spacer-124);
  }

  @media (--md) {
    padding-bottom: var(--spacer-160);
  }
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.textContainer {
  composes: grid from '~g';
}

.carousel .textContainer .content {
  grid-column: start / end;

  @media (--sm) {
    .titles {
      grid-column: col 3 / span 4;
    }

    .bodies {
      grid-column: span 5;
    }
  }

  @media (--md) {
    .titles {
      grid-column: col 3 / span 4;
    }
  }
}
