.videoPlaylist {
  position: relative;

  /* TODO: this z-index could break layering around it - solve post-launch */
  z-index: var(--z-index-1);
  width: 100%;
  padding: var(--spacer-124) 0 var(--spacer-96);

  --shadow-radius: var(--border-radius-20);
  @media (--sm) {
    padding: var(--spacer-96) 0;
  }

  @media (--sm) {
    padding: var(--spacer-160) 0;
  }
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.container {
  composes: grid from '~g';
}

.content {
  margin-bottom: var(--spacer-64);
  grid-column: start / end;

  @media (--sm) {
    margin-bottom: var(--spacer-96);
    grid-column: col 2 / col 14;
    text-align: center;
  }
  @media (--md) {
    grid-column: col 3 / col 13;
  }
}

.containerVideoPlaylist {
  --featured-column-start: start;
  --featured-column-width: 4;
  --playlist-start: start;
  --playlist-width: 4;

  @media (--sm) {
    --featured-column-start: col 1;
    --featured-column-width: 7;
    --playlist-start: col 9;
    --playlist-width: 5;
  }
}

.columnFeatured {
  position: relative;
  grid-column: wrapper-start / wrapper-end;

  @media (--xs) {
    grid-column: var(--featured-column-start) / span var(--featured-column-width);
  }
}

.featuredButton {
  position: absolute;
  z-index: 2;
  top: auto;
  right: var(--spacer-24);
  bottom: var(--spacer-24);
  left: auto;

  @media (--xs) {
    right: var(--spacer-32);
    bottom: var(--spacer-32);
  }

  @media (--sm) {
    right: var(--spacer-40);
    bottom: var(--spacer-40);
  }
}

.featuredLabel {
  position: absolute;
  z-index: 2;
  top: auto;
  bottom: var(--spacer-24);
  left: var(--spacer-24);
  width: min(60%, 28rem);
  text-align: left;

  @media (--xs) {
    bottom: var(--spacer-32);
    left: var(--spacer-32);
  }

  @media (--sm) {
    bottom: var(--spacer-40);
    left: var(--spacer-40);
  }
}

.featuredMediaWrapper {
  composes: cardHover from '~g';
  position: relative;
  display: flex;
  overflow: hidden;
  width: 100%;
  height: 100%;
  align-items: flex-end;
  justify-content: flex-start;
  border-radius: var(--shadow-radius);
  transform: translateZ(0);

  &::after {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--video-playlist-gradient);
    border-radius: 2rem;
    content: '';
  }

  &.hasGradientLight {
    &::after {
      background: var(--video-playlist-gradient-light);
    }
  }

  &.hasGradientHeavy {
    &::after {
      background: var(--video-playlist-gradient-heavy);
    }
  }
}

.featuredPoster {
  position: relative;
  width: 100%;
  transform: scale(calc(var(--card-hover-progress, 0) * var(--default-card-hover-scale-amount) + 1)) translateZ(0);
  transition: var(--card-hover-transform-default-transition);

  --image-aspect-ratio: 1.1;
}

.featuredTitle {
  composes: title5 from '~typo';
  position: relative;
  display: block;
  margin-bottom: var(--spacer-8);
  color: var(--color-grey-90);
}

.featuredSubtitle,
.featuredSubtitleDetail {
  composes: label from '~typo';
  color: var(--color-grey-60);
}

.featuredSubtitle {
  margin-right: var(--spacer-12);
}

.columnVideoList {
  grid-column: start / span 4;
  text-align: center;

  @media (--sm) {
    grid-column: col 9 / span 6;
  }
}

.videoListWrapper {
  composes: subgrid from '~g';
  overflow: hidden;
  height: var(--column-height);
  padding: var(--spacer-40) 0 var(--spacer-32);
  margin: 0;
  mask-image: var(--mask-image);
  scrollbar-color: var(--color-alpha-light-20) var(--transparent);
  scrollbar-width: 6px;

  --subgrid-start: start;
  --subgrid-width: 4;
  --thumbnail-start: start;
  --thumbnail-width: 1;
  --description-start: col 2;
  --description-width: 3;
  --column-width: var(--grid-computed-column-width);
  --column-gutter: var(--grid-gutter-size);

  /* calculate height based on columns and aspect ratio of image in left column */
  --column-height: calc(var(--column-width) * 3 + var(--spacer-24) * 2 + var(--spacer-32) + var(--spacer-40));
  --mask-image: linear-gradient(180deg, rgb(0 0 0 / 0%) 0%, rgb(0 0 0 / 100%) 12%, rgb(0 0 0 / 100%) 78%, rgb(0 0 0 / 20%) 90%, rgb(0 0 0 / 0%) 100%);

  @media (--xs) {
    --column-height: calc(var(--column-width) * 9 / 16 * 3 + var(--spacer-24) * 2 + var(--spacer-32) + var(--spacer-40));
  }

  @media (--sm) {
    --subgrid-start: col 9;
    --subgrid-width: 6;
    --thumbnail-start: start;
    --thumbnail-width: 2;
    --description-start: col 3;
    --description-width: 4;
    --column-height: calc((var(--column-width) * 7 + var(--column-gutter) * 6) / 1.1);

    /* calculate height based on columns and aspect ratio of image in left column */
    padding-top: var(--spacer-48);
    padding-bottom: var(--spacer-64);
    overflow-x: hidden;
    overflow-y: scroll;
  }

  /* scrollbar */
  &::-webkit-scrollbar-track {
    display: none;
  }

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--color-alpha-light-20);
    border-radius: 10px;
  }

  .isPlaylistExpanded & {
    height: auto;
    mask-image: none;

    @media (--sm) {
      height: var(--column-height);
      mask-image: var(--mask-image);
    }
  }
}

.videoListItem {
  composes: subgrid from '~g';
  composes: cardHover from '~g';
  align-items: center;
  justify-content: start;
  margin-bottom: var(--spacer-24);
  text-align: left;

  --subgrid-width: 4;
  --subgrid-start: col 1;

  @media (--sm) {
    --subgrid-width: 6;
    --subgrid-start: col 1;
  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

.itemThumbnailButton,
.itemDescriptionButton {
  padding: 0;
  text-align: left;
}

.itemDescriptionButton {
  transform: translate3d(calc(var(--card-hover-progress, 0) * 0.4rem), 0, 0);
  transition: var(--card-hover-transform-default-transition);
}

.itemThumbnailButton {
  position: relative;
  display: flex;
  overflow: hidden;
  border-radius: 0.8rem;
  grid-column: var(--thumbnail-start) / span var(--thumbnail-width);
  transform: translateZ(0);
}

.itemThumbnail {
  position: relative;
  width: 100%;
  transform: scale(calc(var(--card-hover-progress, 0) * 0.03 + 1)) translateZ(0);
  transition: var(--card-hover-transform-default-transition);

  --image-aspect-ratio: 1;

  @media (--xs) {
    --image-aspect-ratio: 16 / 9;
  }
  @media (--sm) {
    --image-aspect-ratio: 16 / 9;
  }
  @media (--md) {
    --image-aspect-ratio: 16 / 9;
  }
  @media (--lg) {
    --image-aspect-ratio: 16 / 9;
  }
}

.itemDescription {
  grid-column: var(--description-start) / span var(--description-width);
}

.itemTitle,
.itemSubtitleWrapper {
  composes: label from '~typo';
}

.itemTitle {
  display: block;
  color: var(--color-grey-90);
}

.itemSubtitleWrapper {
  color: var(--color-grey-60);
  white-space: nowrap;
}

.itemSubtitle,
.itemSubtitleDetail {
  display: inline-block;
}

.itemSubtitle {
  margin-right: var(--spacer-16);
}

.itemPlayButton {
  position: absolute;
  right: var(--spacer-8);
  bottom: var(--spacer-8);
}

.viewAllButton {
  padding-bottom: var(--spacer-24);

  @media (--sm) {
    display: none;
  }
}

.dropShadow {
  width: 100%;
  height: 100%;
}

.buttonPlay {
  width: 4rem;
  height: 4rem;
}

.buttonPlay,
.videoListItem {
  &:hover,
  [data-button-play-trigger]:hover & {
    --item-hover-progress: 1;

    path {
      fill: var(--button-hover-color);
    }
  }
}
