$editorialSmallBreakpoint: 905px;
$imageAspectRatio: 4 / 3;
@value imageAspectRatio: $imageAspectRatio;

.editorialCards {
  composes: grid from '~g';
  padding: var(--spacer-88) 0;

  --shadow-radius: var(--border-radius-20);
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.content {
  composes: subgrid from '~g';
  row-gap: var(--spacer-96);

  @media (--sm) {
    padding: 0 calc((100vw - 60rem) / 2 - var(--grid-computed-margin-width));
  }

  @media (min-width: $editorialSmallBreakpoint) {
    padding: 0;
    row-gap: var(--spacer-124);
  }
}

.card {
  composes: cardAndButtonHover from '~g';
  grid-column: start / end;

  @media (min-width: $editorialSmallBreakpoint) {
    grid-column: auto / span 7;
  }
}

.glowContainer {
  z-index: auto;

  /* increase canvas size to prevent cropping because these are too small */
  --mobile-canvas-scale: 1.1;

  .glow {
    --glow-translate-x: -50%;
    --glow-translate-y: -40%;
    z-index: var(--z-index-neg);
  }
}

.imageContainer {
  display: block;
  overflow: hidden;
  width: 100%;
  border-radius: var(--shadow-radius);

  --image-aspect-ratio: $imageAspectRatio;
}

.imageElement {
  z-index: var(--z-index-1);
  display: block;
  transform: scale(calc(var(--card-hover-progress, 0) * var(--default-card-hover-scale-amount) + 1));
  transition: var(--card-hover-transform-default-transition), opacity var(--duration-600ms);
}

.innerCardContainer {
  position: relative;
  margin-bottom: var(--spacer-40);
  cursor: pointer;
}

.shadowContainer {
  position: absolute;
  z-index: var(--z-index-1);
  inset: 0;
}

.text {
  position: relative;
  z-index: var(--z-index-2);
  margin-bottom: var(--spacer-12);

  @media (--sm) {
    padding-right: 4vw;
    padding-left: var(--spacer-16);
  }

  @media (--md) {
    padding-right: 2vw;
    padding-left: var(--spacer-32);
  }
}

.icon {
  position: relative;
  z-index: var(--z-index-2);
  width: auto;
  height: 2.5rem;
  box-sizing: content-box;
  margin-bottom: var(--spacer-8);

  @media (--sm) {
    padding-left: var(--spacer-16);
  }

  @media (--md) {
    padding-left: var(--spacer-32);
    margin-bottom: var(--spacer-16);
  }

  &.isTall {
    &.iconSvg,
    &.iconImage {
      height: 5rem;
    }
  }

  &.iconSvg {
    * {
      fill: var(--color-alpha-light-50);
    }
  }
}

.bodies {
  color: var(--color-grey-100);
}

.button {
  padding-left: 0;

  @media (--sm) {
    padding-left: var(--spacer-16);
  }

  @media (--md) {
    padding-left: var(--spacer-32);
  }
}
