.logo {
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon {
  --chiclet-size: 8rem;
  overflow: hidden;
  width: var(--chiclet-size);
  flex-shrink: 0;
  margin-bottom: var(--spacer-20);
  border-radius: var(--icons-border-radius);

  @media (--sm) {
    --chiclet-size: 8rem;
    overflow: hidden;
    margin-bottom: var(--spacer-12);
  }

  &.featured {
    --chiclet-size: 7.8rem;
    margin-top: var(--spacer-40);
    margin-bottom: 0;

    @media (--sm) {
      --chiclet-size: 7.8rem;
      margin-top: var(--spacer-40);
      margin-bottom: 0;
    }
  }

  &.cloud {
    --chiclet-size: 7.8rem;

    @media (--sm) {
      --chiclet-size: 6.8rem;
    }
    @media (--lg) {
      --chiclet-size: 10rem;
    }
  }
}
