.buttonShadow {
  box-shadow: 16.3869px 14.6789px 31px rgb(0 0 0 / 31%), 6.31198px 5.6541px 9.87407px rgb(0 0 0 / 18.8%), 1.33523px 1.19606px 2.52593px rgb(0 0 0 / 12.2%);
}

.pricingCardBlur {
  /* backdrop-filter: blur(50px); */
}

.textContainerGradient {
  background: linear-gradient(180deg, rgb(0 0 0 / 0%) 0.46%, rgb(0 0 0 / 60%) 18%, #000000 48%);
}

.pricingCardWideGradient {
  background: linear-gradient(357.01deg, rgb(0 0 0 / 50%) 1.77%, rgb(0 0 0 / 50%) 42.27%, rgb(37 36 56 / 50%) 99.72%);
}

.carouselSlideGradient {
  background: linear-gradient(0deg, rgb(0 0 0 / 40%), rgb(0 0 0 / 40%)), linear-gradient(73.81deg, rgb(0 0 0 / 50%) 16.66%, rgb(0 0 0 / 0%) 52.25%);
}

.statSuffixLargeGradient {
  background: var(--stat-suffix-gradient, linear-gradient(275.41deg, #4a3725 -152.44%, #36384a 97.44%));
  background-clip: text;
  mix-blend-mode: normal;
  -webkit-text-fill-color: transparent;
}

.statSuffixRegularGradient {
  background: var(--stat-suffix-gradient, linear-gradient(155.1deg, #fcfcfc -2.85%, rgb(252 252 252 / 0%) 78.46%), linear-gradient(180deg, #363846 26.79%, rgb(54 56 70 / 0%) 93.45%));
  background-clip: text;
  mix-blend-mode: normal;
  -webkit-text-fill-color: transparent;
}

.inputField {
  /* backdrop-filter: blur(20px); */
  background: var(--color-alpha-dark-50);
  transition: background-color var(--duration-500ms) var(--ease-basic-butter);

  &:has(input:hover),
  &:has(input:focus) {
    background: transparent;
  }
}

.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.mobileNavShadow {
  box-shadow: var(--mobile-shadow-x) 0 50px rgb(0 0 0 / 50%);
}

.overlaid::after {
  position: absolute;
  display: block;
  background-color: var(--color-grey-00);
  content: '';
  inset: 0;
  opacity: var(--overlay-opacity, 0);
  pointer-events: none;
  transition: opacity 0.1s;
}

.hasSpacerBefore {
  --spacer-height-before: var(--sp-h-b-base);
  /* stylelint-disable-next-line declaration-no-important */
  padding-top: var(--spacer-height-before, 0) !important;

  @media (--xs) {
    --spacer-height-before: var(--sp-h-b-xs);
  }

  @media (--sm) {
    --spacer-height-before: var(--sp-h-b-sm);
  }

  @media (--md) {
    --spacer-height-before: var(--sp-h-b-md);
  }

  @media (--lg) {
    --spacer-height-before: var(--sp-h-b-lg);
  }
}

.hasSpacerAfter {
  --spacer-height-after: var(--sp-h-a-base);
  /* stylelint-disable-next-line declaration-no-important */
  padding-bottom: var(--spacer-height-after, 0) !important;

  @media (--xs) {
    --spacer-height-after: var(--sp-h-a-xs);
  }

  @media (--sm) {
    --spacer-height-after: var(--sp-h-a-sm);
  }

  @media (--md) {
    --spacer-height-after: var(--sp-h-a-md);
  }

  @media (--lg) {
    --spacer-height-after: var(--sp-h-a-lg);
  }
}
