.portableText {
  position: relative;
}

/* by default, will render portable text on a grid */

/* you may want to disable this if portable text is nested in other components */

.isAutoGrid {
  composes: subgrid from '~g';

  --subgrid-start: start;
  --subgrid-width: var(--grid-number-of-columns);
  --content-start: var(--subgrid-start);
  --content-width: var(--subgrid-width);
  --column-start: start;
  --column-width: var(--grid-number-of-columns);
  align-content: start;

  &.isMultipleColumns {
    --subgrid-start: var(--column-start);
    --subgrid-width: var(--column-width);

    &:first-child {
      @media (--sm) {
        --column-start: start;
        --column-width: 7;
      }
    }

    &:nth-child(2) {
      @media (--sm) {
        --column-start: 8;
        --column-width: 6;
      }
    }

    &.hasTouts {
      &:first-child {
        @media (--sm) {
          --column-start: col 2;
          --column-width: 6;
        }

        @media (--md) {
          --column-start: start;
          --column-width: 6;
        }
      }

      &:nth-child(2) {
        @media (--sm) {
          --column-start: 8;
          --column-width: 7;
        }

        @media (--md) {
          --column-start: 7;
          --column-width: 8;
        }
      }
    }
  }
}

.isMultipleColumnsWrapper {
  composes: subgrid from '~g';
  row-gap: var(--spacer-16);

  --subgrid-start: start;
  --subgrid-width: var(--grid-number-of-columns);

  @media (--sm) {
    row-gap: var(--spacer-64);
  }
}

.accents,
.titles,
.bodies,
.types,
.list {
  &:first-child {
    margin-top: 0;
  }

  .isAutoGrid.isAutoRag & {
    grid-column: var(--content-start) / span var(--content-width);
  }
}

/* left aligned, single-column titles */

.accents,
.titles {
  .isSingleColumn & {
    --content-start: start;
    --content-width: var(--grid-number-of-columns);

    @media (--sm) {
      --content-start: col 2;
      --content-width: 11;
    }

    @media (--md) {
      --content-start: col 2;
      --content-width: 11;
    }
  }

  .isSingleColumn.ragTight & {
    @media (--sm) {
      --content-start: col 2;
      --content-width: 9;
    }

    @media (--md) {
      --content-start: col 2;
      --content-width: 9;
    }
  }

  .isSingleColumn.ragWide & {
    @media (--sm) {
      --content-start: col 2;
      --content-width: 12;
    }

    @media (--md) {
      --content-start: col 2;
      --content-width: 12;
    }
  }

  /* center aligned, single-column titles */

  .isSingleColumn.alignCenterAll & {
    text-align: center;
  }

  .isSingleColumn.alignCenter & {
    text-align: left;

    @media (--sm) {
      text-align: center;
    }
  }

  .isSingleColumn.alignCenter &,
  .isSingleColumn.alignCenterAll & {
    @media (--sm) {
      --content-start: col 3;
      --content-width: 10;
    }

    @media (--md) {
      --content-start: col 3;
      --content-width: 10;
    }
  }

  .isSingleColumn.alignCenter.ragTight &,
  .isSingleColumn.alignCenterAll.ragTight & {
    @media (--sm) {
      --content-start: col 4;
      --content-width: 8;
    }

    @media (--md) {
      --content-start: col 4;
      --content-width: 8;
    }
  }

  .isSingleColumn.alignCenter.ragWide &,
  .isSingleColumn.alignCenterAll.ragWide & {
    @media (--sm) {
      --content-start: start;
      --content-width: 14;
    }

    @media (--md) {
      --content-start: start;
      --content-width: 14;
    }
  }

  /* left aligned, multiple column titles */

  .isMultipleColumns & {
    --content-start: start;
    --content-width: var(--column-width);
  }

  .isMultipleColumns.isColumn1 & {
    @media (--sm) {
      --content-start: col 2;
      --content-width: 5;
    }
  }

  .isMultipleColumns.isColumn2 & {
    @media (--sm) {
      --content-start: start;
      --content-width: 7;
    }
  }

  .isMultipleColumns.isColumn1.ragTight & {
    padding-right: 1vw;

    @media (--sm) {
      --content-start: col 3;
      --content-width: 5;
    }
  }

  .isMultipleColumns.isColumn2.ragTight & {
    --content-start: start;
    --content-width: 5;
  }

  .isMultipleColumns.isColumn1.hasTouts & {
    --content-start: start;
    --content-width: 5;
    padding-right: 1vw;
  }
}

/* center aligned, single-column body */

.form,
.buttons {
  margin-top: var(--spacer-32);

  &:first-child {
    margin-top: 0;
  }
}

.form {
  margin-bottom: var(--spacer-32);

  &:last-child {
    margin-bottom: 0;
  }
}

.bodies,
.buttons,
.list {
  .isSingleColumn & {
    --content-start: start;
    --content-width: var(--grid-number-of-columns);

    @media (--sm) {
      --content-start: 2;
      --content-width: 10;
    }
  }

  .isSingleColumn.ragTight & {
    @media (--sm) {
      --content-start: col 2;
      --content-width: 9;
    }

    @media (--md) {
      --content-start: col 2;
      --content-width: 9;
    }
  }

  .isSingleColumn.ragWide & {
    @media (--sm) {
      --content-start: col 2;
      --content-width: 12;
    }

    @media (--md) {
      --content-start: col 2;
      --content-width: 12;
    }
  }

  /* center aligned, single-column body */

  .isSingleColumn.alignCenterAll & {
    text-align: center;
  }

  .isSingleColumn.alignCenter & {
    text-align: left;

    @media (--sm) {
      text-align: center;
    }
  }

  .isSingleColumn.alignCenter &,
  .isSingleColumn.alignCenterAll & {
    @media (--sm) {
      --content-start: col 3;
      --content-width: 10;
    }

    @media (--md) {
      --content-start: col 3;
      --content-width: 10;
    }
  }

  .isSingleColumn.alignCenter.ragTight &,
  .isSingleColumn.alignCenterAll.ragTight & {
    @media (--sm) {
      --content-start: col 4;
      --content-width: 8;
    }

    @media (--md) {
      --content-start: col 4;
      --content-width: 8;
    }
  }

  .isSingleColumn.alignCenter.ragWide &,
  .isSingleColumn.alignCenterAll.ragWide & {
    @media (--sm) {
      --content-start: col 3;
      --content-width: 10;
    }

    @media (--md) {
      --content-start: col 3;
      --content-width: 10;
    }
  }

  /* left aligned, multiple column bodies */

  .isMultipleColumns & {
    --content-start: start;
    --content-width: var(--column-width);
  }

  .isMultipleColumns.isColumn1 & {
    --content-start: col 2;
    --content-width: 5;
  }

  .isMultipleColumns.isColumn2 & {
    --content-start: start;
    --content-width: 7;
  }

  .isMultipleColumns.isColumn1.ragTight & {
    --content-start: col 3;
    --content-width: 5;
  }

  .isMultipleColumns.isColumn2.ragTight & {
    --content-start: start;
    --content-width: 5;
  }

  .isMultipleColumns.isColumn1.hasTouts & {
    --content-start: start;
    --content-width: 5;
    padding-right: 1vw;
  }
}

.types {
  .isSingleColumn.alignCenter &,
  .isSingleColumn.alignCenterAll & {
    grid-column: start / end;

    @media (--sm) {
      margin-right: auto;
      margin-left: auto;
      text-align: unset;
    }

    @media (--md) {
      grid-column: col 4 / span 8;
    }

    @media (--lg) {
      grid-column: col 5 / span 6;
    }

    &.form {
      width: 100%;

      @media (--sm) {
        /* TODO:
        when the 12-column grid is added, the styling below should apply:
        width: calc(100% - var(--spacer-48));
        grid-column: col 5 / span 6;
        */
        grid-column: col 4 / span 8;
      }

      @media (--md) {
        grid-column: col 5 / span 6;
      }
    }

    &:not(.form) {
      @media (--md) {
        max-width: 100%;
      }

      @media (--lg) {
        max-width: 100%;
      }
    }
  }

  .isSingleColumn.alignCenterAll & {
    margin-right: auto;
    margin-left: auto;
  }
}

.touts,
.stats,
.spacer {
  .isSingleColumn & {
    --content-start: start;
    --content-width: var(--grid-number-of-columns);

    @media (--sm) {
      --content-start: col 2;
      --content-width: 12;
    }

    @media (--md) {
      --content-start: col 2;
      --content-width: 12;
    }
  }

  .isSingleColumn.ragWide & {
    --content-start: start;
    --content-width: var(--grid-number-of-columns);

    @media (--sm) {
      --content-start: col 2;
      --content-width: 12;
    }

    @media (--md) {
      --content-start: col 2;
      --content-width: 12;
    }
  }

  .isMultipleColumns.isColumn1 & {
    --content-start: start;
    --content-width: 5;
  }

  .isMultipleColumns.isColumn2 & {
    --content-start: start;
    --content-width: 8;
  }
}

/* typography */

.title0 {
  composes: title0 from '~typo';
  margin-top: var(--spacer-16);

  @media (--sm) {
    margin-top: var(--spacer-32);
  }

  @media (--md) {
    margin-top: var(--spacer-48);
  }

  &:last-child {
    margin-bottom: var(--spacer-40);

    @media (--sm) {
      margin-bottom: var(--spacer-32);
    }

    @media (--md) {
      margin-bottom: var(--spacer-96);
    }
  }
}

.title1 {
  composes: title1 from '~typo';
  margin-top: var(--spacer-20);

  @media (--md) {
    margin-top: var(--spacer-24);

    /* Exception for spacing if we have a body large after */
    + .bodyLarge {
      margin-top: var(--spacer-8);
    }
  }
}

.title2 {
  composes: title2 from '~typo';
  margin-top: var(--spacer-16);
  @media (--sm) {
    margin-top: var(--spacer-20);
  }

  @media (--md) {
    margin-top: var(--spacer-24);
  }
}

.title3 {
  composes: title3 from '~typo';
  margin-top: var(--spacer-24);
  @media (--sm) {
    margin-top: var(--spacer-20);
  }

  @media (--md) {
    margin-top: var(--spacer-24);
  }
}

.title4 {
  composes: title4 from '~typo';
  margin-top: var(--spacer-24);
  @media (--sm) {
    margin-top: var(--spacer-20);
  }

  @media (--md) {
    margin-top: var(--spacer-24);
  }
}

.title5 {
  composes: title5 from '~typo';
  margin-top: var(--spacer-24);
  @media (--sm) {
    margin-top: var(--spacer-20);
  }

  @media (--md) {
    margin-top: var(--spacer-24);
  }
}

.title6 {
  composes: title6 from '~typo';
  margin-top: var(--spacer-32);
}

.body,
.normal {
  margin-top: var(--spacer-16);

  @media (--md) {
    margin-top: var(--spacer-20);
  }
}

.normal {
  composes: body from '~typo';
}

.bodyLarge {
  composes: bodyLarge from '~typo';
  margin-top: var(--spacer-16);

  @media (--md) {
    margin-top: var(--spacer-20);
  }
}

.bodySmall {
  composes: bodySmall from '~typo';
  margin-top: var(--spacer-8);

  @media (--sm) {
    margin-top: var(--spacer-20);
  }
}

.eyebrowSmall {
  composes: eyebrowSmall from '~typo';
  color: var(--color-accent);
}

.eyebrow {
  composes: eyebrow from '~typo';
  color: var(--color-accent);
}

.em {
  composes: em from '~typo';
}

.label {
  composes: label from '~typo';
}

.strong {
  composes: strong from '~typo';
}

.body,
.bodyLarge,
.bodySmall {
  a {
    text-decoration: underline;
  }
}

.list {
  display: block;
  grid-column: var(--content-start) / span var(--content-width);

  @media (--sm) {
    grid-column: 2 / span 12;
  }

  li {
    margin-top: var(--spacer-16);

    strong {
      color: var(--color-grey-80);
    }
  }
}

.bullet,
.number {
  list-style: initial;
}

.listItemOl,
.listItemUl {
  composes: body from '~typo';
  display: list-item;
  margin-left: var(--spacer-24);
}

/* stylelint-disable-next-line selector-class-pattern */
.u {
  text-decoration: underline;
}

.touts {
  margin-top: var(--spacer-64);
}

.accents {
  display: block;
  margin-top: var(--spacer-20);

  @media (--md) {
    margin-top: var(--spacer-24);
  }

  &:first-child {
    margin-top: 0;
  }
}

.titles {
  &:first-child {
    margin-top: 0;
  }
}
