.pricingOverview {
  composes: grid from '~g';
  position: relative;
  z-index: var(--z-index-2);
  overflow: hidden;
  padding-top: calc(var(--nav-height) + var(--spacer-64));
  padding-bottom: var(--spacer-48);
  margin-bottom: var(--spacer-48);
  background: var(--pricing-overview-gradient);
  border-radius: var(--bottom-rounded-module-border-radius);
  opacity: 1;

  @media (--sm) {
    padding-top: calc(var(--nav-height) + var(--spacer-124));
    padding-bottom: var(--spacer-96);
    margin-bottom: var(--spacer-96);
  }

  @media (--md) {
    padding-top: calc(var(--nav-height) + var(--spacer-160));
    padding-bottom: var(--spacer-160);
    margin-bottom: var(--spacer-160);
  }
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.text {
  margin-bottom: var(--spacer-32);
  grid-column: start / wrapper-end;

  @media (--sm) {
    margin-bottom: var(--spacer-48);
    grid-column: start / end;
  }
}

.radioButtons {
  margin-bottom: var(--spacer-64);
  grid-column: start / end;

  @media (--sm) {
    margin-bottom: var(--spacer-96);
  }

  @media (--md) {
    margin-bottom: var(--spacer-160);
  }
}

.titles {
  @media (--sm) {
    grid-column: start / end;
  }

  @media (--md) {
    grid-column: start / span 12;
  }
}

.pricingCardsWrapper,
.customCardsWrapper {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  grid-column: wrapper-start / wrapper-end;

  @media (--xs) {
    grid-column: start / end;
  }
  @media (--sm) {
    flex-flow: row nowrap;
    grid-column: start / end;
  }
}

.pricingCardsWrapper {
  margin-bottom: var(--spacer-40);
  gap: var(--spacer-40) 0;

  @media (--sm) {
    overflow: auto;
    padding: 0 var(--grid-computed-margin-width);
    margin-bottom: var(--spacer-48);
    gap: var(--spacer-48) var(--grid-gutter-size);
    grid-column: wrapper-start / wrapper-end;
    scroll-padding: var(--grid-computed-margin-width) 0 0 var(--grid-computed-margin-width);
    scroll-snap-type: x mandatory;
  }

  @media (--md) {
    overflow: unset;
    padding: 0;
    margin-bottom: var(--spacer-40);
    grid-column: start / end;
  }
}

.pricingCard {
  /* composes: pricingCardBlur from '~deco'; */
  flex: 0 0 var(--card-width);
  padding: var(--spacer-48) var(--spacer-40) var(--spacer-48);
  background: var(--pricing-card-gradient);
  border-radius: 4rem;
  box-shadow: inset 0 0 0 1px var(--color-alpha-light-5);
  scroll-snap-align: start;

  --card-width: 100%;

  @media (--sm) {
    --card-width: calc((var(--grid-computed-inner-width) - var(--grid-computed-column-width) - var(--spacer-40)) / 2);
    padding: var(--spacer-48) var(--spacer-24) var(--spacer-48);
  }

  @media (--md) {
    --card-width: calc((100% - var(--grid-gutter-size) * 2) / 3);
  }
}

.pricingCardWide {
  composes: pricingCardWideGradient from '~deco';
  display: flex;

  /* make grid of 3 columns each --card-width wide */
  flex: 0 0 100%;
  flex-flow: row wrap;
  padding: var(--spacer-48) 0;

  .cardTitle {
    position: relative;
    width: 100%;
    padding: 0 var(--spacer-40);
  }

  .cardIntro {
    --section-width: 100%;
    width: var(--section-width);
    flex: 0 0 var(--section-width);
    padding: 0 var(--spacer-40);

    @media (--sm) {
      --section-width: calc(var(--grid-gutter-size) * 6 + var(--grid-computed-column-width) * 7);
      margin-bottom: var(--spacer-24);
    }

    @media (--md) {
      --section-width: calc(var(--grid-gutter-size) * 5 + var(--grid-computed-column-width) * 5);
    }
  }

  .cardDetails,
  .cardDetailsExtra {
    --section-width: 100%;
    width: var(--section-width);
    flex: 0 0 var(--section-width);
    padding: 0 var(--spacer-64) 0 var(--spacer-40);

    @media (--sm) {
      --section-width: calc(var(--grid-gutter-size) * 4 + var(--grid-computed-column-width) * 5);
      padding: 0;
    }

    @media (--md) {
      --section-width: calc(var(--grid-gutter-size) * 4 + var(--grid-computed-column-width) * 4);
      padding: 0 var(--spacer-64) 0 0;
    }
  }

  .cardDetails {
    position: relative;
    display: flex;
    flex-flow: column nowrap;
    align-items: stretch;
    justify-content: flex-start;

    @media (--sm) {
      margin-left: calc(var(--grid-gutter-size) * 2 + var(--grid-computed-column-width) * 1);
    }

    @media (--md) {
      overflow: unset;
      margin-left: 0;
    }
  }

  .cardDetailsBulletsWrapper {
    --mask-image: linear-gradient(180deg, rgb(0 0 0 / 100%) 0%, rgb(0 0 0 / 100%) 12%, rgb(0 0 0 / 100%) 70%, rgb(0 0 0 / 15%) 87%, rgb(0 0 0 / 0%) 93%);
    padding-top: var(--spacer-192);
    margin-top: calc(-1 * var(--spacer-192));

    @media (--sm) {
      flex: 1 0 100%;
      mask-image: var(--mask-image);
    }

    @media (--md) {
      padding-top: unset;
      margin-top: unset;
      mask-image: unset;
    }

    &.isBulletsExpanded {
      @media (--sm) {
        padding-bottom: var(--spacer-24);
        mask-image: unset;
      }
    }
  }

  .cardDetailBullets {
    position: relative;

    @media (--sm) {
      position: absolute;
    }

    @media (--md) {
      position: relative;
    }

    &.isBulletsExpanded {
      @media (--sm) {
        position: relative;
      }
    }
  }

  .showMoreButton {
    position: absolute;
    top: auto;
    bottom: 0;
    left: 0;
    display: none;

    @media (--sm) {
      display: unset;
    }

    @media (--md) {
      display: none;
    }
  }

  .cardDetailsExtra {
    display: none;

    @media (--md) {
      display: block;
    }
  }

  .cardDescription {
    padding-bottom: 0;
    border-bottom: none;
    margin-bottom: var(--spacer-16);
  }

  .cardTopDescription {
    margin-bottom: var(--spacer-40);
  }

  .cardDetailButton {
    margin-top: var(--spacer-16);
    margin-bottom: 0;

    &:nth-of-type(1) {
      margin-top: 0;
    }
  }
}

.cardIntro {
  margin-bottom: var(--spacer-48);
}

.cardTitle {
  composes: eyebrow from '~typo';
  margin-bottom: var(--spacer-16);
  color: var(--color-cobalt);
}

.cardPriceWrapper {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-end;
  justify-content: flex-start;
  margin-bottom: var(--spacer-8);
}

.cardPriceContext {
  margin-bottom: 0.7em;

  @media (--sm) {
    padding: 0 0 var(--spacer-12);
    margin-bottom: -0.1em;
  }
}

.cardPriceContextLine {
  composes: title6 from '~typo';
  position: relative;
  display: block;
}

.cardSecondaryTitle,
.cardSecondaryTitleWide {
  composes: title3md2 from '~typo';
}

.cardPrice {
  composes: titleStatic68 from '~typo';
  margin-right: var(--spacer-8);
}

.cardDescription {
  composes: bodySmall from '~typo';
  padding-bottom: var(--spacer-24);
  border-bottom: 1px solid var(--color-alpha-hawkes-20);
  margin-bottom: var(--spacer-24);
  color: var(--color-grey-60);
}

.cardTopBullets {
  position: relative;
  width: 100%;
}

.cardTopBullets,
.cardTopDescription {
  margin-bottom: var(--spacer-48);
}

.cardTopBulletItem {
  position: relative;
  width: 100%;
  margin-bottom: var(--spacer-6);

  &:last-child {
    margin-bottom: 0;
  }
}

.cardTopDescription,
.cardTopBulletText {
  composes: bodySmall from '~typo';
  color: var(--color-grey-97);
}

.cardDetailsTitle {
  composes: bodySmall from '~typo';
  composes: medium from '~typo';
  margin-bottom: var(--spacer-8);
  color: var(--color-grey-97);
}

.cardDetailBullets {
  position: relative;
  width: 100%;

  @media (--md) {
    position: relative;
  }
}

.cardDetailBulletItem {
  position: relative;
  width: 100%;
  margin-bottom: var(--spacer-8);
}

.cardDetailBulletItemExtra {
  @media (--md) {
    display: none;
  }
}

.cardDetailBulletCheck {
  position: relative;
  width: 1.2rem;
  height: auto;
  margin-right: var(--spacer-8);

  * {
    fill: var(--color-grey-97);
  }
}

.cardDetailBulletSvgButton,
.topDetailBulletSvgButton {
  position: absolute;
  top: calc(50% - 1rem);
  right: -0.5rem;
  left: auto;
  width: 2.4rem;
  height: 2.4rem;
}

.cardDetailBulletSvg,
.topDetailBulletSvg {
  position: relative;
  width: 1.2rem;
  height: auto;

  * {
    fill: var(--color-grey-60);
  }
}

.cardDetailBulletTooltip,
.topDetailBulletTooltip {
  composes: bodySmall from '~typo';
  position: absolute;
  z-index: var(--z-index-3);
  top: auto;
  right: 0.1rem;
  bottom: var(--spacer-32);
  left: auto;
  width: 100%;
  padding: var(--spacer-12) var(--spacer-24) var(--spacer-12) var(--spacer-20);
  border: 1px solid var(--color-alpha-light-10);
  background: var(--color-grey-05);
  border-radius: 1rem 1rem 0;
  opacity: 0;
  transition: opacity 0.2s var(--ease-out), visibility 0.2s;
  visibility: hidden;

  $triangleSize: 12px;

  &::after {
    position: absolute;
    top: auto;
    right: 0;
    bottom: 0;
    left: auto;
    width: 4rem;
    height: $triangleSize;
    background: var(--color-grey-05);
    border-radius: 1rem 1rem 0;
    content: '';
  }

  [data-is-active] & {
    opacity: 1;
    visibility: visible;
  }
}

.cardDetailBulletTooltipText,
.topDetailBulletTooltipText {
  position: relative;
  z-index: var(--z-index-2);
}

.cardDetailBulletTooltipTail,
.topDetailBulletTooltipTail {
  position: absolute;
  top: 100%;
  right: -1px;
  width: $triangleSize;
  height: $triangleSize;
  border: 1px solid var(--color-alpha-light-10);
  background: var(--color-grey-05);
  transform: skewY(45deg) translateY(-50%);
}

.cardDetailBulletText,
.topDetailBulletText {
  composes: bodySmall from '~typo';
  color: var(--color-grey-60);
}

.cardDetailButton {
  z-index: var(--z-index-2);

  @media (--sm) {
    width: auto;
  }
}

/* Glows */

.topRightGlow {
  position: absolute;
  z-index: var(--z-index-neg);
  top: 0;
  right: 0;
  display: block;
  width: 30rem;
  height: 30rem;
  transform: translateX(50%) translateY(-50%);

  @media (--md) {
    width: 40rem;
    height: 40rem;
  }
}

.centerRightGlow {
  position: absolute;
  z-index: var(--z-index-neg);
  top: 50%;
  left: 98vw;
  display: block;
  width: 50rem;
  height: 100rem;
}
