.gridCards {
  composes: grid from '~g';
  padding: var(--spacer-96) 0;
  background-color: var(--color-grey-00);

  --shadow-scale: 0.45;

  @media (--md) {
    padding-top: var(--spacer-160);
  }
}

.text {
  composes: subgrid from '~g';

  @media (--sm) {
    margin-bottom: var(--spacer-124);
    text-align: center;

    --subgrid-start: 3;
    --subgrid-width: 12;
  }
}

.hasBackground {
  position: relative;

  &::before {
    position: absolute;
    z-index: 0;
    display: block;
    background: var(--module-background, var(--color-grey-00));
    content: '';
    inset: 0;
  }
}

.gridCardeyebrow,
.gridCardtitle2,
.gridCardCtaWrapper {
  grid-column: start / end;
}

.gridCardeyebrow {
  text-align: left;
  @media (--md) {
    text-align: center;
  }
}

.gridCardtitle2 {
  text-align: left;
  @media (--sm) {
    grid-column: col 1 / span 12;
  }
  @media (--md) {
    grid-column: col 2 / span 10;
    text-align: center;
  }
}

.gridCardParagraph {
  text-align: left;
  @media (--sm) {
    grid-column: col 1 / span 11;
  }
  @media (--md) {
    grid-column: col 3 / span 8;
    text-align: center;
  }
}

.gridCardCtaWrapper {
  @media (--md) {
    justify-content: center;
  }
}

.categoriesContainer {
  display: flex;
  flex-flow: row wrap;
  grid-column: wrapper-start / wrapper-end;
  row-gap: var(--spacer-16);
}
