.canvas {
  height: 100vh;
}

.container {
  opacity: var(--image-opacity);
}

.textContainer {
  composes: grid from '~g';
  position: relative;
  z-index: var(--z-index-2);
  margin-top: -48vh;

  &::after {
    position: absolute;
    z-index: var(--z-index-neg);
    top: -10vh;
    left: 0;
    width: 100%;
    height: 60vh;
    content: '';
  }

  @media (--sm) {
    margin-top: -100vh;
  }
}

.hasBackground {
  .textContainer::before {
    position: absolute;
    z-index: var(--z-index-background-color);
    display: block;
    background: var(--module-background, var(--color-grey-00));
    content: '';
    inset: 0;
  }
}

.content {
  composes: subgrid from '~g';

  --subgrid-start: start;
  --subgrid-width: var(--grid-number-of-columns);
  margin-top: -50%;

  @media (--sm) {
    margin-top: 0%;
  }

  .alignCenter {
    .eyebrow,
    .titles,
    .bodies {
      @media (--sm) {
        text-align: center;
      }
    }

    .bodies {
      @media (--sm) {
        grid-column: col 2 / span 12;
      }
    }

    .forms {
      margin-right: auto;
      margin-left: auto;

      @media (--md) {
        max-width: 100%;
        grid-column: col 4 / span 8;
      }

      @media (--lg) {
        max-width: 100%;
        grid-column: col 5 / span 6;
      }
    }

    .buttons {
      margin-right: auto;
      margin-left: auto;
    }
  }

  .eyebrow,
  .titles,
  .bodies,
  .buttons,
  .forms {
    grid-column: start / end;

    @media (--sm) {
      grid-column: col 2 / span 10;
    }
  }
}

.scrollOverlay {
  position: absolute;
  z-index: 2;
  bottom: 0;
  width: 100%;
  height: 100vh;
}

.gradientOverlay {
  position: sticky;
  z-index: var(--z-index-1);
  top: 0;
  left: 0;
  display: var(--text-overlay-display);
  width: 100%;
  height: 100vh;
  background: var(--image-sequence-gradient);
  opacity: var(--text-overlay-opacity, 0);
}
