.xsCards {
  grid-column: wrapper-start / wrapper-end;
  @media (--sm) {
    display: none;
  }

  .cardsContainer {
    display: grid;
    margin: 0 var(--grid-computed-margin-width);
    margin-bottom: var(--spacer-16);
    grid-template-columns: repeat(6, 100%);
  }

  .slideItem {
    padding: 0;
  }
}
