.navigationLink {
  margin: 0 1rem;
}

.navGroup {
  @media (--md) {
    display: flex;
    height: 100%;
    flex-flow: row nowrap;
  }
}

.navGroupItem {
  &:focus span {
    color: var(--color-grey-100);
  }
}

.navGroupSubnav {
  pointer-events: none;
  @media (--md) {
    position: absolute;
    top: var(--nav-height);
    left: 0;
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;

    &.active {
      pointer-events: all;

      .navGroupSubnavItems {
        opacity: 1;
        transform: translateY(0);

        /* animate in */
        transition: opacity var(--desktop-subnav-text-transition) var(--ease-basic-butter), transform var(--desktop-subnav-text-transition) var(--ease-basic-butter);
      }
    }
  }
}

.navGroupSubnavItems {
  height: 0;

  @media (--md) {
    display: flex;
    height: auto;
    flex-flow: row nowrap;
    justify-content: center;
    gap: max(2rem, 2%);
    opacity: 0;
    transform: translateY(-1.5rem);

    /* animate out */
    transition: opacity var(--desktop-subnav-backdrop-transition) var(--ease-basic-butter), transform var(--desktop-subnav-backdrop-transition) var(--ease-basic-butter);
  }
}

.subNavItemWrapper {
  opacity: 0;

  &:first-child {
    padding-top: var(--spacer-8);
  }

  @media (--md) {
    opacity: 1;

    &:first-child {
      padding-top: 0;
    }
  }
}

.subNavItem {
  composes: subnavigation from '~typo';
  padding: 1rem 0;

  @media (--md) {
    padding: var(--spacer-20) 0;
  }
}

.navGroupCaret {
  position: relative;
  top: 0.45em;
  width: 0.5em;
  height: 0.5em;
  margin-left: var(--spacer-8);
  fill: currentcolor;

  @media (--md) {
    position: unset;
    top: 0;
    width: 0.7em;
    height: 0.7em;
  }
}
