.row {
  z-index: 1;
  display: grid;
  width: 96%;
  gap: var(--spacer-16);
  grid-column: wrapper-start / wrapper-end;
  grid-template-columns: repeat(3, 1fr);
  transform: translate3d(var(--row-scroll-progress), 0, 0);
  transition: transform calc(0.1s + var(--row-index) * 0.004s) var(--ease-embellishment);

  --border-radius-20: 1.5rem;

  @media (--md) {
    gap: var(--spacer-40);
  }

  @media (--lg) {
    max-width: min(80vw, 150vh);
  }
}
