.playPauseButton {
  --button-size: 2.1rem;
  --button-scale: 1;
  transition: opacity var(--duration-500ms) var(--ease-micro-bounce) var(--duration-200ms);

  --icon-color: var(--color-grey-88);

  &.isRound {
    --button-size: 4rem;

    @media (--sm) {
      --button-size: 8.8rem;
    }
  }

  &:hover {
    --button-scale: 1.05;
    --icon-color: var(--color-grey-100);
  }
}

.buttonInner {
  position: relative;
  display: block;
  overflow: hidden;
  width: var(--button-size);
  height: var(--button-size);
  transform: scale(var(--button-scale));
  transition: transform var(--duration-300ms) var(--ease-micro-bounce);

  .isRound & {
    background: var(--color-alpha-cinder-50);
    border-radius: 50%;
    box-shadow: inset 0 0 0 1px var(--color-alpha-hawkes-20);
  }
}

.playIcon,
.pauseIcon {
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0;
  transform: translate(-50%, -50%);

  * {
    fill: var(--icon-color);
    transition: fill var(--duration-300ms) var(--ease-default);
  }

  &.isActive {
    opacity: 1;
  }
}

.playIcon {
  height: 2.1rem;

  .isRound & {
    height: 1.4rem;

    @media (--sm) {
      height: 2.6rem;
    }
  }
}

.pauseIcon {
  height: 1.8rem;

  .isRound & {
    height: 1.3rem;

    @media (--sm) {
      height: 2.4rem;
    }
  }
}
