.announcementBanner {
  position: relative;
  display: flex;
  width: 100%;
  padding: var(--spacer-18) var(--spacer-24);
  background: linear-gradient(268.99deg, var(--color-cobalt) 26.6%, var(--color-iris) 69.29%);
  text-align: left;

  @media (--xs) {
    justify-content: center;
    padding: var(--spacer-12) var(--spacer-24);
    text-align: center;
  }

  .closeButton {
    position: absolute;
    top: 50%;
    right: 1rem;
    transform: translateY(-50%);
  }

  .closeButtonInner {
    width: auto;
    height: auto;
  }
}

.bodies {
  composes: announcementBanner from '~typo';
  max-width: 23rem;
  color: var(--color-grey-100);

  @media (--xs) {
    max-width: 100%;
  }
}

.link {
  text-decoration: underline;

  &:hover {
    text-decoration: none;
  }
}
