.category {
  composes: grid from '~g';
  grid-column: wrapper-start / wrapper-end;

  &::before {
    margin-bottom: var(--spacer-24);
    content: '';
    grid-column: start / end;
    grid-row: 1;

    @media (--sm) {
      margin-top: var(--spacer-64);
    }
  }

  &:first-child::before {
    height: 0.2rem;
    background-color: var(--color-grey-15);
    @media (--xs) {
      background-color: transparent;
    }

    @media (--sm) {
      display: none;
    }
  }

  &:not(:first-child)::before {
    margin-top: var(--spacer-64);
  }
}

.categoryTitle {
  composes: title5 from '~typo';
  z-index: var(--z-index-2);
  margin-bottom: var(--spacer-24);
  color: var(--color-grey-90);
  grid-column-start: start;
  grid-row: 2;

  @media (--sm) {
    grid-column-start: col 2;
  }
  @media (--md) {
    grid-column-start: start;
  }
}

.items {
  display: flex;
  margin: 0 var(--grid-computed-margin-width);
  margin-bottom: var(--spacer-16);
  gap: var(--grid-card-gap);
  grid-column: wrapper-start / wrapper-end;

  @media (--sm) {
    display: grid;
    margin: unset;
    grid-column: col 2 / col -1;
    grid-row: 3;
    grid-template-columns: repeat(var(--grid-cards-per-row), 1fr);

    --grid-card-gap: var(--spacer-16);
    --grid-cards-per-row: 2;
  }
  @media (--md) {
    --grid-card-gap: var(--spacer-24);
    --grid-cards-per-row: 3;
    grid-column: start / end;
  }
}

/* prettier-ignore */
.item {
  width: 100%;
  flex-shrink: 0;

  @media (--sm) {
    grid-column: span 1;
  }
}

.card {
  width: 100%;
  padding: 0;
}

.gridCardsCarouselNavigation {
  display: flex;
  flex-direction: row;
  justify-content: center;
  grid-column: wrapper-start / wrapper-end;
  grid-row: 4;

  @media (--sm) {
    display: none;
  }
}
