.navigation {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: var(--spacer-16);
  grid-column: wrapper-start / wrapper-end;
  @media (--sm) {
    display: none;
  }
}

.dot {
  position: relative;
  width: 0.7rem;
  height: 0.7rem;
  padding: 0;
  background-color: var(--color-grey-30);
  border-radius: 50%;

  &::after {
    position: absolute;
    background-color: var(--color-grey-100);
    border-radius: 50%;
    content: '';
    inset: 0;
    opacity: var(--dot-active-opacity, 0);
    transition: opacity 0.3s var(--ease-basic-butter) 0.1s;
  }

  &.isCurrent {
    --dot-active-opacity: 1;
  }
}
