.accordionItem {
  padding-top: var(--spacer-32);
  padding-bottom: var(--spacer-32);
  border-bottom: 0.1rem solid var(--color-alpha-light-10);

  &:first-of-type {
    padding-top: initial;
  }

  &:last-of-type {
    padding-bottom: initial;
    border-bottom: 0;
  }

  &.isOpen {
    .accordionIcon {
      transform: rotate(180deg);
    }
  }
}

.header {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  cursor: pointer;
  text-align: left;
}

.accordionIcon {
  width: 1.6rem;
  height: 1.6rem;
  flex-shrink: 0;
  margin-left: var(--spacer-40);
  transition: transform 0.5s var(--ease-basic-butter);

  path {
    fill: var(--color-alpha-light-60);
  }

  &.small {
    width: 1rem;
    height: 1rem;
  }
}

.content {
  overflow: hidden;
  padding-right: var(--spacer-40);
  transition: height 0.5s var(--ease-basic-butter);

  @media (--sm) {
    padding-right: var(--spacer-64);
  }

  & > * {
    &:first-child {
      padding-top: var(--spacer-8);
    }
  }
}
