.spacer {
  --keyline-scale: 0;
  --height: 0;
  --height-xs: var(--height);
  --height-sm: var(--height);
  --height-md: var(--height);
  --height-lg: var(--height);
  position: relative;
  width: 100%;
  height: var(--height-xs);
  margin: 0;

  @media (--xs) {
    height: var(--height-xs);
  }
  @media (--sm) {
    height: var(--height-sm);
  }

  @media (--md) {
    height: var(--height-md);
  }

  @media (--lg) {
    height: var(--height-lg);
  }
}
