.textWithComponents {
  position: relative;
  padding-top: var(--spacer-124);
  padding-bottom: var(--spacer-124);
  border-radius: var(--top-rounded-module-border-radius);

  &.hasBackgroundImage {
    padding-top: 0;
    background-color: var(--color-grey-00);
  }

  .backgroundWrapper,
  .backgroundColorWrapper {
    position: absolute;
    z-index: var(--z-index-0);
    top: 0;
    left: 0;
    overflow: hidden;
    width: 100%;
    border-top-left-radius: var(--border-radius-40);
    border-top-right-radius: var(--border-radius-40);
  }

  &.isRevealedByGarageDoor {
    .backgroundWrapper,
    .backgroundColorWrapper {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
  }

  .backgroundWrapper {
    height: 40%;
    @media (--sm) {
      height: 56%;
    }

    &::before {
      position: absolute;
      z-index: var(--z-index-3);
      overflow: hidden;
      background: var(--text-with-components-gradient-super-heavy);
      content: '';
      inset: 0;
    }

    &.hasNoGradientMobile {
      &::before {
        background: none;
      }
    }

    &.hasNoGradient {
      &::before {
        @media (--sm) {
          background: none;
        }
      }
    }

    /* default alignment uses default gradients */

    &.hasGradientLightMobile {
      &::before {
        background: var(--text-with-components-gradient-light);
      }
    }

    &.hasGradientLight {
      &::before {
        @media (--sm) {
          background: var(--text-with-components-gradient-light);
        }
      }
    }

    &.hasGradientMediumMobile {
      &::before {
        background: var(--text-with-components-gradient-medium);
      }
    }

    &.hasGradientMedium {
      &::before {
        @media (--sm) {
          background: var(--text-with-components-gradient-medium);
        }
      }
    }

    &.hasGradientHeavyMobile {
      &::before {
        background: var(--text-with-components-gradient-heavy);
      }
    }

    &.hasGradientHeavy {
      &::before {
        @media (--sm) {
          top: auto;
          height: 80%;
          background: var(--text-with-components-gradient-heavy);
        }
      }
    }

    &.hasGradientSuperHeavyMobile {
      &::before {
        background: var(--text-with-components-gradient-super-heavy);
      }
    }

    &.hasGradientSuperHeavy {
      &::before {
        @media (--sm) {
          background: var(--text-with-components-gradient-super-heavy);
        }
      }
    }
  }

  .backgroundColorWrapper {
    height: 100%;

    &::before {
      position: absolute;
      z-index: var(--z-index-background-color);
      display: block;
      background: var(--module-background, var(--color-grey-00));
      content: '';
      inset: 0;
    }
  }
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.container {
  composes: grid from '~g';
  position: relative;
  z-index: var(--z-index-1);
  grid-column: 1 / -1; /* TODO: necessary? */

  /* background image requires padding up top and a background gradient under the text */

  .hasBackgroundImage & {
    &::before {
      width: 100%;
      height: 40vh; /* extra space for background image */
      content: '';
      grid-column: col 1 / span var(--grid-number-of-columns);

      @media (--sm) {
        height: 50vh; /* extra space for background image */
      }

      @media (--md) {
        height: 60vh; /* extra space for background image */
      }
    }

    &::after {
      position: absolute;
      z-index: var(--z-index-neg);
      bottom: 0;
      left: 0;
      width: 100%;
      height: calc(100% - 40vh);
      background: var(--fullwidthimage-text-gradient);
      content: '';

      @media (--sm) {
        height: calc(100% - 50vh);
      }

      @media (--md) {
        height: calc(100% - 60vh);
      }
    }
  }

  /* used for legal pages with large blocks of copy with inline links */
  p a,
  li a {
    display: inline-block;
    color: var(--color-grey-88);
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }
}

.content {
  z-index: var(--z-index-1);
}

/* TODO: remove */
.alignCenter {
  .accents,
  .titles,
  .bodies {
    text-align: left;
    @media (--sm) {
      text-align: center;
    }
  }
}

.topRightGlow {
  position: absolute;
  top: 0;
  right: 0;
  width: 50vw;
  height: 25vw;
  transform: translate3d(50%, -70%, 0);
}

.bottomLeftGlow {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50vw;
  height: 50vw;
  transform: translate3d(-80%, 50%, 0);

  @media (--sm) {
    width: 80rem;
    height: 50rem;
    transform: translate3d(-70%, 70%, 0);
  }
  @media (--md) {
    width: 80rem;
    height: 50rem;
    transform: translate3d(-70%, 70%, 0);
  }
}

.centerRightGlow {
  position: absolute;
  top: 0;
  right: 0;
  width: 70vw;
  height: 100vw;
  transform: translate3d(75%, -25%, 0);

  @media (--sm) {
    width: 45rem;
    height: 80rem;
    transform: translate3d(95%, -20%, 0);
  }
  @media (--md) {
    width: 45rem;
    height: 80rem;
    transform: translate3d(95%, -20%, 0);
  }
}

.fullGlow {
  position: absolute;
  z-index: var(--z-index-neg);
  top: 0;
  right: 0;
  bottom: -50vh;
  left: 0;
}
