.container {
  composes: grid from '~g';
  padding: var(--spacer-64) 0;
}

.hasBackground::before {
  position: absolute;
  z-index: var(--z-index-background-color);
  display: block;
  background: var(--module-background, var(--color-grey-00));
  content: '';
  inset: 0;
}

.garageDoorExtraSpace {
  @media (--md) {
    padding-top: 20vh;
  }
}

.content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: var(--spacer-24) 0;
  grid-column: start / end;

  @media (--xs) {
    padding: 0 5vw;
    grid-column: wrapper-start / wrapper-end;
  }

  @media (--sm) {
    padding: 0;
    gap: var(--spacer-16) 0;
    grid-column: start / end;
  }
}

.icon {
  width: calc(100% / 2);
  height: auto;

  --image-aspect-ratio: 2.81;

  @media (--xs) {
    width: calc(100% / 5);
  }

  @media (--sm) {
    width: calc(100% / 5);
  }

  @media (--md) {
    width: calc(100% / 5);
  }
}
