.modal {
  width: 100%;

  /* max-width: 80rem; */
  margin: 0 auto;

  @media (--sm) {
    grid-column: 3 / span 12;
  }
}

.modalContent {
  max-height: calc(100vh - var(--spacer-160));
  padding: var(--spacer-40) 0 var(--spacer-24);
  background-color: var(--color-grey-00);

  @media (--sm) {
    height: 40em;
    padding-bottom: var(--spacer-48);
  }
}

.slidesContainer {
  display: flex;
}

.slideItem {
  composes: subgrid from '~g';
  position: relative;
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  align-content: start;
  padding: 0 var(--spacer-40);
  pointer-events: none;

  &.active {
    pointer-events: all;
  }

  @media (--sm) {
    position: absolute;
    padding-right: 0;
    padding-left: 0;
  }
}

.modalHeader {
  margin-bottom: var(--spacer-24);
  grid-column: start / end;

  @media (--sm) {
    margin-bottom: var(--spacer-40);
    grid-column: 2 / span 10;
  }
}

.icon {
  height: 2.5rem;
  flex-shrink: 0;
  margin-bottom: var(--spacer-16);
}

.label {
  composes: title4 from '~typo';
  max-width: 40rem;
}

.description {
  composes: bodySmall from '~typo';
}

.description,
.linkWrapper {
  grid-column: start / end;
}

@media (--sm) {
  .description {
    grid-column: 2 / span 7;
  }

  .linkWrapper {
    margin-top: 0;
    grid-column: 10 / span 4;
  }
}

.linkWrapper {
  border-top: 0.1rem solid var(--color-grey-50);
  margin-top: var(--spacer-64);

  @media (--sm) {
    margin-top: 0;
  }
}

.link {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding-top: var(--spacer-16);
  color: var(--color-grey-90);

  a {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
  }

  &::after {
    display: inline-block;
    width: 2.5rem;
    height: 2.5rem;
    background-image: url('/svg/arrow-diagonal.svg');
    content: '';
  }
}

.carouselArrowsContainer {
  composes: subgrid from '~g';
  position: absolute;
  bottom: var(--spacer-32);
  display: none;
  width: 100%;

  @media (--sm) {
    display: grid;
  }

  .carouselArrows {
    position: relative;
  }
}

.carouselPagination {
  justify-content: center;
  padding: var(--spacer-48) 0 0;

  @media (--sm) {
    display: none;
  }
}
