.section {
  --section-vertical-padding: calc(var(--spacer-96) / 2);
  --card-stack-offset: -4rem;
  top: 0;
  order: -1;
  padding: 0;

  @media (--sm) {
    position: sticky;
    top: var(--top-offset, 0);
    order: 1;
    padding: var(--section-vertical-padding) 0;

    --section-vertical-padding: calc(var(--spacer-160 / 2));
  }
  @media (--lg) {
    --section-vertical-padding: calc(var(--spacer-240 / 2));
  }

  --active-progress: 0;

  &.active {
    --active-progress: 1;
  }
}

.content {
  z-index: var(--z-index-4);
  grid-column: var(--content-start) / span var(--content-width);

  @media (--sm) {
    display: flex;
    flex-flow: row wrap;
    grid-column: var(--content-start) / span var(--content-width);
    opacity: calc(1 - var(--card-leave-progress, 0));
    transform: translate3d(0, calc((1 - var(--card-enter-progress, 0)) * 10rem + (var(--card-next-progress, 0)) * -10rem), 0) scale(calc(1 - var(--card-next-progress, 0) * 0.05));
  }

  .accents,
  .bodies {
    color: var(--color-grey-60);
  }

  .title3 {
    ~ .bodySmall {
      margin-top: var(--spacer-8);

      @media (--sm) {
        margin-top: var(--spacer-20);
      }
    }
  }

  .bodySmall + .titles,
  .bodies + .titles {
    margin-top: var(--spacer-40);

    @media (--sm) {
      margin-top: var(--spacer-64);
    }
  }

  .buttons {
    margin-top: var(--spacer-12);

    @media (--sm) {
      margin-top: var(--spacer-6);
    }

    @media (--md) {
      margin-top: var(--spacer-16);
    }
  }

  .stats,
  .touts {
    display: flex;
    flex-flow: row wrap;
    padding: 0;
    margin: 0;
    grid-row-gap: 0;

    @media (--sm) {
      padding: var(--spacer-32) 0;
    }

    > * {
      margin-bottom: var(--spacer-48);

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.mediaContainer {
  margin-bottom: var(--spacer-40);
  grid-column: var(--media-start) / var(--media-end);
  @media (--sm) {
    transform: translate3d(0, calc(var(--card-next-progress, 0) * var(--card-stack-offset) * var(--reverse-index)), 0);
  }
}

.mediaContainerInGlow {
  position: relative;
  z-index: var(--z-index-2);
  overflow: hidden;
  aspect-ratio: var(--media-aspect-ratio);
  border-radius: var(--border-radius-20);

  @media (--sm) {
    transform: scale(calc(1 - var(--card-next-progress, 1) * (0.05 * var(--reverse-index))));
  }

  --image-aspect-ratio: var(--media-aspect-ratio);

  @media (--sm) {
    &::after {
      position: absolute;
      background-color: black;
      content: '';
      inset: 0;
      opacity: calc(var(--card-next-progress, 0) * 0.8);
    }
  }
}

.media {
  width: 100%;
  height: 100%;
}

.mediaElement {
  width: 100%;
  transform: scale(calc(1 + ((1 - var(--card-enter-progress, 0)) * 0.1)));
}

.glowBackground {
  --glow-offset-y: 5%;

  @media (--sm) {
    --glow-opacity: calc(1 - var(--card-next-progress, 0));
    transition-duration: 0s;
  }
}
