.singleFieldForm {
  composes: inputField from '~deco';
  position: relative;
  display: flex;
  padding: 0.41rem 0;
  border-radius: 10em;

  /* prettier-ignore */
  box-shadow: inset 0 0 0 0.075rem var(--color-alpha-light-20);
  transition: box-shadow var(--duration-500ms) var(--ease-basic-butter);

  &:has(input:hover),
  &:has(input:focus) {
    box-shadow: inset 0 0 0 0.075rem var(--color-alpha-hawkes-60);
  }

  &:has(input:active) {
    box-shadow: inset 0 0 0 0.2rem var(--color-alpha-hawkes-60);
    transition-duration: var(--duration-250ms);
  }

  /* prevent displaying error if just empty */
  &.fieldWithError {
    &,
    &:has(input:hover),
    &:has(input:focus) {
      box-shadow: inset 0 0 0 0.075rem var(--color-coral-dark);
    }
  }

  .input {
    padding-right: var(--spacer-6);
    border: 0;
    backdrop-filter: none;
    background-color: transparent;
    box-shadow: none;
  }

  .submitButton {
    z-index: var(--z-index-2);
    align-self: center;

    /* special case for this one because it needs to sit inside the input with equal spacing around the sides */
    padding: 1.47rem var(--spacer-32) 1.37rem;
    margin-right: var(--spacer-6);
    line-height: 1;
  }
}

.inputContainer {
  flex: 1 1 auto;
}

.input {
  width: 100%;
  margin-left: var(--spacer-6);
}

.submitButton {
  flex: 0 0 auto;
}

.formErrorMessage {
  composes: inputError from '~typo';
  position: absolute;
  top: calc(100% + var(--spacer-6));
  left: var(--spacer-24);
  color: var(--color-coral-dark);

  &::first-letter {
    text-transform: uppercase;
  }
}

.fieldErrorMessage {
  composes: inputError from '~typo';
  position: absolute;
  top: calc(100% + var(--spacer-6));
  left: var(--spacer-24);
  color: var(--color-coral-dark);

  &::first-letter {
    text-transform: uppercase;
  }
}
