.xsTestimonials {
  composes: grid from '~g';
  overflow: hidden;
  opacity: 1;

  @media (--sm) {
    display: none;
  }
}

.header {
  composes: label from '~typo';
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: var(--spacer-32);
  grid-column: start/end;
}

.title {
  composes: eyebrow from '~typo';
  color: var(--color-accent);
}

.counter {
  composes: inputError from '~typo';
  opacity: 0.5;
}

.cardsDeckWrapper {
  grid-column: start/end;
}

.cardsDeck {
  position: relative;
  width: 100%;
  margin-top: var(--spacer-8);
  aspect-ratio: 327/397;
}

.card {
  position: absolute;
  z-index: calc(var(--total) - var(--index-xs-testimonial) + 1);
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background-color: var(--main-card-color, var(--color-grey-12));
  border-radius: var(--border-radius-20);
  will-change: transform;

  &::before {
    position: absolute;
    z-index: var(--z-index-1);
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, var(--transparent) 20%, var(--color-alpha-black-90) 128%);
    content: '';
    opacity: 1;
    pointer-events: none;
  }

  .cardMedia {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    transform: scale(calc((var(--progress)) * 0.2 + 1));
    transition: transform var(--duration-200ms) var(--ease-out);
  }

  .cardContent {
    position: absolute;
    z-index: var(--z-index-2);
    right: var(--spacer-16);
    bottom: var(--spacer-24);
    left: var(--spacer-16);
  }
}

.cardContentTitles {
  composes: title5 from '~typo';
  color: var(--color-grey-90);
}

.cardContentBody {
  composes: label from '~typo';
  color: var(--color-grey-60);
}

.cardContentEm {
  color: var(--color-grey-90);
}

.testimonialTitles {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin-top: var(--spacer-32);
}

.testimonialTitleItem {
  flex: 1 0 auto;
  margin-right: var(--spacer-96);
  opacity: 0.3;
  transition: opacity var(--ease-basic-butter) var(--duration-400ms);

  &.active {
    opacity: 1;
  }
}

.testimonialTitle {
  composes: title3 from '~typo';
  background: linear-gradient(45.41deg, var(--gradient-color-1) -20%, var(--gradient-color-2) 120%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.content {
  margin-top: var(--spacer-24);
  grid-column: start/end;
}

.contentTitles {
  composes: title5 from '~typo';
  display: inline;
  margin: 0;
  color: var(--color-grey-40);

  &:first-child {
    color: var(--color-grey-100);
  }
}
