@value bento: '../styles.module.css';
@value bentoSmallBreakpoint from bento;

.bentoCardWrapper {
  --shadow-radius: var(--border-radius-20);
}

.bentoCard {
  composes: subgrid from '~g';
  position: relative;
  z-index: var(--z-index-1);
  overflow: hidden;
  width: 100%;
  box-sizing: content-box;
  align-items: stretch;
  border: var(--bento-border);
  border-radius: var(--shadow-radius);

  --card-grid-width: 4;
  --subgrid-width: var(--card-grid-width);
  --subgrid-start: start;

  @media (--md) {
    --card-grid-width: 7;
  }
}

.bentoCardIsLinked {
  composes: cardAndButtonHover from '~g';
}

.cardRefWrapper {
  position: relative;
  display: inline-block;
}

.imageWrapper {
  position: relative;
  z-index: var(--z-index-2);
  width: 100%;
  grid-column: col 1 / span var(--card-grid-width);
}

.image {
  width: 100%;
}

/* in the rare case that no image is uploaded (present in one comp currently) */
.imageSpacer {
  height: clamp(18rem, 48vw, 34rem);

  @media (--sm) {
    height: clamp(20rem, 44vw, 34rem);
  }

  @media (min-width: bentoSmallBreakpoint) {
    height: max(20rem, 24vw);
  }

  @media (--md) {
    height: clamp(23rem, 22vw, 28rem);
  }
}

.contentContainer {
  composes: subgrid from '~g';
  position: relative;
  z-index: var(--z-index-5);
  width: 100%;
  align-items: end;
  padding: 0 0 var(--spacer-40);

  --subgrid-width: var(--card-grid-width);
  --subgrid-start: start;

  @media (--sm) {
    padding-bottom: var(--spacer-48);
  }

  @media (min-width: bentoSmallBreakpoint) {
    padding-bottom: var(--spacer-32);
  }

  @media (--lg) {
    padding-bottom: var(--spacer-48);
  }
}

.content {
  padding: 0 max(10vw, var(--spacer-24));
  grid-column: col 1 / span 4;
  text-align: center;

  @media (--sm) {
    padding: 0 min(10vw, var(--spacer-96));
  }

  @media (min-width: bentoSmallBreakpoint) {
    padding: 0 max(5vw, var(--spacer-32));
  }

  @media (--md) {
    padding: 0;
    grid-column: col 2 / span 5;
  }

  .titles {
    margin: 0 0 var(--spacer-8);
    color: var(--color-grey-100);

    @media (--sm) {
      margin: 0 0 var(--spacer-12);
    }

    &:last-child {
      margin: 0;
    }
  }

  .bodies {
    margin: 0 0 var(--spacer-4);
    color: var(--color-grey-60);

    &:last-child {
      margin: 0;
    }
  }

  .buttons {
    align-items: center;
    margin-top: 0;

    @media (--sm) {
      align-items: unset;
      justify-content: center;
    }
  }
}

.bodies {
  composes: bodySmallBaseRegularMd from '~typo';
}

.backgroundWrapper {
  position: absolute;
  z-index: var(--z-index-neg); /* TODO: this is required to not sit on top of text, i think it's causing z-index thrashing */
  overflow: hidden;
  inset: 0;

  --image-aspect-ratio: 1;

  [data-order-index='0'] & {
    &::before {
      background: var(--bento-glass-integrations-1);
    }

    .hasSubpageGradient & {
      &::before {
        background: var(--bento-glass-sub-integrations-1);
      }
    }
  }

  [data-order-index='1'] & {
    &::before {
      background: var(--bento-glass-integrations-2);
    }

    .hasSubpageGradient & {
      &::before {
        background: var(--bento-glass-sub-integrations-2);
      }
    }
  }

  [data-order-index='2'] & {
    &::before {
      background: var(--bento-glass-integrations-3);
    }

    .hasSubpageGradient & {
      &::before {
        background: var(--bento-glass-sub-integrations-3);
      }
    }
  }

  [data-order-index='3'] & {
    &::before {
      background: var(--bento-glass-integrations-4);
    }

    .hasSubpageGradient & {
      &::before {
        background: var(--bento-glass-sub-integrations-4);
      }
    }
  }

  &::before,
  &::after {
    position: absolute;
    display: block;
    backface-visibility: hidden;
    content: '';
    inset: 0;
    transform: translate3d(0, 0, 0);
  }

  /* background gradient */
  &::before {
    z-index: var(--z-index-1);
    backface-visibility: hidden;
    content: '';
  }

  /* hover gradient */
  &::after {
    z-index: var(--z-index-2);
    background: var(--bento-hover-gradient);
    opacity: var(--card-hover-progress, 0);
    transition: var(--duration-600ms) opacity var(--ease-default);
  }
}

.backgroundImage {
  position: absolute;
  z-index: var(--z-index-3);
  display: block;
  height: auto;
  inset: 0;
  transform: translate3d(0, 0, 0);
}

.imageWrapper,
.backgroundWrapper {
  transform: scale(calc(var(--card-hover-progress, 0) * var(--bento-card-hover-scale-amount) + 1)) translate3d(calc(var(--card-hover-progress, 0) * ((var(--bento-card-hover-scale-amount) * 100%) / -2)), 0, 0);
  transform-origin: top left;
  transition: var(--card-hover-transform-default-transition);
}
