.navItemWrapper {
  display: flex;
  height: 100%;
  align-items: center;
}

.navItem {
  composes: navigation from '~typo';
  display: flex;
  height: 100%;
  align-items: center;

  /* reset button margin on safari */
  margin: 0;
  color: inherit;
  cursor: pointer;
  text-decoration: none;
  transition: color var(--duration-200ms) var(--ease-out);

  @media (hover: hover) {
    &:hover {
      color: var(--color-grey-100);
    }
  }

  &:active,
  &:focus {
    color: var(--color-grey-100);
  }

  .innerWrapper {
    display: flex;
    align-content: center;
    justify-content: center;
  }
}

.active {
  color: var(--color-grey-100);
}
