:root {
  /* Font Settings */
  --font-size-base: 1.6rem;
  --font-main: 'Frame Gothic', sans-serif;
  --font-secondary: 'Frame Gothic', sans-serif;

  /* Z-Indexes */
  --z-index-background-color: -10;
  --z-index-neg: -1;
  --z-index-0: 0;
  --z-index-1: 1;
  --z-index-2: 2;
  --z-index-3: 3;
  --z-index-4: 4;
  --z-index-5: 5;
  --z-index-10: 10;
  --z-index-nav: 100;
  --z-index-max: 105;
  --z-index-modal: 110;

  /* Shadows */

  --hover-card-hover-gradient: linear-gradient(0deg, rgb(0 0 0 / 95%) 37.25%, rgb(0 0 0 / 26%) 150.4%);

  --hover-card-category-gradient: linear-gradient(0deg, rgb(0 0 0 / 63%) 16.07%, rgb(0 0 0 / 23.4%) 56.98%);

  --hover-card-asymmetrical-hover-gradient: linear-gradient(0deg, rgb(0 0 0 / 80%) 16.07%, rgb(0 0 0 / 20.8%) 56.98%);

  --bento-glass-integrations-1: linear-gradient(221deg, #42545d, #203643 29%, #1c2935 45%, #1b242f 64%, #191d27 83%, #171820);
  --bento-glass-integrations-2: linear-gradient(226deg, #2e3d48 6%, #203643 18%, #1b242f 51%, #191d27 82%, #171820);
  --bento-glass-integrations-3: linear-gradient(208deg, #1e2e3a, #1c2833 19%, #1b242f 40%, #191d26 69%, #171820 88%, #171820);
  --bento-glass-integrations-4: linear-gradient(194deg, #1e2e3a, #1c2833 25%, #1b242f 38%, #191d26 69%, #171820 88%, #171820);
  --bento-hover-gradient: linear-gradient(47.17deg, #181821 7.98%, #1c2631 150.39%);

  --bento-glass-sub-integrations-1: linear-gradient(165.45deg, #272834 8.67%, #16171f 62.25%), linear-gradient(0deg, rgb(54 56 70 / 20%), rgb(54 56 70 / 20%));
  --bento-glass-sub-integrations-2: linear-gradient(165.45deg, #272834 8.67%, #16171f 62.25%), linear-gradient(0deg, rgb(54 56 70 / 20%), rgb(54 56 70 / 20%));
  --bento-glass-sub-integrations-3: linear-gradient(165.45deg, #272834 8.67%, #16171f 62.25%), linear-gradient(0deg, rgb(54 56 70 / 20%), rgb(54 56 70 / 20%));
  --bento-glass-sub-integrations-4: linear-gradient(165.45deg, #272834 8.67%, #16171f 62.25%), linear-gradient(0deg, rgb(54 56 70 / 20%), rgb(54 56 70 / 20%));

  --pricing-overview-gradient: linear-gradient(180deg, rgb(13 13 24 / 0%) 0%, #0c0c17 90.75%);

  /* borders */
  --bento-border: 1px solid rgb(54 56 70 / 20%);

  /* TODO: Refactor this box shadow, it's too much */
  --card-box-shadow: 84.6801px 149.711px 80px rgb(0 0 0 / 21%), 54.8852px 97.0347px 46.8519px rgb(0 0 0 / 15.9%), 32.6175px 57.6663px 25.4815px rgb(0 0 0 / 12.8%), 16.936px 29.9421px 13px rgb(0 0 0 / 10.5%), 6.89986px 12.1986px 6.51852px rgb(0 0 0 / 8.24%),
    1.56815px 2.77242px 3.14815px rgb(0 0 0 / 5.06%);

  /* TODO: get designers to refine this shadow - i made it up */
  --card-small-box-shadow: 32.6175px 57.6663px 25.4815px rgb(0 0 0 / 21.8%), 16.936px 29.9421px 13px rgb(0 0 0 / 15.5%), 6.89986px 12.1986px 6.51852px rgb(0 0 0 / 12.24%), 1.56815px 2.77242px 3.14815px rgb(0 0 0 / 5.06%);

  --carousel-arrow-box-shadow: 5px 7px 18px rgb(0 0 0 / 10%), 0 8px 21px rgb(1 0 19 / 15%);

  /* TODO: talk to designers about unifying the box shadow (same as above) */
  --faq-box-shadow: 57.4265px 55.6974px 109px rgb(0 0 0 / 38%), 29.0722px 28.1968px 47.5172px rgb(0 0 0 / 25.7%), 11.4853px 11.1395px 17.7125px rgb(0 0 0 / 19%);

  /* Gradients */
  --fancy-testimonial-gradient: linear-gradient(70.41deg, #5d3730 -52%, #8b5f4d 26.88%, #343357 75%);

  --fullwidthimage-text-gradient: linear-gradient(180deg, rgb(0 0 0 / 0%) 0.46%, rgb(0 0 0 / 60%) 18%, #000000 48%);

  --integrations-table-background-gradient: linear-gradient(180deg, rgb(0 0 0 / 75%) 63.36%, rgb(3 4 4 / 0%) 107.65%);

  --pricing-card-gradient: linear-gradient(357.01deg, rgb(0 0 0 / 50%) 1.77%, rgb(0 0 0 / 50%) 62.88%, rgb(37 36 56 / 50%) 99.72%);

  /* Gradients have unique directions and colors per module. they also have multiple weights */

  --carousel-gradient: linear-gradient(0deg, rgb(0 0 0 / 40%), rgb(0 0 0 / 40%)), linear-gradient(73.81deg, rgb(0 0 0 / 50%) 16.66%, rgb(0 0 0 / 0%) 52.25%);
  --carousel-gradient-light: linear-gradient(0deg, rgb(0 0 0 / 20%), rgb(0 0 0 / 20%)), linear-gradient(73.81deg, rgb(0 0 0 / 50%) 16.66%, rgb(0 0 0 / 0%) 52.25%);
  --carousel-gradient-heavy: linear-gradient(0deg, rgb(0 0 0 / 60%), rgb(0 0 0 / 60%)), linear-gradient(73.81deg, rgb(0 0 0 / 50%) 16.66%, rgb(0 0 0 / 0%) 52.25%);

  --carousel-gradient-mobile: linear-gradient(0deg, rgb(0 0 0 / 30%), rgb(0 0 0 / 30%)), linear-gradient(73.81deg, rgb(0 0 0 / 50%) 16.66%, rgb(0 0 0 / 0%) 52.25%);
  --carousel-gradient-mobile-light: linear-gradient(0deg, rgb(0 0 0 / 20%), rgb(0 0 0 / 20%)), linear-gradient(73.81deg, rgb(0 0 0 / 50%) 16.66%, rgb(0 0 0 / 0%) 52.25%);
  --carousel-gradient-mobile-heavy: linear-gradient(0deg, rgb(0 0 0 / 60%), rgb(0 0 0 / 60%)), linear-gradient(73.81deg, rgb(0 0 0 / 50%) 16.66%, rgb(0 0 0 / 0%) 52.25%);

  --fullscreen-video-gradient: linear-gradient(0deg, rgb(0 0 0 / 50%), rgb(0 0 0 / 50%));
  --fullscreen-video-gradient-light: linear-gradient(0deg, rgb(0 0 0 / 30%), rgb(0 0 0 / 30%));
  --fullscreen-video-gradient-heavy: linear-gradient(0deg, rgb(0 0 0 / 80%), rgb(0 0 0 / 80%));

  --hero-gradient-light: linear-gradient(18.61deg, rgb(0 0 0 / 50.4%) 21.85%, rgb(0 0 0 / 0%) 73.55%);
  --hero-gradient-medium: linear-gradient(18.61deg, rgb(0 0 0 / 67.2%) 21.85%, rgb(0 0 0 / 0%) 73.55%);
  --hero-gradient-heavy: linear-gradient(18.61deg, rgb(0 0 0 / 84%) 21.85%, rgb(0 0 0 / 0%) 73.55%);
  --hero-gradient-super-heavy: linear-gradient(0.59deg, rgb(0 0 0 / 90%) 11.21%, rgb(0 0 0 / 0%) 72.19%);

  --hero-center-gradient-heavy: linear-gradient(0deg, rgb(0 0 0 / 60%), rgb(0 0 0 / 60%)), linear-gradient(11.73deg, rgb(0 0 0 / 42%) 19.31%, rgb(0 0 0 / 10.5%) 91.4%);
  --hero-center-gradient-light: linear-gradient(0deg, rgb(0 0 0 / 20%), rgb(0 0 0 / 20%)), linear-gradient(11.73deg, rgb(0 0 0 / 42%) 19.31%, rgb(0 0 0 / 10.5%) 91.4%);
  --hero-center-gradient-medium: linear-gradient(0deg, rgb(0 0 0 / 40%), rgb(0 0 0 / 40%)), linear-gradient(11.73deg, rgb(0 0 0 / 42%) 19.31%, rgb(0 0 0 / 10.5%) 91.4%);
  --hero-center-gradient-super-heavy: linear-gradient(0deg, rgb(0 0 0 / 80%), rgb(0 0 0 / 80%)), linear-gradient(11.73deg, rgb(0 0 0 / 42%) 19.31%, rgb(0 0 0 / 10.5%) 91.4%);

  --speedbump-gradient-light: linear-gradient(59.29deg, rgb(0 0 0 / 40%) 33.18%, rgb(0 0 0 / 10%) 74.23%);
  --speedbump-gradient-medium: linear-gradient(59.29deg, rgb(0 0 0 / 60%) 33.18%, rgb(0 0 0 / 10%) 74.23%);
  --speedbump-gradient-heavy: linear-gradient(59.29deg, rgb(0 0 0 / 80%) 33.18%, rgb(0 0 0 / 10%) 74.23%);
  --speedbump-gradient-super-heavy: linear-gradient(59.29deg, rgb(0 0 0 / 90%) 33.18%, rgb(0 0 0 / 10%) 74.23%);

  --speedbump-gradient-light-full-bleed: linear-gradient(55.12deg, rgb(0 0 0 / 47.2%) -4.95%, rgb(0 0 0 / 0%) 132.71%);
  --speedbump-gradient-medium-full-bleed: linear-gradient(55.12deg, rgb(0 0 0 / 67.2%) -4.95%, rgb(0 0 0 / 0%) 132.71%);
  --speedbump-gradient-heavy-full-bleed: linear-gradient(55.12deg, rgb(0 0 0 / 80.2%) -4.95%, rgb(0 0 0 / 0%) 132.71%);
  --speedbump-gradient-super-heavy-full-bleed: linear-gradient(55.12deg, rgb(0 0 0 / 90.2%) -4.95%, rgb(0 0 0 / 0%) 132.71%);

  --speedbump-gradient-light-alt: linear-gradient(0deg, rgb(0 0 0 / 10%), rgb(0 0 0 / 10%)), linear-gradient(220.32deg, rgb(0 0 0 / 0%) 53.82%, rgb(0 0 0 / 40%) 74.91%);
  --speedbump-gradient-medium-alt: linear-gradient(0deg, rgb(0 0 0 / 10%), rgb(0 0 0 / 10%)), linear-gradient(220.32deg, rgb(0 0 0 / 0%) 53.82%, rgb(0 0 0 / 60%) 74.91%);
  --speedbump-gradient-heavy-alt: linear-gradient(0deg, rgb(0 0 0 / 10%), rgb(0 0 0 / 10%)), linear-gradient(220.32deg, rgb(0 0 0 / 0%) 53.82%, rgb(0 0 0 / 80%) 74.91%);
  --speedbump-gradient-super-heavy-alt: linear-gradient(0deg, rgb(0 0 0 / 10%), rgb(0 0 0 / 10%)), linear-gradient(220.32deg, rgb(0 0 0 / 0%) 53.82%, rgb(0 0 0 / 90%) 74.91%);

  @media (--xs) {
    --speedbump-gradient-light-alt: linear-gradient(0deg, rgb(0 0 0 / 10%), rgb(0 0 0 / 10%)), linear-gradient(220.32deg, rgb(0 0 0 / 0%) 48.79%, rgb(0 0 0 / 40%) 68.72%);
    --speedbump-gradient-medium-alt: linear-gradient(0deg, rgb(0 0 0 / 10%), rgb(0 0 0 / 10%)), linear-gradient(220.32deg, rgb(0 0 0 / 0%) 48.79%, rgb(0 0 0 / 60%) 68.72%);
    --speedbump-gradient-heavy-alt: linear-gradient(0deg, rgb(0 0 0 / 10%), rgb(0 0 0 / 10%)), linear-gradient(220.32deg, rgb(0 0 0 / 0%) 48.79%, rgb(0 0 0 / 80%) 68.72%);
    --speedbump-gradient-super-heavy-alt: linear-gradient(0deg, rgb(0 0 0 / 10%), rgb(0 0 0 / 10%)), linear-gradient(220.32deg, rgb(0 0 0 / 0%) 48.79%, rgb(0 0 0 / 90%) 68.72%);
  }

  @media (--sm) {
    --speedbump-gradient-light-alt: linear-gradient(0deg, rgb(0 0 0 / 10%), rgb(0 0 0 / 10%)), linear-gradient(218.52deg, rgb(0 0 0 / 0%) 49.46%, rgb(0 0 0 / 40%) 77.35%);
    --speedbump-gradient-medium-alt: linear-gradient(0deg, rgb(0 0 0 / 10%), rgb(0 0 0 / 10%)), linear-gradient(218.52deg, rgb(0 0 0 / 0%) 49.46%, rgb(0 0 0 / 60%) 77.35%);
    --speedbump-gradient-heavy-alt: linear-gradient(0deg, rgb(0 0 0 / 10%), rgb(0 0 0 / 10%)), linear-gradient(218.52deg, rgb(0 0 0 / 0%) 49.46%, rgb(0 0 0 / 80%) 77.35%);
    --speedbump-gradient-super-heavy-alt: linear-gradient(0deg, rgb(0 0 0 / 10%), rgb(0 0 0 / 10%)), linear-gradient(218.52deg, rgb(0 0 0 / 0%) 49.46%, rgb(0 0 0 / 90%) 77.35%);
  }

  --text-with-components-gradient-light: linear-gradient(354.42deg, var(--color-alpha-black-100) 5%, rgb(0 0 0 / 40%) 22.76%, rgb(0 0 0 / 0%) 79.93%);
  --text-with-components-gradient-medium: linear-gradient(354.42deg, var(--color-alpha-black-100) 5%, rgb(0 0 0 / 60%) 22.76%, rgb(0 0 0 / 0%) 79.93%);
  --text-with-components-gradient-heavy: linear-gradient(354.42deg, var(--color-alpha-black-100) 5%, rgb(0 0 0 / 80%) 22.76%, rgb(0 0 0 / 0%) 79.93%);
  --text-with-components-gradient-super-heavy: linear-gradient(354.42deg, var(--color-alpha-black-100) 5%, rgb(0 0 0 / 100%) 22.76%, rgb(0 0 0 / 0%) 79.93%);

  --video-playlist-gradient: linear-gradient(180deg, rgb(0 0 0 / 0%) 0%, rgb(0 0 0 / 40%) 100%);
  --video-playlist-gradient-light: linear-gradient(180deg, rgb(0 0 0 / 0%) 0%, rgb(0 0 0 / 20%) 100%);
  --video-playlist-gradient-heavy: linear-gradient(180deg, rgb(0 0 0 / 0%) 0%, rgb(0 0 0 / 60%) 100%);

  --nav-gradient-when-blurred: linear-gradient(to bottom, rgb(13 13 24 / 70%) 50%, rgb(13 13 24 / 30%) 100%);

  --nav-gradient-when-unblurred: linear-gradient(to bottom, rgb(0 0 0 / 90%) 50%, rgb(0 0 0 / 50%) 100%);

  --carousel-overlay-gradient: linear-gradient(270deg, #000000 -1.26%, rgb(0 0 0 / 0%) 15.94%), linear-gradient(270deg, rgb(0 0 0 / 55.3%) 1.75%, rgb(0 0 0 / 55.3%) 9.43%, rgb(0 0 0 / 48.8%) 100%);

  --image-sequence-gradient: linear-gradient(30.53deg, rgb(0 0 0 / 90%) 41.93%, rgb(0 0 0 / 23.4%) 63.18%), linear-gradient(360deg, rgb(0 0 0 / 92%) 15.86%, rgb(0 0 0 / 0%) 56.66%);

  --stat-gradient-enterprise: linear-gradient(155.1deg, #fcfcfc -2.85%, rgb(252 252 252 / 0%) 78.46%), linear-gradient(180deg, #363846 26.79%, rgb(54 56 70 / 0%) 93.45%);

  /* Max Widths  */
  --global-max-width: 41rem;

  /* This value will be overriden by Javascript */
  --window-width: 100vw;

  /* Nav Height variable used to prevent elements from being hidden underneath (and other calculations) */
  --nav-height: 7.2rem;
  --nav-height-padded: calc(var(--nav-height) + var(--spacer-12));

  /* Common border radii */
  --border-radius-2: 0.2rem;
  --border-radius-5: 0.5rem;
  --border-radius-8: 0.8rem;

  --border-radius-12: 1.2rem;
  --border-radius-16: 1.6rem;
  --border-radius-20: 2rem;
  --border-radius-40: 4rem;
  --border-radius-large: min(4vw, 4rem);
  --icons-border-radius: 22%;
  --card-border-radius: 2rem;
  --testimonial-card-radius: 1.2rem;
  --top-rounded-module-border-radius: var(--border-radius-40) var(--border-radius-40) 0 0;
  --bottom-rounded-module-border-radius: 0 0 var(--border-radius-40) var(--border-radius-40);

  --default-card-hover-scale-amount: 0.02;
  --bento-card-hover-scale-amount: 0.05;

  /* Grid settings */
  --grid-max-content-width: var(--global-max-width);
  --grid-gutter-size: 24px;
  --grid-margin-size: var(--grid-gutter-size);
  --grid-number-of-columns: 4;

  @media (--sm) {
    --global-max-width: 112rem;
    --nav-height: 8rem;
    --grid-number-of-columns: 14;
    --grid-margin-size: var(--spacer-64);
  }

  @media (--md) {
    --grid-margin-size: var(--grid-gutter-size);
    --top-rounded-module-border-radius: var(--border-radius-large) var(--border-radius-large) 0 0;
    --bottom-rounded-module-border-radius: 0 0 var(--border-radius-large) var(--border-radius-large);
  }

  /* Computed grid variables */

  /* Grid edge margins */
  --grid-computed-margin-width: max(var(--grid-margin-size), var(--grid-gutter-size), (var(--window-width) - var(--grid-max-content-width)) / 2);

  /* Grid column width */
  --grid-computed-column-width: calc(min(var(--window-width) - 2 * var(--grid-computed-margin-width), var(--grid-max-content-width)) / var(--grid-number-of-columns) - var(--grid-gutter-size) * (var(--grid-number-of-columns) - 1) / var(--grid-number-of-columns));

  /* Max grid width, with margins */
  --grid-computed-full-width: calc(var(--grid-gutter-size) * (var(--grid-number-of-columns) - 1) + var(--grid-computed-column-width) * var(--grid-number-of-columns) + var(--grid-computed-margin-width) * 2);

  /* Max grid width, not including margins */
  --grid-computed-inner-width: calc(var(--grid-gutter-size) * (var(--grid-number-of-columns) - 1) + var(--grid-computed-column-width) * var(--grid-number-of-columns));

  /* Animations */
  --duration-60ms: 0.06s;
  --duration-150ms: 0.15s;
  --duration-200ms: 0.2s;
  --duration-300ms: 0.3s;
  --duration-400ms: 0.4s;
  --duration-500ms: 0.5s;
  --duration-600ms: 0.6s;

  --ease-in: cubic-bezier(0.49, 0.025, 0.65, 0.65);
  --ease-out: cubic-bezier(0.28, 0.44, 0.49, 1);
  --ease-in-out: cubic-bezier(0.49, 0.025, 0.49, 1);
  --ease-micro-bounce: cubic-bezier(0, 0.44, 0.6, 1); /* ease-out */
  --ease-default: cubic-bezier(0.25, 0.1, 0.25, 1); /* ease-out-soft */
  --ease-basic-butter: cubic-bezier(0.42, 0, 0.28, 0.99); /* ease-out-softer */
  --ease-embellishment: cubic-bezier(0.42, 0, 0, 1); /* ease-in-out */

  /* Aspect Ratios */
  --studio-display-aspect-ratio: 1.725;
  --macbook-pro-aspect-ratio: 154/100;
}
